import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { LOGIN_BUTTON_CLICKED, SIGN_UP_BUTTON_CLICKED } from 'constants/amplitude/AmplitudeEvents';
import { QR_POPUP } from 'constants/ComponentNames';

import amplitude from 'amplitude-js';

import { AuthMenu } from './styles';
import StylishButton from './StylishButton';
import PopupNew from '../../common/Popup/PopupNew';
import QRPopup from '../common/Popups/QRPopup';
import RequestFormPopup from '../common/Popups/RequestFormPopup/RequestFormPopup';

const Auth = (props) => {
    const [linksStore, setLinksStore] = useState(props.data[0]?.nav_links);
    const [links, setLinks] = useState([]);
    const [pageName, setPageName] = useState({});

    const popupRef = useRef(null);

    const selectPopupSnippet = (popupName) => {
        if (popupName === QR_POPUP) {
            popupRef.current?.open(<QRPopup hasAppsBtns={true} popupRef={popupRef} />);
        } else {
            popupRef.current?.open(<RequestFormPopup sendFrom={props.sendFrom} popupRef={popupRef} />);
        }
    };

    const sendAmplitudeEvent = (title) => {
        const eventName = title === 'Sign up' ? SIGN_UP_BUTTON_CLICKED : LOGIN_BUTTON_CLICKED;
        amplitude.getInstance().logEvent(eventName);
    };

    const linksCodeSnippet =
        Array.isArray(links) &&
        links
            .filter((item) => item !== null)
            .map((link) => {
                return (
                    <li key={link.id}>
                        {pageName && !link.modal_window && link.title && link.url && (
                            <StylishButton
                                onClick={() => {
                                    sendAmplitudeEvent(link.title);
                                }}
                                caption={link.title}
                                fashion={link.color ? link.color : 'primary'}
                                href={link.url}
                                id={link.title}
                            />
                        )}
                        {pageName && link.modal_window && link.title && (
                            <StylishButton
                                caption={link.title}
                                fashion={link.color ? link.color : 'primary'}
                                onClick={() => selectPopupSnippet(link.modal_window)}
                                id={link.title}
                            />
                        )}
                    </li>
                );
            });

    const addPageName = (landing, pageName, isAuth) => {
        const result = { name: '', auth: !!isAuth };
        if (landing) {
            result.name = 'landing';
        } else if (pageName) {
            result.name = pageName;
        } else {
            result.name = 'other';
        }
        setPageName(result);
    };

    const filterLinks = (links, pageData) => {
        switch (pageData.name) {
            case 'landing':
                setLinks(linksStore.filter((item) => item.additional_info == pageData.name && item.authorised_user === pageData.auth));
                break;
            case 'other':
                if (pageData.auth) {
                    setLinks(linksStore.filter((item) => item.authorised_user === pageData.auth));
                } else {
                    setLinks(linksStore.filter((item) => item.additional_info == pageData.name));
                }
                break;

            default:
                setLinks(linksStore.filter((item) => item.additional_info == pageData.name));
        }
    };

    useEffect(() => {
        if (Array.isArray(linksStore) && linksStore.length > 0) {
            addPageName(props.isLanding, props.pageName, props.authUser);
            filterLinks(linksStore, pageName);
        }
    }, [pageName.name]);

    return (
        <>
            {pageName && links && linksCodeSnippet && <AuthMenu hasOneButton={links.length === 1}>{links && linksCodeSnippet}</AuthMenu>}
            <PopupNew ref={popupRef} getPopupComponent={(component) => component} />
        </>
    );
};

Auth.propTypes = {
    authUser: PropTypes.bool,
    businessPageName: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null, undefined])]),
    data: PropTypes.oneOfType([PropTypes.array, PropTypes.oneOf([null])]),
    isLanding: PropTypes.bool,
};
const mapStateToProps = (state) => {
    return {
        authUser: state.auth.authUser,
    };
};
export default connect(mapStateToProps, null)(Auth);
