import React from 'react';

import PropTypes from 'prop-types';

import { CommonButton, IconContainer, LinkButton, PseudoButton } from './styles';
import { SpinnerCircle } from '../../../common/SpinnerCircle/SpinnerCircle';
import { PromoIcon } from '../PromoIcon/PromoIcon';

export const PromoButton = React.forwardRef((props, ref) => {
    if (props.type === 'link') {
        return (
            <LinkButton ref={ref} {...props}>
                {props.caption}
                {props.iconName && (
                    <IconContainer>
                        <PromoIcon name={props.iconName} />
                    </IconContainer>
                )}
                {props.withSpinner && <SpinnerCircle isWhite size="16px" />}
            </LinkButton>
        );
    } else if (props.type === 'pseudo') {
        return (
            <PseudoButton {...props}>
                {props.caption}
                {props.iconName && (
                    <IconContainer>
                        <PromoIcon name={props.iconName} />
                    </IconContainer>
                )}
                {props.withSpinner && <SpinnerCircle isWhite size="16px" />}
            </PseudoButton>
        );
    }
    return (
        <CommonButton ref={ref} {...props}>
            {props.caption}
            {props.iconName && (
                <IconContainer>
                    <PromoIcon name={props.iconName} />
                </IconContainer>
            )}
            {props.withSpinner && <SpinnerCircle isWhite size="16px" />}
        </CommonButton>
    );
});

PromoButton.defaultProps = {
    variant: 'primary',
};

PromoButton.propTypes = {
    type: PropTypes.oneOf(['link', 'pseudo', 'button']),
    variant: PropTypes.oneOf(['primary', 'secondary', 'tertiary']),
    isInverted: PropTypes.bool,
    disabled: PropTypes.bool,
    href: PropTypes.string,
    target: PropTypes.string,
    title: PropTypes.string,
    tabIndex: PropTypes.string,
    caption: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    onClick: PropTypes.func,
    iconName: PropTypes.string,
    withSpinner: PropTypes.bool,
};
