import React from 'react';
import { connect } from 'react-redux';

import { initGoogleAnalytics } from 'analytics/analytics';
import { cleverTapPgaViewEvent } from 'analytics/cleverTap';

import {
    toggleModal,
    toggleModalWithTimer,
    togglePopup,
    toggleSnackBar,
    toggleSpinner,
    updateAuthPromoBlock,
    updateCurrentSiteSection,
    updateMessages,
    updateReduxAction,
} from 'actions/appAction';
import { updateAuth } from 'actions/authAction';
import { initCalculator } from 'actions/calculatorAction';
import { updateCards } from 'actions/cardsAction';
import { chatBlockStatus, toggleChat } from 'actions/chatAction';
import { setExtra } from 'actions/extraAction';
import { setNewForm } from 'actions/formAction';
import { setCheque, updatePaymentsByFilter, updateRequestByFilter } from 'actions/historyAction';
import { updateJsMessages, updatePageMessages } from 'actions/messageAction';

import amplitude from 'amplitude-js';

import { AMPLITUDE_API_KEY } from './AmplitudeUtils';
import { initializeClevertap } from '../../analytics/cleverTap';
import { MONITORING_AFTER_SMS_CODE_LOADING_TIME } from '../../constants/amplitude/AmplitudeEvents';
import { TIME_INTERVAL } from '../../constants/amplitude/AmplitudeParamName';
import { SMS_INSERT_TIME } from '../../constants/LocalStorage';
import update from '../../helpers/Immutability';
import { UpdateReduxService } from '../../services/UpdateReduxService';
import WSChat from '../chat/WSChat';

export default function BasePage(PageComponent) {
    class BasePageComponent extends React.Component {
        constructor(props) {
            super(props);

            this.updateReduxService = new UpdateReduxService(props.lang);
            this.updateReduxService.setMessageAction(props.updateJsMessages, props.updatePageMessages);
            this.updateReduxService.setHistoryAction(props.setCheque, props.updatePaymentsByFilter, props.updateRequestByFilter);
            this.updateReduxService.setChatAction(props.chatBlockStatus);
            this.updateReduxService.setAuthAction(props.updateAuth);
            this.updateReduxService.setFormAction(props.setNewForm);
            this.updateReduxService.setCalculatorAction(props.initCalculator);
            this.updateReduxService.setAppAction(
                props.toggleModal,
                props.togglePopup,
                props.toggleSpinner,
                props.updateAuthPromoBlock,
                props.updateCurrentSiteSection,
                props.updateMessages,
            );
            this.updateReduxService.setExtraAction(props.setExtra);
            this.updateReduxService.setCardsAction(props.updateCards);
            this.updateReduxService.setStateOptions(props.stateOptions);

            const data = __isBrowser__ ? window.__INITIAL_DATA__ : props.staticContext ? props.staticContext.json : props.data;

            this.state = { data: data, loading: props.updateRedux };
        }

        componentDidMount() {
            if (this.props.updateRedux) {
                this.updateReduxService.fetchData(this.props.lang, this.props.isAuthZone, this.props.country).then((data) => {
                    this.setState(
                        update(this.state, {
                            data: { $set: data },
                            loading: { $set: false },
                        }),
                    );
                });
            } else {
                this.props.updateReduxAction();
            }
            window.scrollTo(0, 0);
            if (__isBrowser__) {
                if (document.readyState === 'complete') {
                    this.initAnalytics();
                } else {
                    window.addEventListener('load', this.initAnalytics, { once: true });
                }
            }
            if (__isProd__) {
                amplitude.getInstance().init(AMPLITUDE_API_KEY, null, {
                    serverZone: 'EU',
                    serverZoneBasedApi: true,
                });
            } else {
                amplitude.getInstance = () => {
                    return {
                        setUserId: () => {},
                        setUserProperties: () => {},
                        logEvent: () => {},
                    };
                };
            }
            if (__isBrowser__) {
                cleverTapPgaViewEvent(window.location.href, document.title, this.props.app, this.props.auth, this.props.lang);
                const smsInsertTime = Number(localStorage.getItem('smsInsertTime'));
                if (smsInsertTime) {
                    const now = new Date();
                    const pageLoadedTime = now.getTime();
                    const timeInterval = pageLoadedTime - smsInsertTime;
                    amplitude.getInstance().logEvent(MONITORING_AFTER_SMS_CODE_LOADING_TIME, {
                        [TIME_INTERVAL]: timeInterval,
                    });
                    localStorage.removeItem(SMS_INSERT_TIME);
                }
            }
        }

        componentDidUpdate(prevProps) {
            if (this.props.lang !== prevProps.lang) {
                this.updateReduxService.fetchData(this.props.lang, this.props.isAuthZone, this.props.country);
            }
        }

        initAnalytics = () => {
            const activateAnalytics = __isProd__;
            initGoogleAnalytics(activateAnalytics);
            initializeClevertap(activateAnalytics);
        };

        render() {
            return (
                <>
                    <PageComponent {...this.props} loading={this.state.loading} data={this.state.data} />
                    {this.props.stateOptions?.showChat && <WSChat />}
                </>
            );
        }
    }

    const mapStateToProps = (state) => {
        return {
            app: state.app,
            updateRedux: state.app.updateRedux,
            auth: state.auth,
            lang: state.language.lang,
            country: state.app.country,
            showBrexitTerms: state.extra.showBrexitTerms,
        };
    };

    return connect(mapStateToProps, {
        updateReduxAction,
        togglePopup,
        toggleModal,
        toggleModalWithTimer,
        updatePageMessages,
        updateJsMessages,
        updateAuthPromoBlock,
        updatePaymentsByFilter,
        setCheque,
        updateRequestByFilter,
        setNewForm,
        initCalculator,
        updateMessages,
        toggleSpinner,
        updateAuth,
        updateCards,
        setExtra,
        updateCurrentSiteSection,
        toggleChat,
        chatBlockStatus,
        toggleSnackBar,
    })(BasePageComponent);
}
