import React from 'react';
import { connect } from 'react-redux';

import { togglePopup, toggleSpinner } from 'actions/appAction';
import { changeFormField, changeFormFieldValue } from 'actions/formAction';

import AddPhoto from '../common/AddPhoto/AddPhoto';
import { Row } from '../common/Layout/Row';
import { Text } from '../common/Text/Text';

const UploadDocImg = (props) => {
    return (
        <div key={`doc_${props.field.name}`}>
            <Row indent="24px">
                <Text size="subheader" color="mediumGray">
                    {props.field.placeholder}
                </Text>
            </Row>
            <AddPhoto field={props.field} formName={props.formName} />
        </div>
    );
};

const mapStateToProps = ({ form, messages }, ownProps) => ({
    fields: form[ownProps.formName].fields,
});

export default connect(mapStateToProps, {
    changeFormField,
    changeFormFieldValue,
    toggleSpinner,
    togglePopup,
})(UploadDocImg);
