import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { togglePopup } from 'actions/appAction';

import { PopupBoxClose, PopupButtons, PopupContent, PopupHeader } from './styles';
import { DiskButton } from '../DiskButton/DiskButton';

const SimplePopup = (props) => {
    useEffect(() => {
        return () => {
            if (props.onPopupClose) {
                props.onPopupClose();
            }
        };
    }, []);

    return (
        <>
            {!props.withoutCloseButton && (
                <PopupBoxClose>
                    <DiskButton
                        onClick={() => {
                            if (props.handleClose) {
                                props.handleClose();
                            } else if (props.popupRef?.current) {
                                props.popupRef.current?.close();
                            } else {
                                props.togglePopup(false);
                            }
                        }}
                        iconName="cross"
                        iconColor="mediumGray"
                        size="small"
                        type="button"
                        variant="tertiary"
                        raised={false}
                    />
                </PopupBoxClose>
            )}
            <PopupContent>
                {props.text && <PopupHeader textAlign={props.textAlign}>{props.text}</PopupHeader>}
                <PopupButtons withoutBorder={props.withoutBorder}>{props.children}</PopupButtons>
            </PopupContent>
        </>
    );
};
SimplePopup.propTypes = {
    text: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    withoutCloseButton: PropTypes.bool,
    withoutBorder: PropTypes.bool,
    textAlign: PropTypes.string,
    popupRef: PropTypes.shape({ current: PropTypes.object }),
    handleClose: PropTypes.func,
};
SimplePopup.defaultProps = {
    popupRef: null,
};
export default connect(null, { togglePopup })(SimplePopup);
