import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import colors from '../../../theme/colors';
import media from '../../../theme/media';

const SeparatorLine = styled.div`
    background-color: ${colors.greyRgba28};
    height: 1px;
    margin-left: -24px;
    margin-right: -24px;
    margin-top: ${(props) => (props.marginTop ? props.marginTop : '32px')};
    margin-bottom: ${(props) => (props.marginBottom ? props.marginBottom : '32px')};

    ${media.desktop1920} {
        margin-left: -32px;
        margin-right: -32px;
        margin-top: ${(props) => (props.marginTopDesktop ? props.marginTopDesktop : '48px')};
        margin-bottom: ${(props) => (props.marginBottomDesktop ? props.marginBottomDesktop : '48px')};
    }
`;

export const Separator = (props) => {
    return (
        <SeparatorLine
            marginTop={props.marginTop}
            marginBottom={props.marginBottom}
            marginTopDesktop={props.marginTopDesktop}
            marginBottomDesktop={props.marginBottomDesktop}
        />
    );
};

Separator.propTypes = {
    marginTop: PropTypes.string,
    marginBottom: PropTypes.string,
    marginTopDesktop: PropTypes.string,
    marginBottomDesktop: PropTypes.string,
};
