import React from 'react';
import { connect } from 'react-redux';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { CarouselTilesSection } from 'components/no-auth/common/sections/CarouselTilesSection';
import FaqSection from 'components/no-auth/common/sections/FaqSection';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { HeroSection } from 'components/no-auth/common/sections/HeroSection';
import { SingleIconSection } from 'components/no-auth/common/sections/SingleIconSection';
import { TilesSection } from 'components/no-auth/common/sections/TilesSection';
import TrustpilotSection from 'components/no-auth/common/sections/TrustpilotSection';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

const UnlimitedFreeTransferPage = (props) => {
    return (
        <PromoPageContainer bgColor="eggshell02" noIndent>
            {props.heroTitle && props.heroText && props.heroImg && (
                <HeroSection
                    title={props.heroTitle}
                    text={props.heroText}
                    bgImg={props.heroBgImg}
                    img={props.heroImg}
                    data={props.heroData}
                    btnVariant={'primary'}
                    noIconBtn
                    isInvertedBtn
                />
            )}
            <ColoredSectionContainer>
                {props.agenciesTitle && props.agenciesText && <HeadingSection title={props.agenciesTitle} text={props.agenciesText} />}
                {props.benefitsTitle && <HeadingSection title={props.benefitsTitle} />}
                {props.carouselSection && <CarouselTilesSection data={props.carouselSection} />}
                {props.icons && <SingleIconSection data={props.icons} />}
                {props.trustTitle && props.trustText && <HeadingSection title={props.trustTitle} text={props.trustText} />}
                {props.promo && <TilesSection lang={props.lang} country={props.country} data={props.promo} loadingLazy responsive />}
                {props.downloadTitle && (
                    <HeadingSection
                        text={props.downloadText}
                        title={props.downloadTitle}
                        btn={props.downloadBtn}
                        btnMobile={props.downloadBtnMobile}
                        isMobileBtn={props.downloadIsMobileBtn}
                        hasAppsBtns
                    />
                )}
                {props.lang && <TrustpilotSection lang={props.lang} />}
            </ColoredSectionContainer>
            {props.faqTitle && props.questions && <FaqSection title={props.faqTitle} questions={props.questions} />}
        </PromoPageContainer>
    );
};
const mapStateToProps = (state) => {
    return {
        heroTitle: state.extra?.strapiApi?.hero_block?.title,
        heroText: state.extra?.strapiApi?.hero_block?.text,
        heroImg: state.extra?.strapiApi?.hero_block?.img,
        heroBgImg: state.extra?.strapiApi?.hero_block?.bgImg,
        heroData: state.extra?.strapiApi?.hero_block,

        benefitsTitle: state.extra?.strapiApi?.benefits_headline?.title,

        promo: state.extra?.strapiApi?.conditional_tiles,

        trustTitle: state.extra?.strapiApi?.conditional_headline?.[0]?.title,
        trustText: state.extra?.strapiApi?.conditional_headline?.[0]?.text,

        agenciesTitle: state.extra?.strapiApi?.agencies_headline?.title,
        agenciesText: state.extra?.strapiApi?.agencies_headline?.text,

        agencies: state.extra?.strapiApi?.agencies_icons,

        icons: state.extra?.strapiApi?.row_icons,

        carouselSection: state.extra?.strapiApi?.steps_tiles,

        downloadTitle: state.extra?.strapiApi?.download_headline?.title,
        downloadText: state.extra?.strapiApi?.download_headline?.text,
        downloadBtn: state.extra?.strapiApi?.download_headline?.btn,
        downloadBtnMobile: state.extra?.strapiApi?.download_headline?.btn_mobile,
        downloadIsMobileBtn: state.extra?.strapiApi?.download_headline?.isMobileBtn,

        faqTitle: state.extra?.strapiApi?.faq?.title,
        questions: state.extra?.strapiApi?.faq?.questions,

        lang: state.extra.noAuthFooter?.language?.lang,
        country: state.extra.noAuthFooter?.language?.country,
    };
};
export default connect(mapStateToProps)(NotAuthBasePage(UnlimitedFreeTransferPage));
