import EmailConfirm from 'components/no-auth/email/EmailConfirm';
import Unsubscribe from 'components/no-auth/email/Unsubscribe';

import { AUTH_VIEW } from 'constants/ServerConstants';

import { PROMO } from '../constants/EntryPoint';

const noAuthEmail = [
    {
        path: '/email/unsubscribe',
        component: Unsubscribe,
        view: AUTH_VIEW,
        isAuthZone: false,
        stateOptions: {
            entryPoint: PROMO,
        },
    },
    {
        path: '/email/confirm/:code',
        component: EmailConfirm,
        view: AUTH_VIEW,
        isAuthZone: false,
        isPathWithoutFooter: true,
        stateOptions: {
            entryPoint: PROMO,
        },
    },
];

export default noAuthEmail;
