import React from 'react';
import { connect } from 'react-redux';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { PromoSectionContainer } from 'components/no-auth/common/PromoSectionContainer';
import { RichTextTile } from 'components/no-auth/common/RichTextTile/RichTextTile';
import FaqSection from 'components/no-auth/common/sections/FaqSection';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { HeroSection } from 'components/no-auth/common/sections/HeroSection';
import { HighlightedTilesSection } from 'components/no-auth/common/sections/HighlightedTilesSection';
import { ListingTilesSection } from 'components/no-auth/common/sections/ListingTilesSection';
import { StepBlockSection } from 'components/no-auth/common/sections/StepBlockSection';
import { TilesSection } from 'components/no-auth/common/sections/TilesSection';
import VideoSectionV2 from 'components/no-auth/common/sections/VideoSectionV2';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

const PaymentPage = (props) => {
    return (
        <PromoPageContainer bgColor={'eggshell02'} noIndent>
            {props.heroTitle && (
                <HeroSection
                    title={props.heroTitle}
                    text={props.heroText}
                    img={props.heroImg}
                    btnVariant="primary"
                    noIconBtn
                    horizontalLayout
                    data={props.heroData}
                    fetchPriorityHigh
                />
            )}
            {props.videoUrl && (
                <VideoSectionV2
                    sectionTitle={props.videoTitle}
                    btn={props.videoBtn}
                    videoUrl={props.videoUrl}
                    videoTitle={props.videoTitle}
                    videoPreviewImg={props.videoPreviewImg}
                    hasTopPadding
                />
            )}
            <PromoSectionContainer>
                {props.buildBusinessTitle && (
                    <HeadingSection
                        title={props.buildBusinessTitle}
                        text={props.buildBusinessText}
                        btn={props.buildBusinessBtn}
                        btnMobile={props.buildBusinessBtnMobile}
                        isMobileBtn={props.buildBusinessBtnIsMobileBtn}
                    />
                )}
                {props.fees && <HighlightedTilesSection data={props.fees} />}
                {props.tilesTitle && <HeadingSection title={props.tilesTitle} />}
                {props.cards && <TilesSection lang={props.lang} country={props.country} data={props.cards} loadingLazy />}
                {props.richTextHeadline && <HeadingSection title={props.richTextHeadline} />}
                {props.usecasesTitle && <HeadingSection title={props.usecasesTitle} text={props.usecasesText} />}
                {props.team && <ListingTilesSection data={props.team} loadingLazy />}
                {props.richTextDescription && (
                    <RichTextTile img={props.richTextImg} text={props.richTextDescription} isCenteredContent={props.isCenteredContent} loadingLazy />
                )}
                {props.onboardingTitle && (
                    <HeadingSection
                        title={props.onboardingTitle}
                        text={props.onboardingText}
                        btn={props.onboardingBtn}
                        btnMobile={props.onboardingBtnMobile}
                        isMobileBtn={props.onboardingBtnIsMobileBtn}
                    />
                )}
                {props.steps && <StepBlockSection data={props.steps} />}
            </PromoSectionContainer>
            {props.faqTitle && props.questions && <FaqSection title={props.faqTitle} questions={props.questions} />}
            {props.acquisitionTitle && (
                <ColoredSectionContainer bottomPadding bgColor="purple5">
                    <HeadingSection
                        title={props.acquisitionTitle}
                        btn={props.acquisitionBtn}
                        text={props.acquisitionText}
                        isMobileBtn={props.acquisitionBtnIsMobileBtn}
                        sendFrom={'Payment links page'}
                        noBottomIndent
                    />
                </ColoredSectionContainer>
            )}
        </PromoPageContainer>
    );
};
const mapStateToProps = (state) => {
    return {
        heroTitle: state.extra?.strapiApi?.hero_block?.title,
        heroText: state.extra?.strapiApi?.hero_block?.text,
        heroImg: state.extra?.strapiApi?.hero_block?.img,
        heroData: state.extra?.strapiApi?.hero_block,

        fees: state.extra?.strapiApi?.tiles_highlighted,

        buildBusinessTitle: state.extra?.strapiApi?.build_business_headline?.title,
        buildBusinessText: state.extra?.strapiApi?.build_business_headline?.text,
        buildBusinessBtn: state.extra?.strapiApi?.build_business_headline?.btn,
        buildBusinessBtnMobile: state.extra?.strapiApi?.build_business_headline?.btn_mobile,
        buildBusinessBtnIsMobileBtn: state.extra?.strapiApi?.build_business_headline?.isMobileBtn,

        videoTitle: state.extra?.strapiApi?.video_section?.title,
        videoUrl: state.extra?.strapiApi?.video_section?.url,
        videoBtn: state.extra?.strapiApi?.video_section?.btn,
        videoPreviewImg: state.extra?.strapiApi?.video_section?.img,

        cards: state.extra?.strapiApi?.tiles_section,

        onboardingTitle: state.extra?.strapiApi?.onboarding_headline?.title,
        onboardingText: state.extra?.strapiApi?.onboarding_headline?.text,
        onboardingBtn: state.extra?.strapiApi?.onboarding_headline?.btn,
        onboardingBtnMobile: state.extra?.strapiApi?.onboarding_headline?.btn_mobile,
        onboardingBtnIsMobileBtn: state.extra?.strapiApi?.onboarding_headline?.isMobileBtn,

        steps: state.extra?.strapiApi?.onboarding_steps_block,

        team: state.extra?.strapiApi?.team_section,

        acquisitionTitle: state.extra?.strapiApi?.acquisition_headline?.title,
        acquisitionText: state.extra?.strapiApi?.acquisition_headline?.text,
        acquisitionBtn: state.extra?.strapiApi?.acquisition_headline?.btn,
        acquisitionBtnMobile: state.extra?.strapiApi?.acquisition_headline?.btn_mobile,
        acquisitionBtnIsMobileBtn: state.extra?.strapiApi?.acquisition_headline?.isMobileBtn,

        faqTitle: state.extra?.strapiApi?.faq?.title,
        questions: state.extra?.strapiApi?.faq?.questions,

        tilesTitle: state.extra?.strapiApi?.headline1?.title,

        usecasesTitle: state.extra?.strapiApi?.headline2?.title,
        usecasesText: state.extra?.strapiApi?.headline2?.text,

        richTextHeadline: state.extra?.strapiApi?.rich_text_tile?.title,
        richTextImg: state.extra?.strapiApi?.rich_text_tile?.img,
        richTextDescription: state.extra?.strapiApi?.rich_text_tile?.description,
        isCenteredContent: state.extra?.strapiApi?.rich_text_tile?.isCenteredContent,

        lang: state.extra.noAuthFooter?.language?.lang,
        country: state.extra.noAuthFooter?.language?.country,
    };
};
export default connect(mapStateToProps)(NotAuthBasePage(PaymentPage));
