import React from 'react';

import PropTypes from 'prop-types';

import { JobCardContainer, NewCareerPageLocation, NewCareerPageSubTitle, NewCareerPageTitle } from './styles';

export const JobCardV2 = (props) => {
    return (
        <JobCardContainer href={props.href}>
            <NewCareerPageTitle tag="div" variant="subheader" color="gray80">
                {props.title}
            </NewCareerPageTitle>
            <NewCareerPageSubTitle variant="span" color="gray40">
                <span>{props.job}</span>
                <NewCareerPageLocation>{props.location}</NewCareerPageLocation>
            </NewCareerPageSubTitle>
        </JobCardContainer>
    );
};

JobCardV2.propTypes = {
    href: PropTypes.string,
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    job: PropTypes.string,
    location: PropTypes.string,
};
