import React from 'react';
import { Trans } from 'react-i18next';

import { HELP_PAYSEND } from 'constants/UrlConstants';

import { apiFetchData } from 'helpers/api';

import { InfoPage } from '../common/Layout/InfoPage/InfoPage';

class ErrorBoundary extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            errorInfo: null,
        };
    }

    componentDidCatch(error, errorInfo) {
        this.setState({
            error: error,
            errorInfo: errorInfo,
        });
        this.sendErrorsToServer(error, errorInfo);
    }

    sendErrorsToServer(error, errorInfo) {
        const message = {
            name: 'message',
            value: 'JS Error!  in "' + document.URL + '".',
        };
        const exception = {
            name: 'exception',
            value: error?.stack + '\n' + errorInfo?.componentStack,
        };
        apiFetchData('/exception', { message: message, exception: exception });
    }

    render() {
        if (this.state.errorInfo) {
            return (
                <InfoPage
                    iconName="cross-rounded-thin"
                    iconColor="red"
                    headerText={<Trans>ps.500.title</Trans>}
                    secondaryText={<Trans>ps.404.error.description</Trans>}
                    buttonRowType="link"
                    buttonRowCaption={<Trans>ps.404.help</Trans>}
                    buttonRowHref={HELP_PAYSEND}
                />
            );
        }
        return this.props.children;
    }
}

export default ErrorBoundary;
