import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoButton } from 'components/no-auth/common/PromoButton/PromoButton';
import { PromoDangerouslyHtml } from 'components/no-auth/common/PromoDangerouslyHtml/PromoDangerouslyHtml';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { PromoRow } from 'components/no-auth/common/PromoRow/PromoRow';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { TextComponent } from 'components/no-auth/common/sections/styles';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';
import NotFound from 'components/NotFound';

const NewHelpArticlePage = (props) => {
    if (props.notFound) {
        return <NotFound />;
    }

    return (
        <PromoPageContainer>
            <ColoredSectionContainer dimension="average">
                {props.previewTitle && <HeadingSection title={props.previewTitle} dataTestId="help-article-h1-title" />}
                {props.previewText && (
                    <TextComponent>
                        <PromoDangerouslyHtml dangerouslyHtml={props.previewText} dataTestId="help-article-div-text" tag="div" />
                    </TextComponent>
                )}
            </ColoredSectionContainer>
            {props.btnTitle && props.btnLink && (
                <ColoredSectionContainer dimension="average">
                    <PromoRow textAlign="right" indent="adaptive-32-48-64">
                        <PromoButton caption={props.btnTitle} type="link" href={props.btnLink} />
                    </PromoRow>
                </ColoredSectionContainer>
            )}
        </PromoPageContainer>
    );
};
const mapStateToProps = (state) => {
    return {
        notFound: state?.extra?.notFound,
        previewTitle: state?.extra?.strapiApi?.title,
        previewText: state?.extra?.strapiApi?.text,
        btnTitle: state?.extra?.strapiApi?.btn_title,
        btnLink: state?.extra?.strapiApi?.btn_link,
    };
};

NewHelpArticlePage.propTypes = {
    previewTitle: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    previewText: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    btnTitle: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    btnLink: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
};
export default connect(mapStateToProps)(NotAuthBasePage(NewHelpArticlePage));
