import React from 'react';

import PropTypes from 'prop-types';

import { Container, Inner } from './styles';

export const FixedBottomPanel = (props) => {
    return (
        <Container isPressedAboveMenu={props.isPressedAboveMenu} forSpinner={props.forSpinner} fullwidth={props.fullwidth}>
            <Inner isRightAligned={props.isRightAligned} fullwidth={props.fullwidth}>
                {props.children}
            </Inner>
        </Container>
    );
};

FixedBottomPanel.propTypes = {
    isPressedAboveMenu: PropTypes.bool,
    forSpinner: PropTypes.bool,
    isRightAligned: PropTypes.bool,
    fullwidth: PropTypes.bool,
    children: PropTypes.oneOfType([PropTypes.element, PropTypes.arrayOf(PropTypes.element)]),
};
