import React from 'react';
import { connect } from 'react-redux';

import { Accordion } from 'components/no-auth/common/Accordion/Accordion';
import { AccordionFlag, AccordionHeaderContent } from 'components/no-auth/common/Accordion/styles';
import { BackToTopButton } from 'components/no-auth/common/BackToTopButton/BackToTopButton';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { PromoSectionContainer } from 'components/no-auth/common/PromoSectionContainer';
import { PromoText } from 'components/no-auth/common/PromoText/PromoText';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { CommonSection } from 'components/no-auth/common/sections/styles';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';
import RuleItem from 'components/no-auth/rules/RuleItem';

import { FlagIcon } from '../components/common/FlagIcon/FlagIcon';

const RulesPage = (props) => {
    const getItems = () => {
        return props.countriesList.map((c) => {
            const rules = props.rulesData.find((r) => r.id === c.dataId);
            return {
                header: (
                    <AccordionHeaderContent>
                        {c.code_a2 && (
                            <AccordionFlag>
                                <FlagIcon name={`${c.code_a2.toLowerCase()}`} size={'24px'} />
                            </AccordionFlag>
                        )}
                        {c.name && (
                            <PromoText color={'baseColor'} tag={'span'}>
                                {c.name}
                            </PromoText>
                        )}
                    </AccordionHeaderContent>
                ),
                content: <RuleItem rules={rules} />,
            };
        });
    };

    return (
        <PromoPageContainer bgColor="eggshell03">
            <PromoSectionContainer>
                {props.pageTitle && <HeadingSection title={props.pageTitle} />}
                {Array.isArray(props.countriesList) && Array.isArray(props.rulesData) && (
                    <CommonSection>
                        <Accordion items={getItems()} />
                    </CommonSection>
                )}
                {props.backToTopBtn && <BackToTopButton caption={props.backToTopBtn} />}
            </PromoSectionContainer>
        </PromoPageContainer>
    );
};

const mapStateToProps = (state) => {
    return {
        pageTitle: state.extra?.strapiApi?.page_title?.title,
        countriesList: state.extra?.strapiApi?.countries_list,
        rulesData: state.extra?.strapiApi?.links_data,
        backToTopBtn: state.extra?.strapiApi?.page_title?.backToTopBtn,
    };
};
export default connect(mapStateToProps)(NotAuthBasePage(RulesPage));
