import React, { useRef } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { URL_IMAGES_APPS, URL_IMAGES_I } from 'constants/UrlConstants';

import PopupNew from '../../common/Popup/PopupNew';
import QRPopup from '../common/Popups/QRPopup';

const MobileApp = (props) => {
    const popupRef = useRef(null);

    const getIllustration = () => {
        if (props.isRequest) {
            return (
                <picture>
                    <source
                        className="lazyloading"
                        media="(min-width: 1040px)"
                        srcSet={`${URL_IMAGES_I}/apps-${app_currency_to_code}-c.png 1x, ${URL_IMAGES_I}/apps-${app_currency_to_code}-c@2x.png 2x`}
                    />
                    <source
                        className="lazyloading"
                        media="(min-width: 656px)"
                        srcSet={`${URL_IMAGES_I}/apps-${app_currency_to_code}-b.png 1x, ${URL_IMAGES_I}/apps-${app_currency_to_code}-b@2x.png 2x`}
                    />
                    <source
                        className="lazyloading"
                        srcSet={`${URL_IMAGES_I}/apps-${app_currency_to_code}-a.png 1x, ${URL_IMAGES_I}/apps-${app_currency_to_code}-a@2x.png 2x`}
                    />
                    <img
                        className="lazyloaded"
                        alt="Get the Paysend app today"
                        src={`${URL_IMAGES_I}/apps-${app_currency_to_code}-a.png`}
                        loading="lazy"
                        decoding="async"
                        height="735"
                        width="640"
                    />
                </picture>
            );
        }
        return (
            <picture>
                <source
                    className="lazyloading"
                    media="(min-width: 1040px)"
                    srcSet={`${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-c.png 1x, ${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-c@2x.png 2x`}
                />
                <source
                    className="lazyloading"
                    media="(min-width: 656px)"
                    data-srcset={`${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-b.png 1x, ${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-b@2x.png 2x`}
                    srcSet={`${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-b.png 1x, ${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-b@2x.png 2x`}
                />
                <source
                    className="lazyloading"
                    data-srcset={`${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-a.png 1x, ${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-a@2x.png 2x`}
                    srcSet={`${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-a.png 1x, ${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-a@2x.png 2x`}
                />
                <img
                    className="lazyloaded"
                    alt="Get the Paysend app today"
                    data-src={`${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-a.png`}
                    src={`${URL_IMAGES_APPS}/apps-illustration-${currency_from_code}-a.png`}
                    loading="lazy"
                    decoding="async"
                    height="640"
                    width="672"
                />
            </picture>
        );
    };

    const data = props.data;
    const currency_from_code = props.extra?.currency_from_code ?? 'eur-cny';
    const app_currency_to_code = props.extra?.app_currency_to_code ?? 'link';

    return (
        <>
            <section className="section-mobile-apps">
                <div className="box-1280">
                    <div className="inner-content">
                        <div className="lead-text max-620">
                            <h2 className="title1">{data.title}</h2>
                            <p className="maincopy color-medium-gray">{data.text}</p>
                        </div>
                        <figure className="mobile-apps-illustration">{getIllustration()}</figure>
                        <div className="app-links">
                            <div className="row">
                                <a aria-label="App Store" rel="noreferrer" className="app-link__apple" target="_blank" href={props.extra.apple_store}>
                                    <picture>
                                        <source
                                            className="lazyloading"
                                            srcSet={`${URL_IMAGES_I}/store_app-1x.png 1x, ${URL_IMAGES_I}/store_app-2x.png 2x`}
                                            width="160"
                                            height="44"
                                        />
                                        <img
                                            className="ls-is-cached lazyloaded"
                                            alt="App Store"
                                            width="160"
                                            height="44"
                                            src={`${URL_IMAGES_I}/store_app-1x.png`}
                                            loading="lazy"
                                            decoding="async"
                                        />
                                    </picture>
                                </a>
                                <a aria-label="Google Play" rel="noreferrer" className="app-link__google" target="_blank" href={props.extra.google_play}>
                                    <picture>
                                        <source
                                            className=" lazyloading"
                                            srcSet={`${URL_IMAGES_I}/store_google-1x.png 1x, ${URL_IMAGES_I}/store_google-2x.png 2x`}
                                            width="160"
                                            height="44"
                                        />
                                        <img
                                            className=" ls-is-cached lazyloaded"
                                            alt="Google Play"
                                            width="160"
                                            height="44"
                                            src={`${URL_IMAGES_I}/store_google-1x.png`}
                                            loading="lazy"
                                            decoding="async"
                                        />
                                    </picture>
                                </a>
                                {!props.isRequest && (
                                    <>
                                        <a
                                            aria-label="AppGallery"
                                            rel="noreferrer"
                                            className="app-link__huawei"
                                            target="_blank"
                                            href={props.extra.huawei_store}
                                        >
                                            <picture>
                                                <source
                                                    className=" lazyloading"
                                                    srcSet={`${URL_IMAGES_I}/store_huawei-1x.png 1x, ${URL_IMAGES_I}/store_huawei@2x.png 2x`}
                                                    width="160"
                                                    height="44"
                                                />
                                                <img
                                                    className=" ls-is-cached lazyloaded"
                                                    alt="Huawei Play"
                                                    width="160"
                                                    height="44"
                                                    src={`${URL_IMAGES_I}/store_huawei-1x.png`}
                                                    loading="lazy"
                                                    decoding="async"
                                                />
                                            </picture>
                                        </a>
                                        <div className="qr-code__block">
                                            <a
                                                className="maincopy color-GT ce ajaxLinkDecorated"
                                                style={{ cursor: 'pointer' }}
                                                onClick={(e) => {
                                                    e.preventDefault();
                                                    popupRef.current?.open(<QRPopup popupRef={popupRef} />);
                                                }}
                                                id="qr-code"
                                                data-fragments="popup-body"
                                            >
                                                {props.mobileNameUrl}
                                            </a>
                                        </div>
                                    </>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <PopupNew ref={popupRef} getPopupComponent={(component) => component} />
        </>
    );
};

MobileApp.propTypes = {
    data: PropTypes.object.isRequired,
    extra: PropTypes.object,
};
const mapStateToProps = (state) => {
    return {
        mobileNameUrl: state.messages.pageMessages.mobileNameUrl,
        extra: state.extra,
    };
};

export default connect(mapStateToProps, null)(MobileApp);
