import React, { useRef } from 'react';
import { connect } from 'react-redux';

import { DangerouslyHtml } from 'components/common/DangerouslyHtml/DangerouslyHtml';
import PopupButton from 'components/common/Popup/PopupButton';
import SimplePopup from 'components/common/Popup/SimplePopup';

import { togglePopup } from 'actions/appAction';

import { DELIVERY_ALIPAY, DELIVERY_BANK_ACCOUNT, DELIVERY_CASH, DELIVERY_HOME, DELIVERY_INTERAC, DELIVERY_UPI, DELIVERY_WALLET } from 'constants/Constants';
import { AMOUNT_TO, INVITE_CODE_FIELD, PROMO_CODE_FIELD } from 'constants/fieldsName';
import { RULES } from 'constants/UrlConstants';
import {
    CANADA,
    CHINA,
    INDIA,
    KOREA,
    LEBANON,
    MEXICO,
    NIGERIA,
    PAKISTAN,
    PHILIPPINES,
    REPUBLIC_OF_SOUTH_AFRICA,
    USA,
    VIETNAM,
} from 'constants/WellKnownCountries';

import { getLangCountryPrefix, resolveArguments } from 'helpers/utils';

import { FeeTable, FeeTableRightBlock, FeeTableRow, FeeTableSeparator } from './styles';
import { usePageMessages } from '../../../hooks/selectors';
import PopupNew from '../../common/Popup/PopupNew';
import { PromoDangerouslyHtml } from '../../no-auth/common/PromoDangerouslyHtml/PromoDangerouslyHtml';
import { PromoIcon } from '../../no-auth/common/PromoIcon/PromoIcon';
import { PromoText } from '../../no-auth/common/PromoText/PromoText';
import AddPromoOrInviteCode from '../AddPromoOrInviteCode';
import AliPayReduceCommission from '../AliPayReduceCommission';

const SEPARATOR = ':';

const FeeBlock = (props) => {
    const popupRef = useRef(null);

    const {
        feeCmsText,
        feeRateText,
        feeInviteCode,
        feePromoCode,
        feeReduceFee,
        feeLandingCommissionInfo,
        feeLandingCloseTitle,
        feeLandingTermsAndCond,
        feeRatePopupCommentInfo,
        feeRatePopupUsaCommentInfo,
        feeLandingKoreaInfo,
        feeLandingPakistanInfo,
        feeLandingIndiaUPIInfo,
        feeLandingChinaAlipayInfo,
        feeLandingCanadaInteracInfo,
        cashPickupGuide,
        cashHomeGuide,
        cashPickupVietcomBankGuide,
        feeLandingSARInfo,
    } = usePageMessages();

    const paymentForm = props.paymentForm;
    const hasSeparator = paymentForm.description && paymentForm.description.indexOf(SEPARATOR);

    const getRel = (countryToId, deliveryMethod) => {
        let rel = '';
        if (countryToId === KOREA && deliveryMethod === DELIVERY_BANK_ACCOUNT) {
            rel = 'noreferrer noopener nofollower';
        } else if (countryToId === CHINA && deliveryMethod === DELIVERY_ALIPAY) {
            rel = 'noreferrer noopener nofollower';
        } else if (countryToId === VIETNAM && deliveryMethod === DELIVERY_HOME) {
            rel = 'noreferrer noopener nofollower';
        }
        return rel;
    };
    const getTermsButtonText = (countryToId, deliveryMethod) => {
        let textLink = '';

        if (countryToId === KOREA && deliveryMethod === DELIVERY_BANK_ACCOUNT) {
            textLink = feeLandingKoreaInfo;
        } else if (countryToId === PAKISTAN && deliveryMethod === DELIVERY_WALLET) {
            textLink = feeLandingPakistanInfo;
        } else if (countryToId === INDIA && deliveryMethod === DELIVERY_UPI) {
            textLink = feeLandingIndiaUPIInfo;
        } else if (countryToId === CHINA && deliveryMethod === DELIVERY_ALIPAY) {
            textLink = feeLandingChinaAlipayInfo;
        } else if (countryToId === CANADA && deliveryMethod === DELIVERY_INTERAC) {
            textLink = feeLandingCanadaInteracInfo;
        } else if (countryToId === NIGERIA && deliveryMethod === DELIVERY_CASH) {
            textLink = cashPickupGuide;
        } else if (countryToId === PHILIPPINES && deliveryMethod === DELIVERY_CASH) {
            textLink = cashPickupGuide;
        } else if (countryToId === VIETNAM && deliveryMethod === DELIVERY_CASH) {
            textLink = cashPickupVietcomBankGuide;
        } else if (countryToId === VIETNAM && deliveryMethod === DELIVERY_HOME) {
            textLink = cashHomeGuide;
        } else if (countryToId === MEXICO && deliveryMethod === DELIVERY_CASH) {
            textLink = cashPickupGuide;
        } else if (countryToId === REPUBLIC_OF_SOUTH_AFRICA && deliveryMethod === DELIVERY_BANK_ACCOUNT) {
            textLink = feeLandingSARInfo;
        } else {
            textLink = feeLandingTermsAndCond;
        }

        return textLink;
    };

    const getTermsHref = (countryToId, deliveryMethod, country, lang) => {
        let href = '';
        const chinaAlipayUrl = lang === 'zh' ? '//render.alipay.com/p/c/k6r3ebho' : '//render.alipay.com/p/c/k6r8dy25';

        if (countryToId === KOREA && deliveryMethod === DELIVERY_BANK_ACCOUNT) {
            href = 'https://tinyurl.com/Paysend-partner-E9pay';
        } else if (countryToId === INDIA && deliveryMethod === DELIVERY_UPI) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/india`;
        } else if (countryToId === CHINA && deliveryMethod === DELIVERY_ALIPAY) {
            href = chinaAlipayUrl;
        } else if (countryToId === CANADA && deliveryMethod === DELIVERY_INTERAC) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/canada-interac`;
        } else if (countryToId === NIGERIA && deliveryMethod === DELIVERY_CASH) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/cash-pickup-in-nigeria`;
        } else if (countryToId === PHILIPPINES && deliveryMethod === DELIVERY_CASH) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/cash-pickup-in-philippines`;
        } else if (countryToId === VIETNAM && deliveryMethod === DELIVERY_CASH) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/cash-pickup-in-vietcombank`;
        } else if (countryToId === VIETNAM && deliveryMethod === DELIVERY_HOME) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/home-delivery-in-vietnam`;
        } else if (countryToId === MEXICO && deliveryMethod === DELIVERY_CASH) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/cash-pickup-in-mexico`;
        } else if (countryToId === LEBANON && deliveryMethod === DELIVERY_CASH) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/cash-pickup-in-lebanon`;
        } else if (countryToId === REPUBLIC_OF_SOUTH_AFRICA && deliveryMethod === DELIVERY_BANK_ACCOUNT) {
            href = `${getLangCountryPrefix(lang, country)}/help/transfers/bank-transfer-to-south-africa`;
        } else {
            href = `${getLangCountryPrefix(lang, country)}${RULES}`;
        }

        return href;
    };

    const showReduceFee = (isLanding, canUsePromoCode, canUseInviteCode, fields) => {
        if (isLanding || (!canUsePromoCode && !canUseInviteCode)) {
            return false;
        } else if (canUsePromoCode && !canUseInviteCode && fields[PROMO_CODE_FIELD] !== undefined && fields[PROMO_CODE_FIELD].value) {
            return false;
        } else if (canUseInviteCode && !canUsePromoCode && fields[INVITE_CODE_FIELD] !== undefined && fields[INVITE_CODE_FIELD].value) {
            return false;
        } else if (
            canUseInviteCode &&
            canUsePromoCode &&
            fields[INVITE_CODE_FIELD] !== undefined &&
            fields[INVITE_CODE_FIELD].value &&
            fields[PROMO_CODE_FIELD] !== undefined &&
            fields[PROMO_CODE_FIELD].value
        ) {
            return false;
        } else if (
            (fields[INVITE_CODE_FIELD] !== undefined && fields[INVITE_CODE_FIELD].value) ||
            (fields[PROMO_CODE_FIELD] !== undefined && fields[PROMO_CODE_FIELD].value)
        ) {
            return false;
        } else {
            return true;
        }
    };

    const showPromoCodeInfo = (isLanding, fields) => {
        return !isLanding && fields[PROMO_CODE_FIELD] !== undefined && fields[PROMO_CODE_FIELD].value;
    };

    const showInviteCodeInfo = (isLanding, fields) => {
        return !isLanding && fields[INVITE_CODE_FIELD] !== undefined && fields[INVITE_CODE_FIELD].value;
    };

    const calcSum = (amountField) => {
        const getFee = (tariff, sum) => {
            if (tariff.percent === 0 && tariff.fix > 0) {
                return tariff.fix;
            }

            if (tariff.percent > 0 && tariff.fix === 0) {
                return (sum * tariff.percent) / 100;
            }

            return 0;
        };

        const calcFee = (sum) => {
            const tariffs = props.calculator?.commission?.tariffs;
            if (tariffs) {
                let tariff = tariffs.find((t) => t.from <= sum && sum <= t.to);

                if (tariff === undefined && tariffs.length > 1) {
                    const lastTariff = tariffs[tariffs.length - 1];
                    if (lastTariff && lastTariff.from <= sum && lastTariff.to <= sum) {
                        tariff = lastTariff;
                    }
                }

                if (tariff) {
                    return getFee(tariff, sum);
                } else {
                    const [firstTariff] = tariffs;
                    if (!firstTariff?.from && !firstTariff?.to) {
                        return getFee(firstTariff, sum);
                    }
                }

                return 0;
            }
        };

        const rate = props.calculator?.commission?.convertRate;
        const val = amountField.value.toString().replace(/ /g, '');
        const fee = calcFee(Number(val));
        return Number(val) / rate + fee;
    };

    const getFeeInfo = (isLandingRequest) => {
        if (isLandingRequest) {
            const amountField = props.fields[AMOUNT_TO];

            const view = (num) => {
                let [first, second] = num.split('.');
                const reverseSting = (str) => str.split('').reverse().join('');
                if (first) {
                    first = reverseSting(first);
                    first = first.match(/.{1,3}/g).join(' ');
                    first = reverseSting(first);
                }
                return [first, second].join('.');
            };

            const fee = view(calcSum(amountField).toFixed(2));

            return (
                <>
                    <PromoText color="gray80">{fee}&nbsp;</PromoText>
                    <PromoText color="gray80">{props.calculator?.commission?.restrictionFrom?.ccyIso}</PromoText>
                    <PromoIcon name="info-rounded" color="gray80" size="16px" leftIndent="8px" />
                </>
            );
        } else {
            return (
                <>
                    <PromoText color="gray80">{resolveArguments(paymentForm.feeText, [props.feeAmount, props.fromIso])}</PromoText>
                    {paymentForm.feeTextInfo && <PromoIcon name="info-rounded" color="gray80" size="16px" leftIndent="8px" />}
                </>
            );
        }
    };

    const isClickableFeeText = paymentForm.feeTextInfo || props.isLandingRequest;

    const getSeparator = () => {
        return props.disableSeparator ? false : <FeeTableSeparator />;
    };

    const feePopup = (val) => {
        popupRef.current?.open(
            <SimplePopup text={setHTML(val)} popupRef={popupRef}>
                <PopupButton
                    iconName="check"
                    text={feeLandingCloseTitle}
                    onClick={(e) => {
                        e.preventDefault();
                        popupRef.current?.close();
                    }}
                />
            </SimplePopup>,
        );
    };

    const infoPopup = (text) => {
        popupRef.current?.open(
            <SimplePopup text={text} popupRef={popupRef}>
                <PopupButton
                    iconName="arrow-forward"
                    rel={getRel(props.countryToId, props.deliveryMethod)}
                    target={'_blank'}
                    href={getTermsHref(props.countryToId, props.deliveryMethod, props.country, props.lang)}
                    text={getTermsButtonText(props.countryToId, props.deliveryMethod)}
                />
                <PopupButton
                    iconName="check"
                    text={feeLandingCloseTitle}
                    onClick={(e) => {
                        e.preventDefault();
                        popupRef.current?.close();
                    }}
                />
            </SimplePopup>,
        );
    };

    const setHTML = (val) => <DangerouslyHtml dangerouslyHtml={val} />;
    return (
        <>
            <FeeTable isLock={props.isLock}>
                <FeeTableRow
                    cursorPointer={isClickableFeeText}
                    onClick={() => {
                        if (isClickableFeeText) {
                            paymentForm.feeTextInfo ? feePopup(paymentForm.feeTextInfo) : feePopup(feeLandingCommissionInfo);
                        }
                    }}
                >
                    <PromoText ellipsis color="gray80">
                        {feeCmsText}
                    </PromoText>
                    {getSeparator()}
                    <FeeTableRightBlock data-testid="fee-span-fee-info">{getFeeInfo(props.isLandingRequest)}</FeeTableRightBlock>
                </FeeTableRow>
                {(props.canUsePromoCode || props.canUseInviteCode) && (
                    <FeeTableRow>
                        {showInviteCodeInfo(props.isLanding, props.fields) && (
                            <>
                                <PromoText color="gray80">{feeInviteCode}</PromoText>
                                <PromoText color="gray80">{`"${props.fields[INVITE_CODE_FIELD].value}"`}</PromoText>
                            </>
                        )}
                        {showPromoCodeInfo(props.isLanding, props.fields) && (
                            <>
                                <PromoText color="gray80">{feePromoCode}</PromoText>
                                <PromoText color="gray80">{`"${props.fields[PROMO_CODE_FIELD].value}"`}</PromoText>
                            </>
                        )}
                        {getSeparator()}
                        {showReduceFee(props.isLanding, props.canUsePromoCode, props.canUseInviteCode, props.fields) && (
                            <PromoText
                                variant="link"
                                color="primary"
                                cursorPointer
                                onClick={() =>
                                    props.togglePopup(
                                        true,
                                        props.showPopupReduceFee ? (
                                            <AliPayReduceCommission
                                                updateRedux={props.updateRedux}
                                                formName={props.formName}
                                                canUsePromoCode={props.canUsePromoCode}
                                                canUseInviteCode={props.canUseInviteCode}
                                            />
                                        ) : (
                                            <AddPromoOrInviteCode
                                                updateRedux={props.updateRedux}
                                                formName={props.formName}
                                                canUsePromoCode={props.canUsePromoCode}
                                                canUseInviteCode={props.canUseInviteCode}
                                            />
                                        ),
                                    )
                                }
                            >
                                {feeReduceFee}
                            </PromoText>
                        )}
                    </FeeTableRow>
                )}
                {paymentForm.currencyRateText && (props.isLandingRequest || props.isLanding) && (
                    <FeeTableRow
                        cursorPointer
                        onClick={() => infoPopup(setHTML(USA === props.countryId ? feeRatePopupUsaCommentInfo : feeRatePopupCommentInfo))}
                    >
                        <PromoDangerouslyHtml color="gray80" dangerouslyHtml={feeRateText} />
                        {getSeparator()}
                        <FeeTableRightBlock>
                            <PromoText color="gray80">{paymentForm.currencyRateText}</PromoText>
                            <PromoIcon name="info-rounded" color="gray80" size="16px" leftIndent="8px" />
                        </FeeTableRightBlock>
                    </FeeTableRow>
                )}
                {paymentForm.currencyRateText && !props.isLandingRequest && !props.isLanding && (
                    <FeeTableRow
                        cursorPointer
                        onClick={() => {
                            feePopup(feeRateText);
                            infoPopup(setHTML(USA === props.countryId ? feeRatePopupUsaCommentInfo : feeRatePopupCommentInfo));
                        }}
                    >
                        <PromoDangerouslyHtml color="gray80" dangerouslyHtml={feeRateText} />
                        {getSeparator()}
                        <FeeTableRightBlock>
                            <PromoText color="gray80">{paymentForm.currencyRateText}</PromoText>
                            <PromoIcon name="info-rounded" color="gray80" size="16px" leftIndent="8px" />
                        </FeeTableRightBlock>
                    </FeeTableRow>
                )}
                {paymentForm.description && (
                    <FeeTableRow
                        cursorPointer={paymentForm.comment}
                        onClick={() => {
                            if (paymentForm.comment) {
                                infoPopup(setHTML(paymentForm.comment));
                            }
                        }}
                    >
                        {hasSeparator && <PromoText color="gray80">{paymentForm.description.split(SEPARATOR)[0]}:</PromoText>}
                        {getSeparator()}
                        <FeeTableRightBlock data-testid="fee-span-description">
                            <PromoText color="gray80">{hasSeparator ? paymentForm.description.split(SEPARATOR)[1] : paymentForm.description}</PromoText>
                            {paymentForm.comment && <PromoIcon name="info-rounded" color="gray80" size="16px" leftIndent="8px" />}
                        </FeeTableRightBlock>
                    </FeeTableRow>
                )}
            </FeeTable>
            <PopupNew ref={popupRef} getPopupComponent={(component) => component} />
        </>
    );
};

FeeBlock.defaultProps = {
    disableSeparator: false,
    isLanding: false,
    isLock: false,
};

const mapStateToProps = (state) => {
    return {
        calculator: state.calculator,
        lang: state.language.lang,
        country: state.app.country,
        countryToId: state.calculator?.countryMenu?.countryTo?.id,
        deliveryMethod: state.calculator?.paymentForm?.deliveryMethod,
    };
};
export default connect(mapStateToProps, { togglePopup })(FeeBlock);
