import React, { useRef } from 'react';

import PropTypes from 'prop-types';

import { QR_POPUP } from 'constants/ComponentNames';

import { useMatchMedia } from 'hooks/useMatchMedia';
import promoBreakpoints from 'theme/promoBreakpoints';

import { PromoButton } from './PromoButton';
import PopupNew from '../../../common/Popup/PopupNew';
import QRPopup from '../Popups/QRPopup';
import RequestFormPopup from '../Popups/RequestFormPopup/RequestFormPopup';

const PromoButtonExtended = (props) => {
    const isTablet = useMatchMedia(promoBreakpoints.tablet744);
    const btn = !isTablet && props.isMobileBtn ? props.btnMobile : props.btn;
    const popupRef = useRef(null);

    const selectPopupSnippet = (popupName) => {
        if (popupName === QR_POPUP) {
            popupRef.current?.open(<QRPopup hasAppsBtns={true} popupRef={popupRef} />);
        } else {
            popupRef.current?.open(<RequestFormPopup sendFrom={props.sendFrom} popupRef={popupRef} />);
        }
    };

    return (
        <>
            {btn && btn.title && btn.btn_modal && btn.isModal && (
                <PromoButton
                    onClick={() => selectPopupSnippet(btn.btn_modal)}
                    caption={btn.title}
                    variant={props.variant}
                    iconName={props.iconName}
                    isInverted={props.isInverted}
                />
            )}
            {btn && btn.title && !btn.isModal && (
                <PromoButton type={'link'} href={btn.url} caption={btn.title} variant={props.variant} iconName={props.iconName} isInverted={props.isInverted} />
            )}
            <PopupNew ref={popupRef} getPopupComponent={(component) => component} />
        </>
    );
};
PromoButtonExtended.propTypes = {
    btn: PropTypes.object,
    btnMobile: PropTypes.object,
    isMobileBtn: PropTypes.bool,
    variant: PropTypes.oneOf(['primary', 'secondary', 'tertiary']),
    iconName: PropTypes.string,
    isInverted: PropTypes.bool,
};

export default PromoButtonExtended;
