import { Link } from 'react-router-dom';

import styled, { css } from 'styled-components';

import colors from '../../../theme/colors';
import media from '../../../theme/media';

// Default variant - tertiary
const getBackground = (props) => {
    switch (props.variant) {
        case 'primary':
            return props.disabled ? colors.purpleRgba24 : colors.purple;
        case 'secondary':
            return props.$isInverted ? colors.whiteRgba24 : colors.white;
        case 'tertiary':
            return colors.white;
        case 'error':
            return colors.wildWatermelon08;
        case 'errorHint':
            return colors.white;
        default:
            return colors.white;
    }
};

const getHoverBackground = (props) => {
    switch (props.variant) {
        case 'primary':
            return props.disabled ? colors.purpleRgba24 : colors.lightPurple;
        case 'secondary':
            return props.$isInverted ? colors.whiteRgba48 : colors.purpleRgba08;
        case 'tertiary':
            return colors.superLightGray;
        case 'error':
            return colors.wildWatermelon12;
        case 'errorHint':
            return colors.wildWatermelon08;
        default:
            return colors.superLightGray;
    }
};

const getBoxShadow = (props) => {
    const raised = props.raised && props.raised.toString() === 'true' && !props.disabled;
    if (props.variant === 'primary') {
        return raised ? `0px 8px 8px ${colors.purpleRgba16}` : 'none';
    }
    return raised ? `0 0 1px ${colors.codGray12}, 0 0 8px ${colors.codGray04}, 0 8px 8px ${colors.codGray04}` : 'none';
};

const getSize = (props) => {
    switch (props.size) {
        case 'small':
            return '28px';
        case 'tight':
            return '40px';
        case 'fit':
            return '56px';
        default:
            return '40px';
    }
};

const getDesktopSize = (props) => {
    switch (props.size) {
        case 'small':
            return '32px';
        case 'tight':
            return '56px';
        case 'fit':
            return '112px';
        default:
            return '56px';
    }
};

const CommonCss = css`
    display: inline-flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    background-color: ${(props) => getBackground(props)};
    border: none;
    border-radius: 50%;
    width: ${(props) => getSize(props)};
    height: ${(props) => getSize(props)};
    padding: 0;
    box-shadow: ${(props) => getBoxShadow(props)};
    pointer-events: auto;
    cursor: ${(props) => (props.disabled ? 'default' : 'pointer')};
    margin-right: ${(props) => (props.$marginRight ? props.$marginRight : null)};
    margin-top: ${(props) => (props.$marginTop ? props.$marginTop : null)};

    &:hover {
        background-color: transparent;
    }

    @media (hover: hover) {
        &:hover,
        &:active {
            background-color: ${(props) => getHoverBackground(props)};
        }
    }

    &:focus {
        background-color: ${(props) => getBackground(props)};
    }

    ${media.tablet648} {
        width: ${(props) => (props.ispromo ? getDesktopSize(props) : getSize(props))};
        height: ${(props) => (props.ispromo ? getDesktopSize(props) : getSize(props))};
    }

    ${media.desktop1920} {
        width: ${(props) => getDesktopSize(props)};
        height: ${(props) => getDesktopSize(props)};
    }
`;

export const CommonButton = styled.button`
    ${CommonCss};
`;
export const LinkButton = styled.a`
    ${CommonCss};
`;
export const ReactLinkButton = styled(Link)`
    ${CommonCss};
`;
