import { useSelector } from 'react-redux';

export function useForm(formName) {
    return useSelector(({ form }) => form[formName]);
}

export function useLanguage() {
    return useSelector(({ language }) => language);
}

export function useFields(formName) {
    return useSelector(({ form }) => form[formName]?.fields);
}

export function usePageMessages() {
    return useSelector(({ messages }) => messages.pageMessages);
}

export function useJsMessages() {
    return useSelector(({ messages }) => messages.jsMessages);
}

export function useApp() {
    return useSelector(({ app }) => app);
}

export function useAuth() {
    return useSelector(({ auth }) => auth);
}

export function useExtra() {
    return useSelector(({ extra }) => extra);
}

export function useRequest() {
    return useSelector(({ request }) => request);
}
export function useLogin() {
    return useSelector(({ login }) => login);
}
export function useHistoryRecords() {
    return useSelector(({ history }) => history?.paymentHistory);
}
export function useCheckMeta() {
    return useSelector(({ history }) => history?.cheque);
}
export function useCheck() {
    return useSelector(({ history }) => history?.cheque?.cheque);
}
