import React, { useEffect, useRef, useState } from 'react';

import { SearchBar } from './components/SearchBar/SearchBar';
import { CountryItemPanel } from '../auth/common/components/CountryItemPanel/CountryItemPanel';
import { NotFoundResult } from '../auth/common/search/NotFoundResult';

const CountryDropBoxList = (props) => {
    const searchRef = useRef(null);
    const [itemsList, setItems] = useState(props.items);

    useEffect(() => {
        searchRef.current.focus();
    }, []);

    const search = (e) => {
        const term = e.target.value;
        const resultList = props.items.filter((item) => {
            return (
                term === undefined ||
                (item !== null && item.name !== null && item.name.toLowerCase().indexOf(term.toLowerCase()) >= 0) ||
                (item.synonym !== null && item.synonym.toLowerCase().indexOf(term.toLowerCase()) >= 0)
            );
        });
        setItems(resultList);
    };

    const easySearch = (e) => {
        const term = e.target.value;
        const resultList = props.items.filter((item) => {
            return term === undefined || (item !== null && item.name !== null && item.name.toLowerCase().indexOf(term.toLowerCase()) >= 0);
        });
        setItems(resultList);
    };

    return (
        <>
            <SearchBar
                placeholder={props.searchPlaceholder}
                onChange={props.isStrapiField ? easySearch : search}
                id={`search_field_${props.name}_${props.id}`}
                onCloseButtonClick={props.onCloseButtonClick}
                searchRef={searchRef}
            />

            {itemsList.length === 0 && <NotFoundResult notFoundMessage={props.searchDropBoxEmpty} />}
            {itemsList.length > 0 &&
                itemsList.map((item) => {
                    return (
                        <div key={`phone_countries_list_${item.code}`} id={`reg-country-${item.code}`}>
                            <CountryItemPanel onClick={(e) => props.selectItem(e, item)} countryName={item.name} flagIconName={item.code} />
                        </div>
                    );
                })}
        </>
    );
};
export default CountryDropBoxList;
