import { CHECK_BONUS_BALANCE } from 'constants/UrlConstants';

import { fetchData } from 'helpers/api';

export const UPDATE_AUTH = 'UPDATE_AUTH';
export const UPDATE_BONUS_BALANCE = 'UPDATE_BONUS_BALANCE';

export const updateAuth = (data) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: UPDATE_AUTH,
                data: data,
            }),
        );
    };
};
export const updateBonusBalance = () => {
    return (dispatch) => {
        return Promise.resolve(
            fetchData(CHECK_BONUS_BALANCE, 'GET').then((json) => {
                dispatch({
                    type: UPDATE_BONUS_BALANCE,
                    bonusAmount: json.balance,
                });
            }),
        );
    };
};
