import React, { useEffect, useRef, useState } from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import SearchAddressNotFoundPopup from 'components/auth/send/popups/SearchAddressNotFoundPopup';

import { DropBoxService } from 'services/DropBoxService';

import { toLowerCase } from 'helpers/utils';

import { useExtra, useLogin, usePageMessages } from 'hooks/selectors';

import { InputSearch } from './components/InputSearch';
import { SearchBar } from './components/SearchBar/SearchBar';
import colors from '../../theme/colors';
import media from '../../theme/media';
import { CountryItemPanel } from '../auth/common/components/CountryItemPanel/CountryItemPanel';
import { Icon } from '../common/Icon/Icon';
import { EmptyPage } from '../common/Layout/EmptyPage/EmptyPage';
import { HeaderPanel } from '../common/Layout/HeaderPanel/HeaderPanel';
import { Panel } from '../common/Panel/Panel';
import { PanelContent, PanelFrame, PanelRow, PanelText } from '../common/Panel/styles';
import PopupNew from '../common/Popup/PopupNew';
import CountryOfResidencePopup from '../registration/CountryOfResidencePopup';
import ModalComponent from '../utils/ModalComponent';

const GroupTitle = styled.div`
    padding: 12px 24px;
    background: ${colors.superLightGray};
    color: ${colors.mediumGray};
    margin: 0 -24px;

    ${media.desktop1920} {
        padding: 16px 32px;
        margin: 0 -32px;
    }
`;

const InputDropBoxList = (props) => {
    const searchRef = useRef(null);
    const popupRef = useRef(null);
    const [itemsList, setItems] = useState(props.items);
    const { bankNotInList } = usePageMessages();
    const [isFullViewSearch, toggleIsFullViewSearch] = useState(false);
    const { countryCode } = useLogin();
    const { allCountries } = useExtra();

    const toggleSearch = (e, show) => {
        e.preventDefault();
        if (!show) {
            setItems(props.items);
        }
        toggleIsFullViewSearch(show);
    };

    useEffect(() => {
        if (isShowSearch() && searchRef && searchRef.current) {
            setTimeout(() => {
                searchRef.current.focus();
            }, 100);
        }
    }, []);

    const isShowSearch = () => {
        return Array.isArray(props.items) && props.items.length >= 10;
    };

    const search = (e) => {
        const term = e.target.value;
        const resultList = props.items.filter((item) => {
            return term === undefined || (item !== null && item.text !== null && toLowerCase(item.text).indexOf(toLowerCase(term)) >= 0);
        });

        setItems(resultList);
    };

    const getEmptyList = () => {
        const unlistedItem = DropBoxService.getUnlistedBankItem(props.items);

        const getEmptyPageButtonCaption = () => {
            if (unlistedItem) {
                return bankNotInList;
            } else if (props.showAddressNotFound) {
                return props.notFoundAddress;
            } else return null;
        };

        const getEmptyPageButtonClick = () => {
            if (unlistedItem) {
                return (e) => props.selectItem(e, unlistedItem);
            } else if (props.showAddressNotFound) {
                return (e) => {
                    e.preventDefault();
                    props.togglePopup(true, <SearchAddressNotFoundPopup iconName={props.addressNotFoundIcon} onClick={props.clickNotFoundBtn} />);
                };
            } else return null;
        };

        return (
            <>
                <EmptyPage
                    iconName="ghost"
                    emptyText={props.searchDropBoxEmpty}
                    emptyButtonCaption={getEmptyPageButtonCaption()}
                    emptyButtonClick={getEmptyPageButtonClick()}
                />
            </>
        );
    };

    const getGroupName = (groups, id) => {
        const group = groups.find((g) => g.id === id);
        return group ? group.value : '';
    };

    const getFlagIcon = (item) => {
        return allCountries ? allCountries[item].code : '';
    };

    const getRegularList = (regularItems) => {
        let isAddGroupTitle = Array.isArray(props.groups);
        let prevGropId = null;

        return regularItems.map((item) => {
            const showGroupTitle = isAddGroupTitle && prevGropId !== item.group;
            const ulb = DropBoxService.isUnlistedBank(item);
            prevGropId = item.group;
            return (
                <React.Fragment key={`bank_name_from_list_${item.value}`}>
                    {showGroupTitle && <GroupTitle>{getGroupName(props.groups, item.group)}</GroupTitle>}
                    {props.isCountrySelectionElementType ? (
                        <CountryItemPanel
                            flagIconName={getFlagIcon(item.value)}
                            isSelectedCountry={props.selectedValue === item.value}
                            countryName={item.text}
                            onClick={(e) => {
                                popupRef.current?.open(
                                    <CountryOfResidencePopup
                                        popupRef={popupRef}
                                        selectedCountry={item.value}
                                        loginCountryCode={countryCode}
                                        residenceCountryId={item.value}
                                        closeDropbox={props.closeDropbox}
                                    />,
                                );
                                props.selectItem(e, item, true);
                            }}
                        />
                    ) : (
                        <Panel onClick={(e) => props.selectItem(e, item)} isHoverable negativeMargin>
                            <PanelFrame>
                                <PanelContent>
                                    <PanelRow>
                                        <PanelText multiline purpletext={ulb}>
                                            {ulb ? bankNotInList : item.text}
                                        </PanelText>
                                    </PanelRow>
                                </PanelContent>
                                {props.selectedValue == item.value && <Icon name="check" color="purple" leftIndent="8px" />}
                            </PanelFrame>
                        </Panel>
                    )}
                </React.Fragment>
            );
        });
    };

    const getList = (items) => {
        if (!items || items.length === 0) {
            return getEmptyList();
        } else if (items && items.length > 0) {
            return getRegularList(items);
        }
    };

    return (
        <>
            {!isFullViewSearch ? (
                <ModalComponent isIconClose onCloseIconClick={props.onCloseIconClick}>
                    <HeaderPanel title={props.selectTitle ? props.selectTitle : props.placeholder} />
                    {isShowSearch() && <InputSearch placeholder={props.searchPlaceholder} toggle={toggleSearch} />}
                    {getList(itemsList)}
                </ModalComponent>
            ) : (
                <ModalComponent canCloseModal={false}>
                    <SearchBar
                        placeholder={props.searchPlaceholder}
                        onChange={search}
                        id={`search_field_${props.name}_${props.id}`}
                        onCloseButtonClick={toggleSearch}
                        searchRef={searchRef}
                    />
                    {getList(itemsList)}
                </ModalComponent>
            )}
            <PopupNew ref={popupRef} overflowHidden={true} />
        </>
    );
};

InputDropBoxList.propTypes = {
    showAddressNotFound: PropTypes.bool,
    addressNotFoundIcon: PropTypes.string,
    clickNotFoundBtn: PropTypes.func,
};

export default InputDropBoxList;
