export const UPDATE_PAGE_MESSAGES = 'UPDATE_PAGE_MESSAGES';
export const UPDATE_JS_MESSAGES = 'UPDATE_JS_MESSAGES';
export const UPDATE_HEADER_TITLE = 'UPDATE_HEADER_TITLE';
export const UPDATE_HEADER_INFO = 'UPDATE_HEADER_INFO';
export const UPDATE_HEADER_GRAY_TITLE = 'UPDATE_HEADER_GRAY_TITLE';

export const updatePageMessages = (pageMessages) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: UPDATE_PAGE_MESSAGES,
                pageMessages: pageMessages,
            }),
        );
    };
};
export const updateJsMessages = (jsMessages) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: UPDATE_JS_MESSAGES,
                jsMessages: jsMessages,
            }),
        );
    };
};
export const updateHeaderTitle = (headerTitle) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: UPDATE_HEADER_TITLE,
                headerTitle: headerTitle,
            }),
        );
    };
};
