import React from 'react';

import PropTypes from 'prop-types';

import { FlagIcon } from 'components/common/FlagIcon/FlagIcon';

import { CountryLink, CountryName, FlagWrapper } from './styles';

const PromoCountryItemPlain = (props) => {
    return (
        <CountryLink href={props.href}>
            <FlagWrapper>
                <FlagIcon name={props.flagName} size={'24px'} />
            </FlagWrapper>
            <CountryName tag="div" variant="subheader" color="gray40" ellipsis nowrap>
                {props.countryName}
            </CountryName>
        </CountryLink>
    );
};
PromoCountryItemPlain.propTypes = {
    href: PropTypes.string,
    flagName: PropTypes.string,
    countryName: PropTypes.string,
};
export default PromoCountryItemPlain;
