import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import Spinner from 'components/utils/Spinner';

import { togglePopup } from 'actions/appAction';

import { ComplexButtonPanel, PopupBody, PopupBoxClose, SecondaryButton } from './styles';
import { Button } from '../Button/Button';
import { DiskButton } from '../DiskButton/DiskButton';

const getButtonType = (primaryButtonType) => {
    switch (primaryButtonType) {
        case 'link':
            return 'link';
        case 'button':
            return 'button';
        case 'submit':
            return 'submit';
        default:
            return 'button';
    }
};

const ComplexPopup = (props) => {
    return (
        <>
            {!props.withoutCloseButton && (
                <PopupBoxClose>
                    <DiskButton
                        onClick={() => {
                            if (props.handleClose) {
                                props.handleClose();
                            } else if (props.popupRef?.current) {
                                props.popupRef.current?.close();
                            } else {
                                props.togglePopup(false);
                            }
                        }}
                        iconName="cross"
                        iconColor="mediumGray"
                        size="small"
                        type="button"
                        variant="tertiary"
                        raised={false}
                    />
                </PopupBoxClose>
            )}

            <PopupBody textCenter={props.textCenter} hasBottomPanel={props.hasBottomPanel} hasPseudoTable={props.hasPseudoTable}>
                {props.isShowSpinner && <Spinner forceShow insidePopup isEmptyText />}
                {props.children}
                {!props.withoutButton && (
                    <ComplexButtonPanel withoutButton={props.withoutButton}>
                        {props.secondaryButtonText && (
                            <SecondaryButton>
                                <Button
                                    type={getButtonType(props.secondaryButtonType)}
                                    href={props.secondaryButtonHref ? props.secondaryButtonHref : null}
                                    target={props.secondaryButtonTarget ? props.secondaryButtonTarget : null}
                                    tabIndex={props.secondaryButtonTabIndex ? props.secondaryButtonTabIndex : null}
                                    size="fit"
                                    variant="secondary"
                                    raised={true}
                                    caption={props.secondaryButtonText}
                                    onClick={(e) => {
                                        if (props.onSecondaryButtonClick) {
                                            props.onSecondaryButtonClick(e);
                                        }
                                    }}
                                />
                            </SecondaryButton>
                        )}
                        {props.primaryButtonText && (
                            <Button
                                type={getButtonType(props.primaryButtonType)}
                                href={props.primaryButtonHref ? props.primaryButtonHref : null}
                                target={props.primaryButtonTarget ? props.primaryButtonTarget : null}
                                tabIndex={props.primaryButtonTabIndex ? props.primaryButtonTabIndex : null}
                                form={props.primaryButtonForm ? props.primaryButtonForm : null}
                                disabled={props.primaryButtonDisabled}
                                ref={props.formRef}
                                size="fit"
                                variant="primary"
                                raised={true}
                                caption={props.primaryButtonText}
                                onClick={(e) => {
                                    if (props.onPrimaryButtonClick) {
                                        props.onPrimaryButtonClick(e);
                                    }
                                }}
                            />
                        )}
                        {props.precheckPopupButtons}
                    </ComplexButtonPanel>
                )}
            </PopupBody>
        </>
    );
};
ComplexPopup.defaultProps = {
    popupRef: null,
};
ComplexPopup.propTypes = {
    handleClose: PropTypes.func,
    popupRef: PropTypes.shape({ current: PropTypes.object }),
    withoutCloseButton: PropTypes.bool,
    textCenter: PropTypes.bool,
    hasBottomPanel: PropTypes.bool,
    hasPseudoTable: PropTypes.bool,
    withoutButton: PropTypes.bool,
    secondaryButtonText: PropTypes.string,
    secondaryButtonType: PropTypes.string,
    secondaryButtonHref: PropTypes.string,
    secondaryButtonTarget: PropTypes.string,
    secondaryButtonTabIndex: PropTypes.string,
    onSecondaryButtonClick: PropTypes.func,
    primaryButtonText: PropTypes.string,
    primaryButtonType: PropTypes.string,
    primaryButtonHref: PropTypes.string,
    primaryButtonTarget: PropTypes.string,
    primaryButtonTabIndex: PropTypes.string,
    primaryButtonForm: PropTypes.string,
    primaryButtonDisabled: PropTypes.bool,
    onPrimaryButtonClick: PropTypes.func,
};

const mapStateToProps = (state) => ({
    isShowSpinner: state.app.isShowSpinner,
});
export default connect(mapStateToProps, { togglePopup })(ComplexPopup);
