import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import PromoButtonExtended from 'components/no-auth/common/PromoButton/PromoButtonExtended';
import { PromoDangerouslyHtml } from 'components/no-auth/common/PromoDangerouslyHtml/PromoDangerouslyHtml';
import { PromoSectionContainer } from 'components/no-auth/common/PromoSectionContainer';
import PromoIllustration from 'components/no-auth/common/widgets/Illustration/PromoIllustration';

import promoColors from 'theme/promoColors';
import promoMedia from 'theme/promoMedia';

const HeroContainer = styled.div`
    padding-top: 32px;
    padding-bottom: ${(props) => (props.img && !props.horizontalLayout ? null : '32px')};
    text-align: center;
    box-sizing: border-box;
    background: ${(props) => promoColors[props.bgColor]};
    background-image: ${(props) => (props.bgImg ? `url(${props.bgImg[0].url})` : null)};
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: ${(props) => (props.img ? 'space-between' : 'center')};
    min-height: ${(props) => (props.bgImg ? '600px' : null)};
    overflow: hidden;

    ${promoMedia.retina} {
        background-image: ${(props) => (props.bgImg ? `url(${props.bgImg[1].url})` : null)};
    }

    ${promoMedia.tablet744} {
        background-image: ${(props) => (props.bgImg ? `url(${props.bgImg[2].url})` : null)};
        padding-top: 48px;
        padding-bottom: ${(props) => (props.img && !props.horizontalLayout ? null : '48px')};
        min-height: ${(props) => (props.bgImg ? '540px' : null)};
    }

    ${promoMedia.retina744} {
        background-image: ${(props) => (props.bgImg ? `url(${props.bgImg[3].url})` : null)};
    }

    ${promoMedia.desktop1440} {
        background-image: ${(props) => (props.bgImg ? `url(${props.bgImg[4].url})` : null)};
        text-align: ${(props) => (props.horizontalLayout ? 'left' : 'center')};
        padding-top: 64px;
        padding-bottom: ${(props) => (props.img && !props.horizontalLayout ? null : '64px')};
        min-height: ${(props) => (props.bgImg ? '718px' : null)};
    }

    ${promoMedia.retina1440} {
        background-image: ${(props) => (props.bgImg ? `url(${props.bgImg[5].url})` : null)};
    }
`;

const HeroContent = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;

    ${promoMedia.desktop1440} {
        flex-direction: ${(props) => (props.horizontalLayout ? 'row' : 'column')};
    }
`;

const HorizontalImg = styled.div`
    ${promoMedia.desktop1440} {
        margin-left: 38px;
        width: 520px;
        height: 520px;
        flex-shrink: 0;
    }
`;

const HeroTitle = styled.div`
    margin-bottom: 16px;

    ${promoMedia.tablet744} {
        margin-bottom: 24px;
    }

    ${promoMedia.desktop1440} {
        margin-bottom: 32px;
        margin-top: ${(props) => (props.horizontalLayout ? '64px' : null)};
    }
`;

const HeroSubtitle = styled.div`
    max-width: 335px;
    margin: 0 auto 16px;

    ${promoMedia.tablet744} {
        max-width: 504px;
        margin: 0 auto 24px;
    }

    ${promoMedia.desktop1440} {
        max-width: 893px;
        margin: 0 auto 32px;
    }
`;

const BtnContainer = styled.div`
    margin-bottom: ${(props) => (props.indent ? '16px' : null)};

    ${promoMedia.tablet744} {
        margin-bottom: ${(props) => (props.indent ? '24px' : null)};
    }

    ${promoMedia.desktop1440} {
        margin-bottom: ${(props) => (props.indent ? '32px' : null)};
    }
`;

export const HeroSection = (props) => {
    return (
        <HeroContainer bgColor={props.data?.color?.bgColor} bgImg={props.bgImg} img={props.img} horizontalLayout={props.horizontalLayout}>
            <PromoSectionContainer>
                <HeroContent horizontalLayout={props.horizontalLayout}>
                    <div>
                        {props.title && (
                            <HeroTitle horizontalLayout={props.horizontalLayout}>
                                <PromoDangerouslyHtml
                                    tag="h1"
                                    variant="h1"
                                    color={props.data?.color?.whiteText ? 'white' : props.data?.color?.titleColor}
                                    gradientColor={props.data?.color?.whiteText ? null : props.data?.color?.titleGradient}
                                    dangerouslyHtml={props.title}
                                />
                            </HeroTitle>
                        )}
                        {props.text && (
                            <HeroSubtitle>
                                <PromoDangerouslyHtml
                                    variant="subheader"
                                    color={props.data?.color?.whiteText ? 'white' : 'gray80'}
                                    dangerouslyHtml={props.text}
                                />
                            </HeroSubtitle>
                        )}
                        {props.data?.btn && (
                            <BtnContainer indent={props.img}>
                                <PromoButtonExtended
                                    btn={props.data?.btn}
                                    btnMobile={props.data?.btn_mobile}
                                    isMobileBtn={props.data?.isMobileBtn}
                                    variant={props.btnVariant}
                                    isInverted={props.isInvertedBtn}
                                    iconName={props.noIconBtn ? '' : props.iconName}
                                />
                            </BtnContainer>
                        )}
                    </div>
                    {props.horizontalLayout && props.img && (
                        <HorizontalImg>
                            <PromoIllustration img={props.img} fetchPriorityHigh={props.fetchPriorityHigh} />
                        </HorizontalImg>
                    )}
                </HeroContent>
            </PromoSectionContainer>
            {!props.horizontalLayout && props.img && (
                <PromoSectionContainer dimension="wide">
                    <PromoIllustration img={props.img} fetchPriorityHigh={props.fetchPriorityHigh} />
                </PromoSectionContainer>
            )}
        </HeroContainer>
    );
};

HeroSection.defaultProps = {
    iconName: 'arrow-right',
    btnVariant: 'tertiary',
};

HeroSection.propTypes = {
    bgImg: PropTypes.array,
    bgColor: PropTypes.oneOf([
        'baseColor',
        'white',
        'primary',
        'primaryB2B',
        'purple80',
        'purple40',
        'purple20',
        'purple5',
        'gray80',
        'gray40',
        'gray20',
        'gray5',
        'aubergine01',
        'aubergine02',
        'aubergine03',
        'eggshell01',
        'eggshell02',
        'eggshell03',
        'passionFruit01',
        'passionFruit02',
        'passionFruit03',
        'oceanBlue01',
        'oceanBlue02',
        'oceanBlue03',
        'blueberryBlue01',
        'blueberryBlue02',
        'blueberryBlue03',
        'jade01',
        'jade02',
        'jade03',
    ]),
    title: PropTypes.string,
    titleGradient: PropTypes.oneOf([
        'aubergine01',
        'aubergine02',
        'aubergine03',
        'passionFruit01',
        'passionFruit02',
        'passionFruit03',
        'oceanBlue01',
        'oceanBlue02',
        'oceanBlue03',
        'blueberryBlue01',
        'blueberryBlue02',
        'blueberryBlue03',
        'jade01',
        'jade02',
        'jade03',
    ]),
    titleColor: PropTypes.oneOf(['baseColor', 'white', 'primary', 'primaryB2B', 'gray80', 'gray40']),
    text: PropTypes.string,
    img: PropTypes.array,
    iconName: PropTypes.string,
    whiteText: PropTypes.bool,
    btnVariant: PropTypes.oneOf(['primary', 'secondary', 'tertiary']),
    horizontalLayout: PropTypes.bool,
    noIconBtn: PropTypes.bool,
    noMargin: PropTypes.bool,
    isInvertedBtn: PropTypes.bool,
    fetchPriorityHigh: PropTypes.bool,
};
