import React from 'react';

import { AUTOCOMPLETE_ADDRESS, AUTOCOMPLETE_POST_CODE, COUNTRY, DATE, DROP_BOX, PHONE_COUNTRY, SBP_DROP_BOX, TEXT } from 'constants/FieldType';

import { inputPropType } from 'helpers/customPropTypes';

import InputBase from './InputBase';
import { LOGIN_REGISTRATION_FORM_NAME } from '../../constants/formName';

const InputText = (props) => {
    const field = props.field;
    if (field.value === null) {
        field.value = '';
    }

    const setAutocomplete = () => {
        const fieldName = field?.name;
        let autocompleteValue = 'off';
        if (props.formName === LOGIN_REGISTRATION_FORM_NAME) {
            switch (fieldName) {
                case 'name':
                    autocompleteValue = 'given-name';
                    break;
                case 'name_f':
                    autocompleteValue = 'family-name';
                    break;
                case 'address':
                    autocompleteValue = 'street-address';
                    break;
                case 'email':
                    autocompleteValue = 'email';
                    break;
                default:
                    autocompleteValue = 'disabled';
            }
        }
        return autocompleteValue;
    };

    return (
        <input
            id={props.id ? props.id : field.name}
            name={field.canonicalPath ? field.canonicalPath : field.name}
            maxLength={props.setMaxLength && field.maxLength ? field.maxLength : ''}
            value={props.customValue || field.value}
            type={field.type}
            className={props.isNewInput ? null : 'field field-duo'}
            autoComplete={setAutocomplete()}
            readOnly={props.isReadOnly || field.isReadOnly}
            autoFocus={props.focus}
            onClick={props.onClick}
            onFocus={props.onFocus}
            onChange={props.onChange}
            onBlur={props.onBlur}
        />
    );
};

InputText.propTypes = inputPropType([
    COUNTRY,
    'tel',
    'email',
    'number',
    'upload_doc',
    'password',
    'search',
    'digital',
    SBP_DROP_BOX,
    TEXT,
    'short_text',
    'url',
    DATE,
    'hidden',
    DROP_BOX,
    AUTOCOMPLETE_ADDRESS,
    AUTOCOMPLETE_POST_CODE,
    PHONE_COUNTRY,
]);

export default InputBase(InputText);
