import React from 'react';

import PropTypes from 'prop-types';

import {
    ButtonContainer,
    ContentContainer,
    ContentContainerColored,
    ImgContainer,
    TileBlock,
    TileColoredButton,
    TileColoredTitle,
    TileLink,
    TileWithImgTitle,
    TileWithoutBorderButtonContainer,
    TileWithoutBorderContentContainer,
    TileWithoutBorderImgContainer,
    TileWithoutBorderTitle,
} from './styles';
import { PromoButton } from '../PromoButton/PromoButton';
import { PromoDangerouslyHtml } from '../PromoDangerouslyHtml/PromoDangerouslyHtml';
import { PromoDiskButton } from '../PromoDiskButton/PromoDiskButton';
import PromoIllustration from '../widgets/Illustration/PromoIllustration';

const getComponent = (props) => {
    if (props.url) {
        return TileLink;
    } else return TileBlock;
};

export const Tile = (props) => {
    const Container = getComponent(props);
    return (
        <Container variant={props.variant} size={props.size} bgColor={props.bgColor} fullwidth={props.fullwidth} href={props.url} responsive={props.responsive}>
            {props.variant === 'colored' && (
                <>
                    <ContentContainerColored>
                        {props.title && (
                            <TileColoredTitle tag="h3" variant="h1" color="white">
                                {props.title}
                            </TileColoredTitle>
                        )}
                        {props.subtitle && <PromoDangerouslyHtml tag="div" variant="subheader" color="white" dangerouslyHtml={props.subtitle} />}
                    </ContentContainerColored>
                    {props.url && (
                        <TileColoredButton>
                            <PromoDiskButton iconName="arrow-right" type="pseudo" variant="primary" size="large" isInverted />
                        </TileColoredButton>
                    )}
                </>
            )}
            {props.variant === 'withImg' && props.img && (
                <>
                    {props.img && (
                        <ImgContainer fullwidth={props.fullwidth} responsive={props.responsive} imgTop={props.imgTop}>
                            <PromoIllustration img={props.img} loadingLazy={props.loadingLazy} />
                        </ImgContainer>
                    )}
                    <ContentContainer fullwidth={props.fullwidth}>
                        {props.title && (
                            <TileWithImgTitle tag="h3" variant="h3" color={props.titleColor} gradientColor={props.titleGradient}>
                                {props.title}
                            </TileWithImgTitle>
                        )}
                        {props.subtitle && (
                            <PromoDangerouslyHtml tag="div" variant="subheader" color={props.grayText ? 'gray40' : 'gray80'} dangerouslyHtml={props.subtitle} />
                        )}
                        {props.btn_title && (
                            <ButtonContainer>
                                <PromoButton caption={props.btn_title} variant="secondary" type="pseudo" />
                            </ButtonContainer>
                        )}
                    </ContentContainer>
                </>
            )}
            {props.variant === 'withoutBorder' && props.img && (
                <>
                    {props.img && (
                        <TileWithoutBorderImgContainer fullwidth imgTop={props.imgTop}>
                            <PromoIllustration img={props.img} loadingLazy={props.loadingLazy} />
                        </TileWithoutBorderImgContainer>
                    )}
                    <TileWithoutBorderContentContainer>
                        {props.title && (
                            <TileWithoutBorderTitle tag="h3" variant="h3" color={props.titleColor} gradientColor={props.titleGradient}>
                                {props.title}
                            </TileWithoutBorderTitle>
                        )}
                        {props.subtitle && (
                            <PromoDangerouslyHtml tag="div" variant="subheader" color={props.grayText ? 'gray40' : 'gray80'} dangerouslyHtml={props.subtitle} />
                        )}
                        {props.btn_title && (
                            <TileWithoutBorderButtonContainer>
                                <PromoButton caption={props.btn_title} variant="secondary" type="pseudo" />
                            </TileWithoutBorderButtonContainer>
                        )}
                    </TileWithoutBorderContentContainer>
                </>
            )}
        </Container>
    );
};

Tile.defaultProps = {
    bgColor: 'aubergine01',
    size: 'md',
};

Tile.propTypes = {
    variant: PropTypes.oneOf(['colored', 'withImg', 'withoutBorder']),
    size: PropTypes.oneOf(['sm', 'md', 'lg']),
    bgColor: PropTypes.oneOf([
        'baseColor',
        'white',
        'primary',
        'primaryB2B',
        'purple80',
        'purple40',
        'purple20',
        'purple5',
        'gray80',
        'gray40',
        'gray20',
        'gray5',
        'aubergine01',
        'aubergine02',
        'aubergine03',
        'eggshell01',
        'eggshell02',
        'eggshell03',
        'passionFruit01',
        'passionFruit02',
        'passionFruit03',
        'oceanBlue01',
        'oceanBlue02',
        'oceanBlue03',
        'blueberryBlue01',
        'blueberryBlue02',
        'blueberryBlue03',
        'jade01',
        'jade02',
        'jade03',
    ]),
    title: PropTypes.string,
    subtitle: PropTypes.string,
    titleColor: PropTypes.oneOf(['baseColor', 'white', 'primary', 'primaryB2B', 'gray80', 'gray40']),
    titleGradient: PropTypes.oneOf([
        'aubergine01',
        'aubergine02',
        'aubergine03',
        'passionFruit01',
        'passionFruit02',
        'passionFruit03',
        'oceanBlue01',
        'oceanBlue02',
        'oceanBlue03',
        'blueberryBlue01',
        'blueberryBlue02',
        'blueberryBlue03',
        'jade01',
        'jade02',
        'jade03',
    ]),
    img: PropTypes.array,
    imgTop: PropTypes.bool,
    fullwidth: PropTypes.bool,
    btn_title: PropTypes.string,
    url: PropTypes.string,
    grayText: PropTypes.bool,
    withoutBorder: PropTypes.bool,
    responsive: PropTypes.bool,
    loadingLazy: PropTypes.bool,
};
