import React from 'react';
import { connect } from 'react-redux';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { PromoSectionContainer } from 'components/no-auth/common/PromoSectionContainer';
import FaqSection from 'components/no-auth/common/sections/FaqSection';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { HeroSection } from 'components/no-auth/common/sections/HeroSection';
import { HighlightedTilesSection } from 'components/no-auth/common/sections/HighlightedTilesSection';
import { ListingTilesSection } from 'components/no-auth/common/sections/ListingTilesSection';
import { QuoteSectionV2 } from 'components/no-auth/common/sections/QuoteSectionV2';
import { StepBlockSection } from 'components/no-auth/common/sections/StepBlockSection';
import { TilesSection } from 'components/no-auth/common/sections/TilesSection';
import VideoSectionV2 from 'components/no-auth/common/sections/VideoSectionV2';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

const PaymentPageUS = (props) => {
    return (
        <PromoPageContainer bgColor={'eggshell02'} noIndent>
            {props.heroTitle && (
                <HeroSection
                    title={props.heroTitle}
                    text={props.heroText}
                    img={props.heroImg}
                    btnVariant="primary"
                    noIconBtn
                    horizontalLayout
                    data={props.heroData}
                    fetchPriorityHigh
                />
            )}
            {props.videoUrl && (
                <VideoSectionV2
                    sectionTitle={props.videoSectionTitle}
                    sectionText={props.videoSectionText}
                    btn={props.videoBtn}
                    videoUrl={props.videoUrl}
                    videoTitle={props.videoTitle}
                    videoPreviewImg={props.videoPreviewImg}
                    hasTopPadding
                    videoAtTop
                />
            )}

            <PromoSectionContainer>{props.tiles_highlighted && <HighlightedTilesSection data={props.tiles_highlighted} />}</PromoSectionContainer>

            <ColoredSectionContainer>
                {props.tilesHeadlineTitle && (
                    <HeadingSection
                        title={props.tilesHeadlineTitle}
                        text={props.tilesHeadlineText}
                        btn={props.tilesHeadlineBtn}
                        btnMobile={props.tilesHeadlineBtnMobile}
                        isMobileBtn={props.tilesHeadlineBtnIsMobileBtn}
                    />
                )}
                {props.tiles && <TilesSection data={props.tiles} loadingLazy />}
                {props.listingTilesHeadlineTitle && (
                    <HeadingSection
                        title={props.listingTilesHeadlineTitle}
                        text={props.listingTilesHeadlineText}
                        btn={props.listingTilesHeadlineBtn}
                        btnMobile={props.listingTilesHeadlineBtnMobile}
                        isMobileBtn={props.listingTilesHeadlineBtnIsMobileBtn}
                    />
                )}
                {props.listingTilesSection && <ListingTilesSection data={props.listingTilesSection} loadingLazy />}
            </ColoredSectionContainer>
            <ColoredSectionContainer>
                {props.quote && <QuoteSectionV2 data={props.quote} loadingLazy />}
                {props.stepBlockHeadlineTitle && (
                    <HeadingSection
                        title={props.stepBlockHeadlineTitle}
                        text={props.stepBlockHeadlineText}
                        btn={props.stepBlockHeadlineBtn}
                        btnMobile={props.stepBlockHeadlineBtnMobile}
                        isMobileBtn={props.stepBlockHeadlineBtnIsMobileBtn}
                    />
                )}
                {props.steps && <StepBlockSection data={props.steps} />}
            </ColoredSectionContainer>

            {props.faqTitle && props.questions && <FaqSection title={props.faqTitle} questions={props.questions} bgColor="white" />}
            {props.acquisitionTitle && (
                <ColoredSectionContainer bottomPadding bgColor="purple5">
                    <HeadingSection
                        title={props.acquisitionTitle}
                        btn={props.acquisitionBtn}
                        text={props.acquisitionText}
                        isMobileBtn={props.acquisitionBtnIsMobileBtn}
                        sendFrom={'Payment links page'}
                        noBottomIndent
                    />
                </ColoredSectionContainer>
            )}
        </PromoPageContainer>
    );
};
const mapStateToProps = (state) => {
    return {
        heroTitle: state.extra?.strapiApi?.hero_block?.title,
        heroText: state.extra?.strapiApi?.hero_block?.text,
        heroImg: state.extra?.strapiApi?.hero_block?.img,
        heroData: state.extra?.strapiApi?.hero_block,

        videoSectionTitle: state.extra?.strapiApi?.video_section?.title,
        videoSectionText: state.extra?.strapiApi?.video_section?.description,
        videoUrl: state.extra?.strapiApi?.video_section?.url,
        videoBtn: state.extra?.strapiApi?.video_section?.btn,
        videoPreviewImg: state.extra?.strapiApi?.video_section?.img,

        tiles_highlighted: state.extra?.strapiApi?.tiles_highlighted,

        tilesHeadlineTitle: state.extra?.strapiApi?.headline1?.title,
        tilesHeadlineText: state.extra?.strapiApi?.headline1?.text,
        tilesHeadlineBtn: state.extra?.strapiApi?.headline1?.btn,
        tilesHeadlineBtnMobile: state.extra?.strapiApi?.headline1?.btn_mobile,
        tilesHeadlineBtnIsMobileBtn: state.extra?.strapiApi?.headline1?.isMobileBtn,

        tiles: state.extra?.strapiApi?.tiles_section,

        listingTilesHeadlineTitle: state.extra?.strapiApi?.headline2?.title,
        listingTilesHeadlineText: state.extra?.strapiApi?.headline2?.text,
        listingTilesHeadlineBtn: state.extra?.strapiApi?.headline2?.btn,
        listingTilesHeadlineBtnMobile: state.extra?.strapiApi?.headline2?.btn_mobile,
        listingTilesHeadlineBtnIsMobileBtn: state.extra?.strapiApi?.headline2?.isMobileBtn,

        listingTilesSection: state.extra?.strapiApi?.listing_tiles_section,

        quote: state.extra?.strapiApi?.quote_section,

        stepBlockHeadlineTitle: state.extra?.strapiApi?.headline3?.title,
        stepBlockHeadlineText: state.extra?.strapiApi?.headline3?.text,
        stepBlockHeadlineBtn: state.extra?.strapiApi?.headline3?.btn,
        stepBlockHeadlineBtnMobile: state.extra?.strapiApi?.headline3?.btn_mobile,
        stepBlockHeadlineBtnIsMobileBtn: state.extra?.strapiApi?.headline3?.isMobileBtn,

        steps: state.extra?.strapiApi?.steps_block,

        faqTitle: state.extra?.strapiApi?.faq?.title,
        questions: state.extra?.strapiApi?.faq?.questions,

        acquisitionTitle: state.extra?.strapiApi?.headline4?.title,
        acquisitionText: state.extra?.strapiApi?.headline4?.text,
        acquisitionBtn: state.extra?.strapiApi?.headline4?.btn,
        acquisitionBtnMobile: state.extra?.strapiApi?.headline4?.btn_mobile,
        acquisitionBtnIsMobileBtn: state.extra?.strapiApi?.headline4?.isMobileBtn,

        lang: state.extra.noAuthFooter?.language?.lang,
        country: state.extra.noAuthFooter?.language?.country,
    };
};
export default connect(mapStateToProps)(NotAuthBasePage(PaymentPageUS));
