export const resolveArguments = (message = '', args) => {
    const regex = /\{\d}/g;
    message = message === undefined || message === null ? '' : message;
    if (Array.isArray(args)) {
        let resolveMessage = message;
        const matchArray = message.match(regex);
        if (matchArray) {
            args.forEach((argument, index) => {
                resolveMessage = resolveMessage.replaceAll(`\{${index}\}`, argument);
            });
            if (matchArray.length !== args.length && !__isDev__) {
                resolveMessage = resolveMessage.replace(regex, '');
            }
        }
        return resolveMessage;
    } else if (isObject(args)) {
        let newMessage;
        const matchArray = message.match(regex);
        if (matchArray && matchArray[0]) {
            const newMessForFirst = message.replaceAll(`\{0\}`, args);
            if (matchArray.length > 1 && !__isDev__) {
                newMessage = newMessForFirst.replace(regex, '');
            } else {
                return newMessForFirst;
            }
            return newMessage;
        } else {
            return message;
        }
    } else {
        return message;
    }
};

export const getCurrentLang = (languages, locale) => {
    const lang = languages.find((el) => el.localeCode === locale);
    return lang ? lang.name : '';
};

export const getJsDate = (jsonDate) => {
    const date = jsonDate.dateTime.date;
    const time = jsonDate.dateTime.time;
    const strDate = `${date.year}-${addZero(date.month)}-${addZero(date.day)}T${addZero(time.hour)}:${addZero(time.minute)}:${addZero(time.second)}.${
        time.nano
    }Z`;
    return new Date(strDate);
};
// convert date from DD.MM.YYYY & DD/MM/YYYY to YYYY-MM-DD
export const getIsoDate = (dateStr) => {
    const dateArr = dateStr.split(dateStr[2]);
    return `${dateArr[2]}-${dateArr[1]}-${dateArr[0]}`;
};
export const getJsDate2 = (jsonDate = {}) => {
    const strDate = `${jsonDate.year}-${addZero(jsonDate.monthValue)}-${addZero(jsonDate.dayOfMonth)}T${addZero(jsonDate.hour)}:${addZero(
        jsonDate.minute,
    )}:${addZero(jsonDate.second)}.${jsonDate.nano}Z`;
    return new Date(strDate);
};

export const getDDMMMYYYYDate = (date, lang) => {
    // convert date from "2023-08-02T19:07:01" to "02 Aug 2023"
    const jsDate = new Date(date);
    return `${addZero(jsDate.getDate())} ${jsDate.toLocaleString(lang, { month: 'short' })} ${addZero(jsDate.getFullYear())}`;
};

export const getDayMonthDate = (date, lang, length) => {
    // convert date from "02.09.2023 19:07:01" to "09 Sep" or "09 September"
    const jsDate = new Date(date?.replace( /(\d{2}).(\d{2}).(\d{4})/, "$2/$1/$3"));
    return `${addZero(jsDate.getDate())} ${jsDate.toLocaleString(lang, { month: length })}`;
};

export const isDateToday = (date) => {
    const dateToCheck = new Date(date);
    const today = new Date();
    const isToday =
        dateToCheck.getDate() === today.getDate() &&
        dateToCheck.getMonth() === today.getMonth() &&
        dateToCheck.getFullYear() === today.getFullYear();
    return isToday;
};

export const addZero = (number) => (number < 10 ? '0' + number : number);

export const getDeltaSecondsBetweenDates = (dateStart, dateEnd) => {
    const mStart = dateStart.getTime();
    const mEnd = dateEnd.getTime();
    return ((mEnd - mStart) / 60) * 1000;
};

export const getGaGMTdate = (dateTime) => {
    const jsDate = new Date(dateTime);
    const h = Math.floor(Math.abs(jsDate.getTimezoneOffset()) / 60);
    const gmt = (jsDate.getTimezoneOffset() < 0 ? '+' : '-') + addZero(h) + ':' + addZero(Math.abs(jsDate.getTimezoneOffset()) - h * 60);
    return `${addZero(jsDate.getDate())}.${addZero(jsDate.getMonth() + 1)}.${jsDate.getFullYear()}, ${addZero(jsDate.getHours())}:${addZero(
        jsDate.getMinutes(),
    )} (GMT${gmt})`;
};

export const getGaActivityDate = (dateTime, lang) => {
    const jsDate = new Date(dateTime + 'Z');
    // format 14 Oct · Wed 09:09
    return `${addZero(jsDate.getDate())} ${jsDate.toLocaleString(lang, { month: 'short' }).replace('.', '')} · ${jsDate.toLocaleString(lang, {
        weekday: 'short',
    })} ${addZero(jsDate.getHours())}:${addZero(jsDate.getMinutes())}`;
};

export const getLimitEndDate = (date, lang) => {
    // format Tuesday, June 20, 2023 12:32 AM
    return new Intl.DateTimeFormat(`${lang}${window?.navigator?.language.substring(2)}`, { dateStyle: 'full', timeStyle: 'short' }).format(new Date(date));
};

export const getHistoryTransactionDate = (date, lang) => {
    const options = {
        month: 'short',
        day: 'numeric',
        weekday: 'short',
        hour: 'numeric',
        minute: '2-digit',
    };
    return new Intl.DateTimeFormat(`${lang}${window?.navigator?.language.substring(2)}`, options).format(new Date(date));
};

export const getTransferStatusDate = (dateTime, lang) => {
    const jsDate = new Date(dateTime);
    // format 4 Oct · Wed 09:09
    return `${jsDate.getDate()} ${jsDate.toLocaleString(lang, { month: 'short' }).replace('.', '')} · ${jsDate.toLocaleString(lang, {
        weekday: 'short',
    })} ${addZero(jsDate.getHours())}:${addZero(jsDate.getMinutes())}`;
};

export const getGMTdate = (dateTime) => {
    const jsDate = typeof dateTime === 'object' ? getJsDate2(dateTime) : new Date(dateTime);
    const h = Math.floor(Math.abs(jsDate.getTimezoneOffset()) / 60);
    const gmt = (jsDate.getTimezoneOffset() < 0 ? '+' : '-') + addZero(h) + ':' + addZero(Math.abs(jsDate.getTimezoneOffset()) - h * 60);
    return `${addZero(jsDate.getDate())}.${addZero(jsDate.getMonth() + 1)}.${jsDate.getFullYear()}, ${addZero(jsDate.getHours())}:${addZero(
        jsDate.getMinutes(),
    )} (GMT${gmt})`;
};

export const timer = (timerID, callFun, delay = 300) => {
    if (timerID) {
        clearTimeout(timerID);
    }
    timerID = setTimeout(() => {
        callFun();
    }, delay);
    return timerID;
};

export const isObject = (obj) => {
    return obj !== null && typeof obj !== 'undefined';
};
export const validOnMod10 = (cardNumber) => {
    var arr = cardNumber.split('').map(function (char, index) {
        var digit = parseInt(char);
        if ((index + cardNumber.length) % 2 === 0) {
            var digitX2 = digit * 2;
            return digitX2 > 9 ? digitX2 - 9 : digitX2;
        }
        return digit;
    });
    return !(
        arr.reduce(function (a, b) {
            return a + b;
        }, 0) % 10
    );
};
export const random128Hex = () => {
    const random16Hex = () => {
        return (0x10000 | (Math.random() * 0x10000)).toString(16).substr(1);
    };
    return (
        random16Hex() + random16Hex() + '-' + random16Hex() + '-' + random16Hex() + '-' + random16Hex() + '-' + random16Hex() + random16Hex() + random16Hex()
    );
};

export const getLangCountryPrefix = (lang, country) => {
    let prefix = '/';
    if (lang && typeof country === 'string') {
        prefix = `/${lang}-${country.toLowerCase()}`;
    } else if (lang) {
        prefix = `/${lang}`;
    }
    return prefix;
};
export const placeSrcSetAttr = (img1, img2) => {
    if (img1 && img2) return `${img1} 1x, ${img2} 2x`;
    if (img1 && !img2) return `${img1} 2x`;
};

export const isSpecialCountry = (countryId) => {
    return countryId === USA || countryId === CANADA;
};

export const getExtra = (extra, extraName) => {
    if (extra && typeof extra === 'string') {
        const extras = extra.split(';');
        const filtered = extras.filter((e) => e && e.split('=')[0] === extraName);
        return filtered && filtered[0] ? filtered[0].split('=')[1] : null;
    } else {
        return null;
    }
};

export const hasLinkedFromCard = (sources) => {
    let flag = false;
    if (sources) {
        Object.keys(sources).forEach((key) => {
            if (key.indexOf(DC) >= 0) {
                flag = true;
            }
        });
    }
    return flag;
};

export const hasLinkedToCard = (sources) => {
    let flag = false;
    if (sources && Object.keys(sources).length > 1) {
        flag = true;
    }
    return flag;
};

export const toLowerCase = (str) => {
    return str && typeof str === 'string' ? str.toLowerCase() : '';
};

export const toUpperCase = (str) => {
    return str && typeof str === 'string' ? str.toUpperCase() : '';
};

export const getEmptyStringIfNull = (str) => {
    return str === null ? '' : str;
};
export const isNotEmptyArray = (arr) => Array.isArray(arr) && arr.length > 0;

export const hashCode = (str) => {
    let hash = 0,
        i,
        chr;
    for (i = 0; i < str.length; i++) {
        chr = str.charCodeAt(i);
        hash = (hash << 5) - hash + chr;
        hash |= 0; // Convert to 32bit integer
    }
    return hash;
};

export const copyToClipboard = (text) => {
    let textArea = document.createElement('textarea');
    textArea.style.background = 'transparent';
    textArea.style.position = 'fixed';
    textArea.style.top = 0;
    textArea.style.left = 0;
    textArea.value = text;

    document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();

    try {
        document.execCommand('copy');
    } catch (err) {
        console.log('Oops, unable to copy');
    }
    document.body.removeChild(textArea);
};

export const canShareDetails = () => {
    return navigator && navigator.share;
};

export const shareDetails = (title, text) => {
    if (canShareDetails()) {
        navigator
            .share({
                title: title,
                text: text,
            })
            .then(() => {
                console.log('Successfully shared');
            })
            .catch((error) => {
                console.error('Something went wrong sharing the block', error);
            });
    }
};
export const copyTextToClipboard = (text) => {
    const textArea = document.createElement('textarea');
    textArea.style.background = 'transparent';
    textArea.style.position = 'fixed';
    textArea.style.top = 0;
    textArea.style.left = 0;
    textArea.value = text;

    document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();

    try {
        return document.execCommand('copy');
    } catch (err) {
        console.log('Oops, unable to copy');
        return false;
    } finally {
        document.body.removeChild(textArea);
    }
};

