import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { RichTextTile } from 'components/no-auth/common/RichTextTile/RichTextTile';
import { HeroSection } from 'components/no-auth/common/sections/HeroSection';
import LoneButtonSection from 'components/no-auth/common/sections/LoneButtonSection';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

const BonusPage = (props) => {
    return (
        <>
            {props.heroTitle && <HeroSection title={props.heroTitle} data={props.heroData} btnVariant="primary" noIconBtn />}
            <ColoredSectionContainer dimension="average">
                {props.content && <RichTextTile text={props.content} />}
                {props.termsLink && <LoneButtonSection btn={props.termsLink} variant="tertiary" desktopAlignLeft />}
            </ColoredSectionContainer>
        </>
    );
};

BonusPage.propTypes = {
    heroTitle: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    heroData: PropTypes.oneOfType([PropTypes.object, PropTypes.oneOf([null])]),
    content: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    termsLink: PropTypes.oneOfType([PropTypes.object, PropTypes.oneOf([null])]),
};

const mapStateToProps = (state) => {
    return {
        heroTitle: state.extra?.strapiApi?.hero_block?.title,
        heroData: state.extra?.strapiApi?.hero_block,
        content: state.extra?.strapiApi?.bonus_content,
        termsLink: state.extra?.strapiApi?.terms_link,
    };
};

export default connect(mapStateToProps)(NotAuthBasePage(BonusPage));
