import React from 'react';

import PropTypes from 'prop-types';

import { Container, Inner } from './styles';

export const NavBar = (props) => {
    return (
        <Container fullwidth={props.fullwidth} isHiddenDesktop={props.isHiddenDesktop}>
            <Inner align={props.align} fullwidth={props.fullwidth} isSearchBar={props.isSearchBar} insideStories={props.insideStories}>
                {props.children}
            </Inner>
        </Container>
    );
};

NavBar.propTypes = {
    children: PropTypes.oneOfType([
        PropTypes.element,
        PropTypes.arrayOf(PropTypes.element),
        PropTypes.any, //TODO
    ]),
    alignLeft: PropTypes.bool,
    alignRight: PropTypes.bool,
    align: PropTypes.oneOf(['left', 'right', 'space-between', 'center']),
    fullwidth: PropTypes.bool,
    isHiddenDesktop: PropTypes.bool,
    isSearchBar: PropTypes.bool,
    insideStories: PropTypes.bool,
};
