import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import promoMedia from '../../../../theme/promoMedia';

const Icons = styled.ul`
    list-style-type: none;
    margin: 0 auto;
    padding-top: 32px;
    text-align: center;

    ${promoMedia.tablet744} {
        padding-top: 48px;
    }

    ${promoMedia.desktop1440} {
        padding-top: 64px;
    }
`;
export const SingleIconSection = (props) => {
    return (
        <Icons>
            {Array.isArray(props.data) &&
                props.data
                    .filter((item) => item !== null)
                    .map((item) => {
                        return (
                            <li key={item.id}>
                                <img alt={item.alternativeText} src={item.url} height={item.height} width={item.width} />
                            </li>
                        );
                    })}
        </Icons>
    );
};

SingleIconSection.propTypes = {
    data: PropTypes.array,
};
