import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { toggleModal } from 'actions/appAction';

import { DiskButton } from '../common/DiskButton/DiskButton';
import { NavBar } from '../common/Layout/NavBar/NavBar';
import { ModalBody, ModalContainer, ModalViewport } from '../common/Modal/styles';

const ModalComponent = (props) => {
    const ref = useRef(null);
    const [isScroll, setScroll] = useState(false);

    useEffect(() => {
        if (__isBrowser__) {
            document.body.style.overflow = 'hidden';
        }
        return () => {
            if (__isBrowser__ && !props.isShowPopup && !props.isShowModal) {
                document.body.style.overflow = '';
            }
        };
    }, [props.isShowModal, props.isShowPopup]);

    useEffect(() => {
        if (props.isShowPopup) {
            document.removeEventListener('keydown', keyPressHandler);
        } else if (props.canCloseModal && __isBrowser__) {
            document.addEventListener('keydown', keyPressHandler);
        }
        return () => {
            document.removeEventListener('keydown', keyPressHandler);
        };
    }, [props.isShowPopup]);

    const handleScroll = () => {
        setScroll(ref?.current?.scrollTop !== 0);
    };

    const keyPressHandler = (e) => {
        if (e.keyCode === 27 && !props.isShowPopup) {
            e.preventDefault();
            if (props.modalRef?.current) {
                props.modalRef.current?.close();
            } else {
                props.toggleModal(false);
            }
        }
    };

    const onCloseClick = (e) => {
        if (props.onCloseIconClick) {
            props.onCloseIconClick(e);
        } else {
            if (props.modalRef?.current) {
                props.modalRef.current?.close();
            } else {
                props.toggleModal(false);
            }
        }
    };

    return (
        <ModalContainer data-testid={props.dataTestId} isStoriesWrapper={props.isStoriesWrapper}>
            <ModalViewport onScroll={handleScroll} ref={ref}>
                <ModalBody isVerticalCentered={props.isVerticalCentered} hasBottomPanel={props.hasBottomPanel} isStoriesWrapper={props.isStoriesWrapper}>
                    {props.children}
                </ModalBody>
                {(props.canCloseModal || props.rightHeaderComponent) && (
                    <NavBar align={props.rightHeaderComponent && !props.canCloseModal ? 'right' : 'space-between'}>
                        {props.canCloseModal && (
                            <DiskButton
                                to={props.linkTo ? props.linkTo : '#'}
                                onClick={onCloseClick}
                                iconName={props.isIconClose ? 'cross' : 'arrow-back'}
                                raised={isScroll}
                            />
                        )}
                        {props.onClickMoreIcon && <DiskButton onClick={props.onClickMoreIcon} type="tagA" iconName="dots" raised={isScroll} />}
                        {props.rightHeaderComponent && props.rightHeaderComponent}
                    </NavBar>
                )}
            </ModalViewport>
        </ModalContainer>
    );
};

ModalComponent.defaultProps = {
    canCloseModal: true,
    modalRef: null,
};

const mapStateToProps = ({ app }) => {
    return {
        isShowPopup: app.isShowPopup,
        isShowModal: app.isShowModal,
    };
};
export default connect(mapStateToProps, { toggleModal })(ModalComponent);

ModalComponent.propTypes = {
    canCloseModal: PropTypes.bool,
    isIconClose: PropTypes.bool, // когда нужен крестик вместо стрелки
    linkTo: PropTypes.string,
    onCloseIconClick: PropTypes.func,
    onClickMoreIcon: PropTypes.func,
    isVerticalCentered: PropTypes.bool,
    hasBottomPanel: PropTypes.bool,
    modalRef: PropTypes.shape({ current: PropTypes.object }),
};
