import React from 'react';
import { connect } from 'react-redux';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import FaqSection from 'components/no-auth/common/sections/FaqSection';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { HeroSection } from 'components/no-auth/common/sections/HeroSection';
import { StepBlockSection } from 'components/no-auth/common/sections/StepBlockSection';
import { TilesSection } from 'components/no-auth/common/sections/TilesSection';
import VacanciesV2 from 'components/no-auth/hr/Vacancies/VacanciesV2';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

const CareerPage = (props) => {
    return (
        <>
            <PromoPageContainer bgColor="eggshell03">
                {props.heroTitle && (
                    <HeroSection title={props.heroTitle} text={props.heroText} bgImg={props.heroBgImg} img={props.heroImg} data={props.heroData} />
                )}
                {props.jobTitle && props.jobPlaceholder && props.vacancies && (
                    <ColoredSectionContainer>
                        <VacanciesV2
                            title={props.jobTitle}
                            placeholder={props.jobPlaceholder}
                            jobs={props.vacancies}
                            lang={props.lang}
                            country={props.country}
                            centered
                        />
                    </ColoredSectionContainer>
                )}

                <ColoredSectionContainer bgColor={'purple5'} bottomPadding>
                    {props.headline1Title && <HeadingSection title={props.headline1Title} />}
                    {props.stepsTiles && <StepBlockSection data={props.stepsTiles} />}
                </ColoredSectionContainer>

                <ColoredSectionContainer>{props.tilesCareer && <TilesSection data={props.tilesCareer} loadingLazy responsive />}</ColoredSectionContainer>

                {props.faqTitle && props.questions && <FaqSection title={props.faqTitle} questions={props.questions} />}
            </PromoPageContainer>
        </>
    );
};
const mapStateToProps = (state) => {
    return {
        heroTitle: state.extra?.strapiApi?.hero_block?.title,
        heroText: state.extra?.strapiApi?.hero_block?.text,
        heroImg: state.extra?.strapiApi?.hero_block?.img,
        heroBgImg: state.extra?.strapiApi?.hero_block?.bgImg,
        heroData: state.extra?.strapiApi?.hero_block,

        vacancies: state.extra?.strapiApi?.vacancies,
        jobTitle: state.extra?.strapiApi?.job_titles?.main_title,
        jobPlaceholder: state.extra?.strapiApi?.job_titles?.search_placeholder,

        headline1Title: state.extra?.strapiApi?.headline1?.title,

        stepsTiles: state.extra?.strapiApi?.step_tiles,

        faqTitle: state.extra?.strapiApi?.faq?.title,
        questions: state.extra?.strapiApi?.faq?.questions,

        tilesCareer: state.extra?.strapiApi?.tiles_career,

        lang: state.extra.noAuthFooter?.language?.lang,
        country: state.extra.noAuthFooter?.language?.country,
    };
};
export default connect(mapStateToProps)(NotAuthBasePage(CareerPage));
