import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { sendGtEvent } from 'analytics/analytics';
import { cleverTapEvent } from 'analytics/cleverTap';

import InputAmount from 'components/form-multi/InputAmount';
import { getFieldsForSend } from 'components/form-multi/utils';

import { toggleSpinner } from 'actions/appAction';
import { initCalculator } from 'actions/calculatorAction';
import { setExtra } from 'actions/extraAction';
import { changeFormFieldValue, setNewForm, updateFormProperties } from 'actions/formAction';
import { updatePageMessages } from 'actions/messageAction';

import { SEND_MONEY_FROM, SEND_MONEY_FROM_AMOUNT, SEND_MONEY_TO, SEND_MONEY_TO_AMOUNT } from 'constants/CleverTapEvents';
import { AMOUNT, CHOSEN_COUNTRY, COUNTRY, CURRENCY, LANGUAGE } from 'constants/CleverTapParamNames';
import { REQ_ERROR, VALID_ERROR } from 'constants/Constants';
import { ACCOUNT_TO, DELIVERY_METHOD } from 'constants/fieldsName';
import { GT_SEND_MONEY_FROM_AMOUNT } from 'constants/GoogleAnalyticsEvents';

import { apiFetchData, changeBrowserUrlWithOutReload } from 'helpers/api';
import { countryPropType } from 'helpers/customPropTypes';
import { getLangCountryPrefix, resolveArguments } from 'helpers/utils';

import amplitude from 'amplitude-js';
import { useHandleCloseDropdownMenu } from 'hooks/useHandleCloseDropdownMenu';

import AmountCurrency from './AmountCurrency';
import { AmountRow } from './styles';
import { PromoHintContainer } from '../../form-multi/styles';
import { PromoDangerouslyHtml } from '../../no-auth/common/PromoDangerouslyHtml/PromoDangerouslyHtml';
import CountryList from '../CalculatorDropdown/CountryList';

const sendClevertap = ({ lang, ...props }, { commission: commissionData, ...data }) => {
    const {
        restrictionFrom: { ccyIso: ccyIsoFrom },
        restrictionTo: { ccyIso: ccyIsoTo },
    } = commissionData;

    const { countryFrom: previousCountryFrom, countryTo: previousCountryTo } = props.calculator.countryMenu;
    const { countryFrom: chosenFrom, countryTo: chosenTo } = data.countryMenu;

    amplitude.getInstance().logEvent('Calculator Adjusted');

    if (previousCountryFrom.code.toLowerCase().trim() !== chosenFrom.code.toLowerCase().trim()) {
        cleverTapEvent(SEND_MONEY_FROM, {
            [COUNTRY]: previousCountryFrom ? previousCountryFrom.code : '',
            [CHOSEN_COUNTRY]: chosenFrom.code,
            [CURRENCY]: ccyIsoFrom,
            [LANGUAGE]: lang,
        });
    }

    if (previousCountryTo.code.toLowerCase().trim() !== chosenTo.code.toLowerCase().trim()) {
        cleverTapEvent(SEND_MONEY_TO, {
            [COUNTRY]: previousCountryTo ? previousCountryTo.code : '',
            [CHOSEN_COUNTRY]: chosenTo.code,
            [CURRENCY]: ccyIsoTo,
            [LANGUAGE]: lang,
        });
    }
};

const sendToClevertap = (value, props) => {
    if (props.calculator?.commission && props.isLanding) {
        const {
            restrictionFrom: { ccyIso: ccyIsoFrom },
            restrictionTo: { ccyIso: ccyIsoTo },
        } = props.calculator.commission;

        const { countryFrom: chosenFrom, countryTo: chosenTo } = props.calculator.countryMenu;

        amplitude.getInstance().logEvent('Calculator Adjusted');

        if (props.isFrom) {
            cleverTapEvent(SEND_MONEY_FROM_AMOUNT, {
                [CURRENCY]: ccyIsoFrom,
                [CHOSEN_COUNTRY]: chosenFrom.code,
                [AMOUNT]: value,
                [LANGUAGE]: props.lang,
            });
        } else {
            cleverTapEvent(SEND_MONEY_TO_AMOUNT, {
                [CURRENCY]: ccyIsoTo,
                [CHOSEN_COUNTRY]: chosenTo.code,
                [AMOUNT]: value,
                [LANGUAGE]: props.lang,
            });
        }
    }
};

const getUrl = (country, currency, props) => {
    const isFrom = props.isFrom;
    const isLanding = props.isLanding;
    const currencyId = isFrom ? props.countryTo.currencies[0].id : props.countryFrom.currencies[0].id;
    const fromCountry = isFrom ? country : props.countryFrom;
    const fromCode = isLanding ? fromCountry.seoNameFrom : fromCountry.code;
    const toCountry = isFrom ? props.countryTo : country;
    const toCode = isLanding ? toCountry.seoNameTo : toCountry.code;
    const dependedURI = isLanding ? resolveArguments(props.localeDependentURI, [fromCode, toCode]) : `/send/${fromCode}/${toCode}`;
    const fromCurrId = isFrom ? currency.id : currencyId;
    const toCurrId = isFrom ? currencyId : currency.id;

    return `${
        isLanding ? getLangCountryPrefix(props.lang, props.country) : '/auth'
    }${dependedURI}?fromCurrId=${fromCurrId}&toCurrId=${toCurrId}&isFrom=${!isFrom}`;
};

const getErrorText = (field, props) => {
    const errorMessageKey = field.value ? VALID_ERROR : REQ_ERROR;
    return field.errorMessage ? field.errorMessage : props.jsMessages[errorMessageKey];
};

const Amount = (props) => {
    const [showCountryList, setShowCountryList] = useState(true);
    const [selectedCountry, setSelectedCountry] = useState({});

    const wrapperRef = useRef(null);
    const [isOpenDropdownMenu, setOpenDropdownMenu] = useHandleCloseDropdownMenu(wrapperRef);

    const formName = props.formName;

    useEffect(() => {
        setShowCountryList(true);
    }, [isOpenDropdownMenu]);

    const initSelectedCountry = (e, country, currency, setShowCountryList, props) => {
        props.toggleSpinner(true);
        setShowCountryList(true);

        setOpenDropdownMenu(false);

        const url = getUrl(country, currency, props);
        if (!props.isLanding && props.submitRef && props.submitRef.current) {
            props.updateFormProperties(props.formName, url, false).then(() => {
                props.changeFormFieldValue(props.formName, null, ACCOUNT_TO);
                props.changeFormFieldValue(props.formName, null, DELIVERY_METHOD).then(() => {
                    props.submitRef.current.click();
                });
            });
            changeBrowserUrlWithOutReload(url);
        } else {
            apiFetchData(url)
                .then((data) => {
                    props.setNewForm(props.formName, getFieldsForSend(data));
                    props.updatePageMessages(data.messages);
                    props.initCalculator(data);
                    props.setExtra(data.extra);
                    changeBrowserUrlWithOutReload(url, data);
                    props.toggleSpinner(false);

                    sendClevertap(props, data);
                })
                .catch(() => props.toggleSpinner(false));
        }
    };

    const selectCountry = (e, country, currency, isCurrencySelected) => {
        e.preventDefault();
        if (country.currencies.length > 1) {
            if (isCurrencySelected) {
                initSelectedCountry(e, country, currency, setShowCountryList, props);
            } else {
                setShowCountryList(false);
                setSelectedCountry(country);
            }
        } else {
            initSelectedCountry(e, country, currency, setShowCountryList, props);
        }
    };

    const closeCurrencyButton = (e, country) => {
        e.preventDefault();
        setShowCountryList(true);
        setSelectedCountry(country);
    };

    const { countryBean, amountFieldBean, countryList, isLanding, isFrom } = props;

    const fields = props.forms[formName].fields;
    const amountField = fields[amountFieldBean.name];
    const isShowList = countryList && countryList.length > 1;

    return (
        <AmountRow isLock={props.isLock} ref={wrapperRef}>
            {isOpenDropdownMenu && isShowList ? (
                <CountryList
                    amountField={amountField}
                    showCountryList={showCountryList}
                    selectedCountry={selectedCountry}
                    countryList={countryList}
                    isFrom={isFrom}
                    handlerSelectCountry={selectCountry}
                    handlerCloseButtonCurrencyCountry={closeCurrencyButton}
                />
            ) : (
                <>
                    <InputAmount
                        onBlurHandler={(value) => {
                            sendToClevertap(value, props);
                            sendGtEvent(GT_SEND_MONEY_FROM_AMOUNT);
                            props.onBlurHandler && props.onBlurHandler();
                        }}
                        onFocusHandler={props.onFocusHandler}
                        field={amountField}
                        formName={formName}
                        minAmount={amountFieldBean.minValidValue}
                        maxAmount={amountFieldBean.maxValidValue}
                        multiple={amountFieldBean.multiple}
                        enabledValidateMinMaxAmount={props.enabledValidateMinMaxAmount}
                        onChangeHandler={props.onChangeHandler}
                        isLanding={isLanding}
                        isFrom={isFrom}
                        isNewInput
                    >
                        <AmountCurrency
                            isShowList={isShowList}
                            isLanding={isLanding}
                            isFrom={isFrom}
                            amountFieldBean={amountFieldBean}
                            countryBean={countryBean}
                            handlerToggleMenu={() => setOpenDropdownMenu(true)}
                        />
                    </InputAmount>
                    {amountField.isError && (
                        <PromoHintContainer>
                            <PromoDangerouslyHtml dangerouslyHtml={getErrorText(amountField, props)} tag="div" color="error" />
                        </PromoHintContainer>
                    )}
                </>
            )}
        </AmountRow>
    );
};

Amount.propTypes = {
    isFrom: PropTypes.bool.isRequired,
    isSend: PropTypes.bool.isRequired,
    formName: PropTypes.string.isRequired,
    amountFieldBean: PropTypes.object.isRequired,
    countryBean: countryPropType,
    countryTo: countryPropType,
    countryFrom: countryPropType,
    countryList: PropTypes.arrayOf(countryPropType),
    isLock: PropTypes.bool,
};

Amount.defaultProps = {
    isLock: false,
    amountFieldBean: {},
    isLanding: false,
    enabledValidateMinMaxAmount: true,
};

const mapStateToProps = ({ app, auth, form, language, messages, calculator }) => ({
    isAuth: auth.authUser,
    forms: form,
    lang: language.lang,
    country: app.country,
    messages: messages.pageMessages,
    jsMessages: messages.jsMessages,
    calculator,
});

export default connect(mapStateToProps, {
    toggleSpinner,
    setNewForm,
    initCalculator,
    updatePageMessages,
    changeFormFieldValue,
    updateFormProperties,
    setExtra,
})(Amount);
