import React, { useEffect, useState } from 'react';

import { InfoPage } from 'components/common/Layout/InfoPage/InfoPage';

import { CATEGORY_TRANSFER_TO_CASH, CATEGORY_TRANSFER_TO_HOME_DELIVERY, CATEGORY_TRANSFER_TO_INTERAC } from 'constants/Constants';
import { AUTH_INVITE_CODE_PROMO, AUTH_MASTERCARD_PROMO, AUTH_MY_CARDS_URL, AUTH_SEND_MAIN_LINK } from 'constants/UrlConstants';

import { timer } from 'helpers/utils';

import amplitude from 'amplitude-js';

import { REACHING_LIMIT_SCREEN, UPGRADE_LIMIT_SCREEN, WAITING_SCREEN } from './Constants';
import PromoReachingLimit from './PromoReachingLimit';
import PromoUpgradeLimit from './PromoUpgradeLimit';
import { sendGtEvent } from '../../../analytics/analytics';
import { cleverTapEvent } from '../../../analytics/cleverTap';
import { TRANSFER_CONFIRMATION_SHOWN } from '../../../constants/amplitude/AmplitudeEvents';
import { COUNTRY, DELIVERY_METHOD_AMPLITUDE, SENDING_METHOD_AMPLITUDE, TRANSFER_ID } from '../../../constants/amplitude/AmplitudeParamName';
import { SEND_SUCCESS } from '../../../constants/CleverTapEvents';
import { AMOUNT_SEND_CURRENCY, PAYSEND_ID, RECEIVING_COUNTRY_CODE, SEND_CURRENCY, SENDING_COUNTRY_CODE, TRX_ID } from '../../../constants/CleverTapParamNames';
import { GT_SEND_SUCCESS } from '../../../constants/GoogleAnalyticsEvents';
import { GT_AMOUNT_SEND_CURRENCY, GT_SEND_CURRENCY, GT_TRX_ID, GT_USER_ID } from '../../../constants/GoogleAnalyticsParamNames';
import { useAuth, useExtra, usePageMessages } from '../../../hooks/selectors';
import { providerCategories } from '../../utils/AmplitudeUtils';

const Waiting = (props) => {
    let timerID = null;
    const { cardPan, cheque, statusBean } = useExtra();
    const { ballId } = useAuth();
    const { headerTitle, statusDoneTitle, cancelTransaction30Minutes } = usePageMessages();
    const [screen, setScreen] = useState(WAITING_SCREEN);
    const fromCategory = cheque?.providerFromCategory.toString();
    const toCategory = cheque?.providerToCategory.toString();
    const sendingMethod = providerCategories[fromCategory];
    const deliveryMethod = providerCategories[toCategory];
    const toCountry = cheque.toCountry.nameEn;

    const isDeliveryOnCashOrInteract = () => {
        return (
            cheque?.providerToCategory === CATEGORY_TRANSFER_TO_CASH ||
            cheque?.providerToCategory === CATEGORY_TRANSFER_TO_HOME_DELIVERY ||
            cheque?.providerToCategory === CATEGORY_TRANSFER_TO_INTERAC
        );
    };

    useEffect(() => {
        amplitude.getInstance().logEvent(TRANSFER_CONFIRMATION_SHOWN, {
            [COUNTRY]: toCountry,
            [SENDING_METHOD_AMPLITUDE]: sendingMethod,
            [DELIVERY_METHOD_AMPLITUDE]: deliveryMethod,
            [TRANSFER_ID]: cheque?.paymentId,
        });
        cleverTapEvent(SEND_SUCCESS, {
            [TRX_ID]: cheque?.paymentId,
            [SEND_CURRENCY]: cheque?.amountFromCurrency,
            [AMOUNT_SEND_CURRENCY]: cheque?.amountFrom,
            [PAYSEND_ID]: ballId,
            [SENDING_COUNTRY_CODE]: cheque?.fromCountryId,
            [RECEIVING_COUNTRY_CODE]: cheque?.toCountryId,
        });
        sendGtEvent(GT_SEND_SUCCESS, {
            [GT_USER_ID]: ballId,
            [GT_TRX_ID]: cheque?.paymentId,
            [GT_SEND_CURRENCY]: cheque?.amountFromCurrency,
            [GT_AMOUNT_SEND_CURRENCY]: cheque?.amountFrom,
        });
    }, []);

    useEffect(() => {
        if (isDeliveryOnCashOrInteract()) {
            timerID = timer(
                timerID,
                () => {
                    window.location.replace(AUTH_SEND_MAIN_LINK);
                },
                1000,
            );
        } else if (!props.smsOnSuccessPay && props.status && parseInt(props.status.refreshTimer) > 0) {
            timerID = timer(
                timerID,
                () => {
                    location.reload();
                },
                parseInt(props.status.refreshTimer),
            );
        }
        return () => clearTimeout(timerID);
    }, []);

    const getPrimaryButtonLink = () => {
        if (props.status?.showUpgradeLimitPromo) {
            return '#';
        } else if (props.status?.showMastercardPromoBlock) {
            return `${AUTH_MASTERCARD_PROMO}?id=${props.status.id}`;
        } else if (cardPan) {
            return AUTH_MY_CARDS_URL;
        } else if (props.status?.showInviteCodeReferralButton) {
            return AUTH_INVITE_CODE_PROMO;
        } else {
            return AUTH_SEND_MAIN_LINK;
        }
    };
    const primaryButtonOnClick = (e) => {
        if (props.status?.showUpgradeLimitPromo) {
            e.preventDefault();
            setScreen(REACHING_LIMIT_SCREEN);
        }
    };
    const renderScreen = (currentScreen) => {
        switch (currentScreen) {
            case REACHING_LIMIT_SCREEN:
                return <PromoReachingLimit primaryButtonOnClick={() => setScreen(UPGRADE_LIMIT_SCREEN)} />;
            case UPGRADE_LIMIT_SCREEN:
                return <PromoUpgradeLimit />;
            default:
                return (
                    <InfoPage
                        iconName="check-rounded-thin"
                        iconColor="purple"
                        headerText={headerTitle}
                        secondaryText={statusBean?.canCancelTransaction ? cancelTransaction30Minutes : ''}
                        primaryButtonCaption={!isDeliveryOnCashOrInteract() ? statusDoneTitle : null}
                        primaryButtonType="link"
                        primaryButtonLink={getPrimaryButtonLink()}
                        primaryButtonOnClick={primaryButtonOnClick}
                    />
                );
        }
    };
    return renderScreen(screen);
};

export default Waiting;
