import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { togglePopup } from '../../../actions/appAction';
import { DangerouslyHtml } from '../../common/DangerouslyHtml/DangerouslyHtml';
import PopupButton from '../../common/Popup/PopupButton';
import SimplePopup from '../../common/Popup/SimplePopup';

const TextPopup = (props) => {
    const getText = (text) => {
        return <DangerouslyHtml dangerouslyHtml={text} />;
    };

    const onClick = () => {
        if (props.onClick) {
            props.onClick();
        }
        if (props.popupRef?.current) {
            props.popupRef.current?.close();
        } else {
            props.togglePopup(false);
        }
    };

    return (
        <SimplePopup text={getText(props.text)} popupRef={props.popupRef}>
            <PopupButton iconName="check" text={props.buttonText} onClick={onClick} />
        </SimplePopup>
    );
};

export default connect(null, { togglePopup })(TextPopup);

TextPopup.propTypes = {
    onClick: PropTypes.func,
    popupRef: PropTypes.shape({ current: PropTypes.object }),
};
TextPopup.defaultProps = {
    popupRef: null,
};
