import styled, { css } from 'styled-components';

import promoColors from '../../../../theme/promoColors';
import promoMedia from '../../../../theme/promoMedia';
import { PromoText } from '../PromoText/PromoText';

const getHeight = (props) => {
    if (props.size === 'sm') {
        return '400px';
    } else if (props.size === 'lg') {
        return '510px';
    } else {
        return '480px';
    }
};
const getTabletHeight = (props) => {
    if (props.size === 'sm') {
        return '664px';
    } else if (props.size === 'lg') {
        return '628px';
    } else {
        return props.variant === 'colored' ? '504px' : '628px';
    }
};
const getDesktopHeight = (props) => {
    if (props.size === 'sm') {
        return '464px';
    } else if (props.size === 'lg') {
        return '864px';
    } else {
        return '648px';
    }
};

const CommonCss = css`
    background: ${(props) => (props.variant === 'withoutBorder' ? null : props.variant === 'withImg' ? 'white' : promoColors[props.bgColor])};
    padding: ${(props) => (props.variant === 'colored' ? '32px' : null)};
    border-radius: ${(props) => (props.variant === 'withoutBorder' ? null : '24px')};
    box-shadow: ${(props) => (props.variant === 'withoutBorder' ? null : promoColors.shadowLoose)};
    height: ${(props) => (props.variant === 'withoutBorder' || props.responsive ? null : '100%')};
    min-height: ${(props) => getHeight(props.size)};
    box-sizing: border-box;
    word-break: break-word;
    display: flex;
    flex-direction: column;
    align-items: ${(props) => (props.variant === 'withoutBorder' ? 'center' : null)};
    justify-content: space-between;
    overflow: hidden;
    position: relative;

    ${promoMedia.tablet744} {
        padding: ${(props) => (props.variant === 'colored' ? '48px' : null)};
        min-height: ${(props) => (props.variant === 'withoutBorder' || props.responsive ? null : getTabletHeight(props))};
        border-radius: ${(props) => (props.variant === 'withoutBorder' ? null : '36px')};
    }

    ${promoMedia.desktop1440} {
        padding: ${(props) => (props.variant === 'colored' ? '64px' : null)};
        flex-direction: ${(props) => ((props.fullwidth && props.variant === 'withImg') || props.variant === 'withoutBorder' ? 'row' : null)};
        min-height: ${(props) => (props.variant === 'withoutBorder' ? null : props.fullwidth ? '468px' : getDesktopHeight(props))};
        border-radius: ${(props) => (props.variant === 'withoutBorder' ? null : '40px')};
        justify-content: ${(props) => (props.variant === 'withoutBorder' ? 'flex-start' : null)};
    }
`;

export const TileLink = styled.a`
    ${CommonCss};
    text-decoration: none;
`;
export const TileBlock = styled.div`
    ${CommonCss};
`;

export const ContentContainerColored = styled.span`
    ${promoMedia.desktop1440} {
        display: block;
        max-width: 780px;
    }
`;
export const TileColoredTitle = styled(PromoText)`
    margin-bottom: 12px;

    ${promoMedia.tablet744} {
        margin-bottom: 16px;
    }
`;
export const TileColoredButton = styled.span`
    display: inline-flex;
    margin-top: 16px;
    justify-content: flex-end;
`;

export const TileWithImgTitle = styled(PromoText)`
    margin-bottom: 8px;

    ${promoMedia.tablet744} {
        margin-bottom: 12px;
    }

    ${promoMedia.desktop1440} {
        margin-bottom: 16px;
    }
`;

export const TileWithoutBorderTitle = styled(PromoText)`
    margin-bottom: 16px;
`;

export const ContentContainer = styled.span`
    display: flex;
    flex-direction: column;
    padding: 32px;

    ${promoMedia.tablet744} {
        padding: 48px;
    }
    ${promoMedia.desktop1440} {
        padding: 64px;
        justify-content: center;
    }
`;

export const TileWithoutBorderContentContainer = styled.span`
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 32px;

    ${promoMedia.tablet744} {
        padding: 48px;
    }

    ${promoMedia.desktop1440} {
        padding: 64px;
    }
`;

export const ButtonContainer = styled.span`
    display: block;
    margin-top: 16px;

    ${promoMedia.tablet744} {
        margin-top: 24px;
    }
    ${promoMedia.desktop1440} {
        margin-top: 32px;
    }
`;
export const TileWithoutBorderButtonContainer = styled.span`
    display: block;
    margin-top: 32px;
`;
export const ImgContainer = styled.span`
    display: block;
    overflow: hidden;
    width: ${(props) => (props.responsive ? '335px' : null)};
    min-width: ${(props) => (props.responsive ? '335px' : null)};
    height: ${(props) => (props.responsive ? '335px' : null)};
    margin: ${(props) => (props.responsive ? '0 auto' : null)};

    ${promoMedia.tablet744} {
        width: ${(props) => (props.responsive ? '504px' : null)};
        min-width: ${(props) => (props.responsive ? '504px' : null)};
        height: ${(props) => (props.responsive ? '504px' : null)};
    }

    ${promoMedia.desktop1440} {
        width: ${(props) => (props.fullwidth ? '468px' : null)};
        min-width: ${(props) => (props.fullwidth ? '468px' : null)};
        height: ${(props) => (props.fullwidth ? '468px' : null)};
        margin: 0;
        order: ${(props) => (!props.imgTop ? '2' : null)};
    }

    img {
        width: 100%;
    }
`;

export const TileWithoutBorderImgContainer = styled.span`
    display: flex;
    overflow: hidden;
    margin-top: 16px;

    ${promoMedia.tablet744} {
        width: 468px;
        min-width: 468px;
        height: 468px;
        margin-top: 0;
    }

    ${promoMedia.desktop1440} {
        order: ${(props) => (props.imgTop ? '2' : null)};
    }

    img {
        width: 100%;
    }
`;
