import React, { useEffect, useMemo, useState } from 'react';

import PropTypes from 'prop-types';

import { PromoBadge } from 'components/no-auth/common/PromoBadge/PromoBadge';
import { PromoIcon } from 'components/no-auth/common/PromoIcon/PromoIcon';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';

import { resolveArguments, toLowerCase } from 'helpers/utils';

import {
    NewCareerPageBadgesContainer,
    NewCareerPageCardsTitle,
    NewCareerPageInputContainer,
    NewCareerPageJobCardsContainer,
    NewCareerPageStyledInput,
} from './styles';
import { JobCardV2 } from '../JobCard/JobCardV2';

const VacanciesV2 = (props) => {
    const counts = {};
    const [value, setValue] = useState('');
    const [jobs, setJobs] = useState(props.jobs);
    const [searchJobs, setSearchJobs] = useState(props.jobs);
    const [IsSearch, setIsSearch] = useState(false);
    const job_amount = props.jobs.length;
    const result = props.jobs.map((obj) => obj.department);
    const jobList = !IsSearch ? jobs : searchJobs;

    for (const num of result) {
        counts[num] = counts[num] ? counts[num] + 1 : 1;
    }
    useEffect(() => {
        filterJobs(department);
        setValue('');
    }, []);

    const filterJobs = (dep) => {
        setJobs(props.jobs.filter((job) => (dep ? job.department === dep : job.department === Object.keys(counts)[0])));
        setValue('');
    };

    const search = (value) => {
        let obj = props.jobs.filter((o) => `${o.title} ${o.department} ${o.location}`.toLowerCase().includes(value.toLowerCase()));
        setValue(value);
        setIsSearch(true);
        setSearchJobs(obj);
    };

    const [department, setDepartment] = useState(props.departamentProps ? props.departamentProps : Object.keys(counts)[0]);

    const filteredJobList = useMemo(() => Array.isArray(jobList) && jobList.filter((item) => item !== null && item.id !== props.jobId), [jobList, props.jobId]);

    const requiredBeVerb = filteredJobList.length > 1 ? props.pluralVerb : props.singularVerb;

    return (
        <div id="jobs">
            {!props.insideVacancy && props.title && <HeadingSection title={resolveArguments(props.title, job_amount)} />}
            {props.insideVacancy && props.title && filteredJobList.length > 0 && props.departamentProps && (
                <HeadingSection title={resolveArguments(props.title, [requiredBeVerb, filteredJobList.length, toLowerCase(props.departamentProps)])} />
            )}
            {!props.insideVacancy && props.title && props.jobs && (
                <>
                    <NewCareerPageInputContainer>
                        <NewCareerPageStyledInput
                            type="search"
                            name="search"
                            value={value}
                            placeholder={resolveArguments(props.placeholder, job_amount)}
                            onChange={(e) => search(e.target.value)}
                        />
                        <PromoIcon name="search" size="24px" color="gray80" />
                    </NewCareerPageInputContainer>
                    <NewCareerPageBadgesContainer centered={props.centered}>
                        {Object.keys(counts)
                            .filter((item) => item !== null)
                            .map((key) => (
                                <PromoBadge
                                    isSelected={!IsSearch && department === key}
                                    caption={key + ' (' + counts[key] + ')'}
                                    onClick={() => {
                                        setIsSearch(false);
                                        setDepartment(key);
                                        filterJobs(key);
                                    }}
                                    key={key}
                                />
                            ))}
                    </NewCareerPageBadgesContainer>
                    {!IsSearch && (
                        <NewCareerPageCardsTitle tag="div" variant="subheader" color="gray40">
                            {department}
                        </NewCareerPageCardsTitle>
                    )}
                </>
            )}
            <NewCareerPageJobCardsContainer>
                {Array.isArray(filteredJobList) &&
                    filteredJobList.map((item) => {
                        return (
                            <JobCardV2
                                key={item.id}
                                href={`/${props.lang}-${props.country}/careers/${item.id}`}
                                title={item.title}
                                job={item.department}
                                location={item.location}
                            />
                        );
                    })}
            </NewCareerPageJobCardsContainer>
        </div>
    );
};
VacanciesV2.propTypes = {
    centered: PropTypes.bool,
    insideVacancy: PropTypes.bool,
};
export default VacanciesV2;
