import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import colors from '../../../theme/colors';

const getIconColor = (color) => {
    switch (color) {
        case 'darkGray':
            return colors.baseColor;
        case 'mediumGray':
            return colors.mediumGray;
        case 'lightGray':
            return colors.lightGray;
        case 'superLightGray':
            return colors.superLightGray;
        case 'greyRgba28':
            return colors.greyRgba28;
        case 'purple':
            return colors.purple;
        case 'purpleRgba24':
            return colors.purpleRgba24;
        case 'purpleRgba40':
            return colors.purpleRgba40;
        case 'red':
            return colors.red;
        case 'white':
            return colors.white;
        case 'whiteRgba64':
            return colors.whiteRgba64;
        case 'teal':
            return colors.teal;
        default:
            return colors.baseColor;
    }
};

const defaultSize = '24px';

const Container = styled.span`
    display: inline-block;
    height: ${(props) => (props.size ? props.size : defaultSize)};
    margin-right: ${(props) => (props.rightIndent ? props.rightIndent : null)};
    margin-left: ${(props) => (props.leftIndent ? props.leftIndent : null)};

    svg {
        width: ${(props) => (props.size ? props.size : defaultSize)};
        height: ${(props) => (props.size ? props.size : defaultSize)};
        --accent: ${(props) => getIconColor(props.color)};
    }
`;

function renderSVG({ id }) {
    return (
        <svg>
            <use xlinkHref={`/i/svg-icons/icons.svg#${id}`} />
        </svg>
    );
}

const StyledSVGIcon = styled(renderSVG)`
    display: inline-block;
`;

export const Icon = (props) => {
    return (
        <Container rightIndent={props.rightIndent} leftIndent={props.leftIndent} color={props.color} size={props.size}>
            <StyledSVGIcon id={props.name} />
        </Container>
    );
};

Icon.propTypes = {
    name: PropTypes.string,
    color: PropTypes.oneOf([
        'darkGray',
        'mediumGray',
        'lightGray',
        'superLightGray',
        'greyRgba28',
        'purple',
        'purpleRgba24',
        'purpleRgba40',
        'red',
        'white',
        'whiteRgba64',
        'teal',
    ]),
    size: PropTypes.oneOf(['16px', '20px', '24px', '32px', '48px', '56px', '64px', '80px', '96px', '100%']),
    rightIndent: PropTypes.oneOf(['4px', '8px', '12px', '16px', '24px', '32px']),
    leftIndent: PropTypes.oneOf(['4px', '8px', '12px', '16px', '24px', '32px']),
};
