import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { Icon } from 'components/common/Icon/Icon';
import { SectionContainer } from 'components/common/Layout/SectionContainer';

import { HELP_INFO_SUPPORTED_COUNTRIES } from 'constants/UrlConstants';

import { getLangCountryPrefix } from 'helpers/utils';

import GeoCountry from './GeoCountry';
import { Countries, Description, LeadText, LearnMore, SectionGeo, ShowAll, Tags, Title } from './styles';

const Geo = (props) => {
    const getList = (letters, countries, countryFrom, countryTo) => {
        const list = letters.map((letter) => {
            const tegs = countries.map((country) => {
                if (country.name.startsWith(letter)) {
                    return (
                        <GeoCountry
                            key={country.code}
                            uri={props.localeDependentURI}
                            country={country}
                            countryFrom={countryFrom}
                            countryTo={countryTo}
                            onlyMessages={props.data.onlyMessages}
                            isRequest={props.isRequest}
                        />
                    );
                }
            });
            return (
                <li key={letter}>
                    <Tags>{tegs}</Tags>
                </li>
            );
        });
        return list;
    };
    const {
        countryFrom,
        countryTo,
        data: { title, text, learnMore, letters, countries },
    } = props;
    return (
        <SectionGeo id="com-geography-block-impl" className="no-spinner">
            <SectionContainer dimension={'wide'}>
                <LeadText>
                    <Title color={'darkGray'} isPromo size={'header'} variant={'h2'}>
                        {title}
                    </Title>
                    <Description color={'mediumGray'} isPromo size={'main'} variant={'paragraph'} dangerouslySetInnerHTML={{ __html: text }} />
                </LeadText>
                {props.mobileBrowser ? null : <Countries>{getList(letters, countries, countryFrom, countryTo)}</Countries>}
                <ShowAll>
                    <a href={`${getLangCountryPrefix(props.lang, props.country)}${HELP_INFO_SUPPORTED_COUNTRIES}`}>
                        <LearnMore color={'purple'} isPromo size={'main'}>
                            {learnMore}
                        </LearnMore>
                        <Icon name="chevron-right" size="24px" color="purple" />
                    </a>
                </ShowAll>
            </SectionContainer>
        </SectionGeo>
    );
};
Geo.propTypes = {
    localeDependentURI: PropTypes.string.isRequired,
    data: PropTypes.shape({
        title: PropTypes.string.isRequired,
        text: PropTypes.string.isRequired,
        learnMore: PropTypes.string.isRequired,
        letters: PropTypes.arrayOf(PropTypes.string).isRequired,
        countries: PropTypes.arrayOf(
            PropTypes.shape({
                name: PropTypes.string.isRequired,
                code: PropTypes.string.isRequired,
            }),
        ).isRequired,
    }),
};
const mapStateToProps = (state) => {
    return {
        lang: state.language.lang,
        country: state.app.country,
        mobileBrowser: state.app.isMobileBrowser,
    };
};
export default connect(mapStateToProps)(Geo);
