import React from 'react';
import { connect } from 'react-redux';

import styled from 'styled-components';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { PromoRow } from 'components/no-auth/common/PromoRow/PromoRow';
import { PromoText } from 'components/no-auth/common/PromoText/PromoText';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

import promoColors from 'theme/promoColors';
import promoMedia from 'theme/promoMedia';

const StateTerm = styled(PromoText)`
    margin-bottom: 12px;
`;
const StateFrames = styled.div`
    & > div,
    & > ul {
        flex: 1;
    }

    li + li {
        margin-top: 24px;
    }

    ${promoMedia.tablet744} {
        display: flex;
        justify-content: space-between;
    }
`;
const States = styled.ul`
    & > li {
        border-top: 1px solid ${promoColors.gray5};
        padding: 32px 0 0;

        & + li {
            margin-top: 32px;
        }
    }
`;

const LicensePage = (props) => {
    const data = [props.sectionText1, props.sectionText2, props.sectionText3];
    const getDescription = () => {
        if (data.length > 0) {
            return data
                .filter((item) => item !== null)
                .map((text) => `<p>${text}</p>`)
                .join('');
        } else {
            return '';
        }
    };

    return (
        <PromoPageContainer>
            <ColoredSectionContainer doubleTopPadding={!props.sectionTitle}>
                {props.sectionTitle && <HeadingSection title={props.sectionTitle} text={getDescription()} textLeft />}
                {props.stateData && (
                    <States>
                        {props.stateData.map((data, index) => {
                            return (
                                <li key={index}>
                                    <StateFrames>
                                        <PromoRow indent="32px">
                                            <PromoText tag="p" variant="subheader">
                                                {data.name}
                                            </PromoText>
                                        </PromoRow>
                                        <ul>
                                            <li>
                                                <StateTerm tag="div" variant="note" color="gray40">
                                                    {props.tableTitleAgency}
                                                </StateTerm>
                                                <PromoText tag="p">{data.regulatory}</PromoText>
                                            </li>
                                            <li>
                                                <StateTerm tag="div" variant="note" color="gray40">
                                                    {props.tableTitleLicense}
                                                </StateTerm>
                                                <PromoText tag="p">{data.license}</PromoText>
                                            </li>
                                            <li>
                                                <StateTerm tag="div" variant="note" color="gray40">
                                                    {props.tableTitleStatue}
                                                </StateTerm>
                                                <PromoText tag="p">{data.statue}</PromoText>
                                            </li>
                                        </ul>
                                    </StateFrames>
                                </li>
                            );
                        })}
                    </States>
                )}
            </ColoredSectionContainer>
        </PromoPageContainer>
    );
};
const mapStateToProps = ({ extra }) => ({
    sectionTitle: extra.strapiApi?.title,
    sectionText1: extra.strapiApi?.text1,
    sectionText2: extra.strapiApi?.text2,
    sectionText3: extra.strapiApi?.text3,
    stateData: extra.strapiApi?.state_info,
    tableTitleLicense: extra.strapiApi?.title_license,
    tableTitleAgency: extra.strapiApi?.title_agency,
    tableTitleStatue: extra.strapiApi?.title_statue,
});
export default connect(mapStateToProps)(NotAuthBasePage(LicensePage));
