import styled, { css } from 'styled-components';

import colors from '../../../theme/colors';
import fonts from '../../../theme/fonts';
import media from '../../../theme/media';

const getBackground = (props) => {
    switch (props.variant) {
        case 'primary':
            return colors.purple;
        case 'secondary':
            return colors.purpleRgba08;
        case 'tertiary':
            return colors.lightGray;
        case 'negative':
            return colors.red;
        default:
            return colors.purple;
    }
};

const CommonCss = css`
    background-color: ${(props) => getBackground(props)};
    border: 2px solid ${colors.white};
    color: ${(props) => (props.variant === 'secondary' ? colors.purple : colors.white)};
    display: inline-block;
    letter-spacing: 0.08em;
`;

const TightCss = css`
    border-radius: 24px;
    font-family: ${fonts.bold};
    font-weight: 600;
    font-size: 8px;
    line-height: 10px;
    min-height: 10px;
    min-width: 4px;
    padding: 1px 4px;
    text-transform: uppercase;

    ${media.desktop1920} {
        border-radius: 20px;
        font-size: 11px;
        line-height: 16px;
        min-height: 16px;
        padding: 0 6px;
    }
`;

const FitCss = css`
    border-radius: 24px;
    font-family: ${fonts.bold};
    font-weight: 600;
    font-size: 8px;
    line-height: 10px;
    padding: 5px 8px;
    text-transform: uppercase;

    ${media.desktop1920} {
        border-radius: 30px;
        font-size: 11px;
        line-height: 16px;
        padding: 6px 12px;
    }
`;

export const Container = styled.span`
    ${CommonCss}
    ${(props) => props.size === 'tight' && TightCss}
    ${(props) => props.size === 'fit' && FitCss}
`;
