import { BAD_DATE, LOCATION, SPRING_REDIRECT_HEADER } from 'constants/Constants';
import { LOGIN, SECURITY_CHECK_URL } from 'constants/UrlConstants';

import { convertFieldsToSearchParams } from '../components/form-multi/utils';
import { amplitudeLogout } from '../components/utils/AmplitudeUtils';

export const getFullEncodeApiUrl = (path = '/') => {
    let url = path;
    if (url.indexOf(SECURITY_CHECK_URL) > 0) {
        url = `${SECURITY_CHECK_URL}`;
    }
    return encodeURI('/api' + url);
};

export const processResponseForCloudFlare = (response, path, options, actions) => {
    if (response.status === 497 && actions?.toggleCloudFlareModal) {
        actions.toggleCloudFlareModal(true, path, options);
        throw new Error('CloudFlare error');
    }
};

export function apiFetchData(path = '/', fields, controllerRef, cloudFlareObj = {}) {
    const params = new URLSearchParams();
    if (fields) {
        Object.values(fields).forEach((field) => {
            if (field.value) {
                params.append(field.canonicalPath ? field.canonicalPath : field.name, field.value);
            }
        });
    }
    const options = {
        method: 'POST',
        mode: 'cors',
        credentials: 'include',
        body: params,
        redirect: 'manual',
        headers: { Accept: 'application/json' },
    };
    if (controllerRef) {
        if (!controllerRef.current) {
            controllerRef.current = new AbortController();
        }
        try {
            controllerRef.current.abort();
        } catch (e) {
            console.warn(e);
        } finally {
            controllerRef.current = new AbortController();
        }
        options.signal = controllerRef.current.signal;
    }
    return fetch(getFullEncodeApiUrl(path), options).then((response) => {
        processResponseForCloudFlare(response, getFullEncodeApiUrl(path), options, cloudFlareObj);
        if (response.status === 403) {
            window.location.replace(LOGIN);
            amplitudeLogout();
            throw new Error(`redirect to ${LOGIN}`);
        }
        if (response.type === 'opaqueredirect') {
            window.location.replace(LOGIN);
            throw new Error(`redirect to ${LOGIN}`);
        }
        if (response.headers.has(SPRING_REDIRECT_HEADER)) {
            window.location.replace(response.headers.get(SPRING_REDIRECT_HEADER));
            throw new Error(`redirect to ${response.headers.get(SPRING_REDIRECT_HEADER)}`);
        } else if (response.headers.has(LOCATION)) {
            window.location.replace(response.headers.get(LOCATION));
            throw new Error(`redirect to ${response.headers.get(LOCATION)}`);
        } else if (response.status === 500) {
            throw new Error(`response status 500 ${response.statusText}`);
        }
        return response.json();
    });
}

