export function fetchData(path = '/', method = 'POST', controllerRef) {
    const options = {
        method: method,
        mode: 'cors',
        credentials: 'include',
        headers: { Accept: 'application/json' },
    };
    if (controllerRef) {
        if (!controllerRef.current) {
            controllerRef.current = new AbortController();
        }
        try {
            controllerRef.current.abort();
        } catch (e) {
            console.warn(e);
        } finally {
            controllerRef.current = new AbortController();
        }
        options.signal = controllerRef.current.signal;
    }
    return fetch(getFullEncodeApiUrl(path), options)
        .then((response) => {
            if (response.status === 403) {
                window.location.replace(LOGIN);
                amplitudeLogout();
                throw new Error(`redirect to ${LOGIN}`);
            }
            if (response.headers.has(SPRING_REDIRECT_HEADER)) {
                window.location.replace(response.headers.get(SPRING_REDIRECT_HEADER));
                throw new Error(`redirect to ${LOGIN}`);
            } else if (response.headers.has(LOCATION)) {
                window.location.replace(response.headers.get(LOCATION));
                throw new Error(`redirect to ${response.headers.get(LOCATION)}`);
            }
            return response.json();
        })
        .catch((error) => {
            console.warn(error);
            return null;
        });
}

export function apiFetchDataForFlink(path, data) {
    return fetch(getFullEncodeApiUrl(path), {
        method: 'POST',
        mode: 'cors',
        body: convertFieldsToSearchParams(data, true),
        credentials: 'include',
    }).then((response) => response.json());
}

export function apiFetchDataForApplePay(path, data) {
    let params = new URLSearchParams();
    for (const [key, value] of Object.entries(data)) {
        params.append(key, encodeURIComponent(value));
    }
    return fetch(getFullEncodeApiUrl(path), {
        method: 'POST',
        mode: 'cors',
        credentials: 'include',
        body: params,
        headers: { Accept: 'application/json' },
    }).then((response) => response.json());
}

export function apiFetchDataBlob(path, addErrorMessageBody) {
    return fetch(getFullEncodeApiUrl(path), {
        method: 'GET',
        mode: 'cors',
        credentials: 'include',
        headers: { Accept: 'application/pdf' },
    }).then((response) => {
        if (response.status === 500) {
            const message = 'Server Error';
            addErrorMessageBody(message);
            throw new Error(message);
        }
        const isErrorWithDate = response.headers.has(BAD_DATE);
        if (isErrorWithDate) {
            const message = response.headers.get(BAD_DATE);
            addErrorMessageBody(message);
        } else {
            return response.blob();
        }
    });
}

export const changeBrowserUrlWithOutReload = (historyURL, json = {}) => {
    if (window && window.history && window.history.pushState) {
        window.history.pushState({ html: json, pageTitle: json.pageTitle }, '', historyURL);
    } else {
        window.location.href = historyURL;
    }
};

export const encodeQueryFields = (fields) => {
    const ret = [];
    Object.keys(fields).forEach((key) => {
        const field = fields[key];
        ret.push(encodeURIComponent(field.canonicalPath ? field.canonicalPath : field.name) + '=' + encodeURIComponent(field.value));
    });

    return ret.join('&');
};

export const getParams = (search) => {
    return new Map(
        search
            .slice(1)
            .split('&')
            .map((kv) => kv.split('=')),
    );
};
