import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import InputText from 'components/form-multi/InputText';
import { getField } from 'components/form-multi/utils';

import { togglePopup, toggleSpinner } from 'actions/appAction';
import { addNewFields, changeFormField, changeFormFieldValue, removeFieldError, setFieldError } from 'actions/formAction';
import { updatePaymentForm } from 'actions/sendAction';

import { REQ_ERROR } from 'constants/Constants';
import { INVITE_CODE_FIELD, PROMO_CODE_FIELD } from 'constants/fieldsName';

import { apiFetchData } from 'helpers/api';

import { useForm, useJsMessages, usePageMessages } from 'hooks/selectors';

import ComplexPopup from '../common/Popup/ComplexPopup';
import PopupButton from '../common/Popup/PopupButton';
import SimplePopup from '../common/Popup/SimplePopup';
import { ComplexPopupRow } from '../common/Popup/styles';

const getFieldName = (isPromoCode) => {
    return isPromoCode ? PROMO_CODE_FIELD : INVITE_CODE_FIELD;
};

const getActualUri = (isPromoCode, path) => {
    if (path) {
        return path;
    }
    return isPromoCode ? '/auth/send/promoCode.check' : '/auth/send/inviteCode.check';
};

const useAddNewFields = (addNewFieldsAction, removeFieldErrorAction, formName, messages, canUsePromoCode, canUseInviteCode, fields = {}) => {
    useEffect(() => {
        let newFields = {};
        if (!fields[PROMO_CODE_FIELD] && canUsePromoCode) {
            newFields[PROMO_CODE_FIELD] = getField(PROMO_CODE_FIELD, '', null, false, false, messages.promoCodePlaceholder, 'text', true, true);
        }
        if (!fields[INVITE_CODE_FIELD] && canUseInviteCode) {
            newFields[INVITE_CODE_FIELD] = getField(INVITE_CODE_FIELD, '', null, false, false, messages.invitationCodePlaceholder, 'text', true, true);
        }
        addNewFieldsAction(formName, newFields);
        return () => {
            if (!fields[PROMO_CODE_FIELD]?.value && fields[PROMO_CODE_FIELD]?.isError) {
                removeFieldErrorAction(formName, PROMO_CODE_FIELD);
            }
            if (!fields[INVITE_CODE_FIELD]?.value && fields[INVITE_CODE_FIELD]?.isError) {
                removeFieldErrorAction(formName, INVITE_CODE_FIELD);
            }
        };
    }, [
        addNewFieldsAction,
        canUseInviteCode,
        canUsePromoCode,
        fields,
        formName,
        messages.invitationCodePlaceholder,
        messages.promoCodePlaceholder,
        removeFieldErrorAction,
    ]);
};
const useHandleClickOutside = (showChoose, popupRef, formName, isPromoCode, actions) => {
    const handleClickOutside = (e) => {
        if (!showChoose && Boolean(popupRef.current) && !popupRef.current.contains(e.target)) {
            actions.changeFormFieldValue(formName, '', getFieldName(isPromoCode));
            actions.removeFieldError(formName, getFieldName(isPromoCode));
        }
    };
    useEffect(() => {
        if (__isBrowser__ && !showChoose) {
            document.addEventListener('click', handleClickOutside);
        }
        return () => {
            document.removeEventListener('click', handleClickOutside);
        };
    }, [showChoose]);
};

const AddPromoOrInviteCode = (props) => {
    const popupRef = useRef(null);
    const [, setClearField] = useState(true);
    const [isPromoCode, setPromoCode] = useState(props.canUsePromoCode);
    const [showChoose, setShowChoose] = useState(props.canUseInviteCode && props.canUsePromoCode);
    const messages = usePageMessages();
    const jsMessages = useJsMessages();
    const { fields } = useForm(props.formName);

    useAddNewFields(props.addNewFields, props.removeFieldError, props.formName, messages, props.canUsePromoCode, props.canUseInviteCode, fields);

    useHandleClickOutside(showChoose, popupRef, props.formName, isPromoCode, {
        changeFormFieldValue: props.changeFormFieldValue,
        removeFieldError: props.removeFieldError,
    });

    const closePopup = () => {
        if (props.handleClose) {
            props.handleClose();
        } else {
            props.togglePopup(false);
        }
    };

    const addCode = (e) => {
        e.preventDefault();
        if (!fields[getFieldName(isPromoCode)].value && fields[getFieldName(isPromoCode)].value.length === 0) {
            props.setFieldError(props.formName, getFieldName(isPromoCode), jsMessages[REQ_ERROR]);
        } else {
            props.toggleSpinner(true);
            apiFetchData(getActualUri(isPromoCode, props.path), fields)
                .then((json) => {
                    if ((json?.extra?.showError && json.errors) || (json?.errors && Object.keys(json.errors).length > 0)) {
                        Object.keys(json.errors).forEach((name) => {
                            props.setFieldError(props.formName, name, json.errors[name]);
                        });
                        props.changeFormFieldValue(props.formName, '', getFieldName(isPromoCode));
                    } else {
                        setClearField(false);
                        props.updateRedux(json);
                        closePopup();
                    }
                })
                .catch((err) => console.error(err))
                .finally(() => props.toggleSpinner(false));
        }
    };

    const makeChoose = (e, isPromo) => {
        e.preventDefault();
        setPromoCode(isPromo);
        setShowChoose(false);
    };

    const renderShowChoose = (
        <SimplePopup text={messages.headerChoose} handleClose={props.handleClose}>
            <PopupButton
                title={messages.invitationCodePlaceholder}
                iconName="person"
                text={messages.invitationCodePlaceholder}
                onClick={(e) => makeChoose(e, false)}
            />
            <PopupButton title={messages.promoCodePlaceholder} iconName="scissors" text={messages.promoCodePlaceholder} onClick={(e) => makeChoose(e, true)} />
        </SimplePopup>
    );

    const renderPopup = () => {
        if (fields[getFieldName(isPromoCode)]) {
            return (
                <ComplexPopup handleClose={props.handleClose} textCenter primaryButtonText={messages.addCodeAdd} onPrimaryButtonClick={(e) => addCode(e)}>
                    <ComplexPopupRow>{<p>{isPromoCode ? messages.headerPromoCode : messages.headerInvitationCode}</p>}</ComplexPopupRow>
                    <InputText isClearErrorOnFocus formName={props.formName} field={fields[getFieldName(isPromoCode)]} isNewInput />
                </ComplexPopup>
            );
        }
    };

    return <div ref={popupRef}>{showChoose ? renderShowChoose : renderPopup()}</div>;
};

AddPromoOrInviteCode.propTypes = {
    popupRef: PropTypes.shape({ current: PropTypes.object }),
    handleClose: PropTypes.func,
    updateRedux: PropTypes.func,
    formName: PropTypes.string,
    canUsePromoCode: PropTypes.bool,
    canUseInviteCode: PropTypes.bool,
    path: PropTypes.string,
};
export default connect(null, {
    addNewFields,
    setFieldError,
    updatePaymentForm,
    changeFormFieldValue,
    togglePopup,
    toggleSpinner,
    changeFormField,
    removeFieldError,
})(AddPromoOrInviteCode);
