import React from 'react';

import PropTypes from 'prop-types';

import { EmptyContainer } from './styles';
import { Button } from '../../Button/Button';
import { Icon } from '../../Icon/Icon';
import { Text } from '../../Text/Text';
import { Row } from '../Row';

export const EmptyPage = (props) => {
    return (
        <EmptyContainer>
            {props.iconName && (
                <Row indent="16px">
                    <Icon name={props.iconName} color="mediumGray" />
                </Row>
            )}
            <Text color="mediumGray" indent={props.emptyButtonCaption ? 'adaptive-16-24' : null}>
                {props.emptyText}
            </Text>
            {props.emptyButtonCaption && <Button variant="row" caption={props.emptyButtonCaption} onClick={props.emptyButtonClick} negativeMargin />}
        </EmptyContainer>
    );
};

EmptyPage.propTypes = {
    iconName: PropTypes.string,
    emptyText: PropTypes.string,
    emptyButtonCaption: PropTypes.string,
    emptyButtonClick: PropTypes.func,
};
