import React, { useState } from 'react';

import styled from 'styled-components';

import { DangerouslyHtml } from 'components/common/DangerouslyHtml/DangerouslyHtml';
import { FlagIcon } from 'components/common/FlagIcon/FlagIcon';
import { Icon } from 'components/common/Icon/Icon';
import { Text } from 'components/common/Text/Text';

import { articlePropType } from 'helpers/customPropTypes';

import colors from 'theme/colors';
import media from 'theme/media';

const CSS_COLLAPSED = 'collapsed';
const Value = styled(Text)``;
const IconWrapper = styled.span`
    height: 24px;
    overflow: hidden;
    margin-left: 16px;
    min-width: 24px;
    transition-duration: 0.4s;
    transition-property: transform;
    width: 24px;
`;
const FlagWrapper = styled.span`
    height: 24px;
    overflow: hidden;
    margin-right: 24px;
    min-width: 24px;
    width: 24px;

    ${media.tablet648} {
        margin-top: 4px;
    }
`;
const FlexWrapper = styled.h3`
    display: flex;
    font-weight: normal;
    flex-grow: 1;
    justify-content: space-between;
`;
const AccordeonHeader = styled.a`
    cursor: pointer;
    display: flex;
    padding: 30px 0 2px;

    &:hover {
        ${Value} {
            color: ${colors.purple};
        }

        ${IconWrapper} {
            use {
                --accent: ${colors.purple};
            }
        }
    }

    &.collapsed {
        ${IconWrapper} {
            transform: rotate(180deg);
        }
    }

    ${media.tablet648} {
        padding: 40px 0 8px;
    }
`;
const AccordeonContent = styled.div`
    margin-bottom: 24px;
    margin-top: 24px;
    max-width: 592px;
`;
const Accordeon = (props) => {
    const [isOpen, setOpen] = useState(false);
    const clickHandler = (e) => {
        e.preventDefault();
        setOpen(!isOpen);
    };
    return (
        <li>
            <AccordeonHeader className={isOpen ? CSS_COLLAPSED : ''} onClick={clickHandler}>
                {props.addIcon ? (
                    <FlagWrapper>
                        <FlagIcon name={props.addIcon} size={'24px'} />
                    </FlagWrapper>
                ) : null}
                <FlexWrapper>
                    <Value color={'darkGray'} isPromo size={'main'} variant={'span'}>
                        {props.title}
                    </Value>
                    <IconWrapper>
                        <Icon color={'darkGray'} name={'chevron-down'} size={'24px'} />
                    </IconWrapper>
                </FlexWrapper>
            </AccordeonHeader>
            {isOpen && (
                <AccordeonContent>
                    <DangerouslyHtml
                        color={`${props.addIcon ? 'darkGray' : 'mediumGray'}`}
                        dangerouslyHtml={props.text}
                        isPromo
                        size={'main'}
                        type={'text'}
                        variant={'div'}
                    />
                    {props.picture ? props.picture : null}
                    {props.link ? props.link : null}
                </AccordeonContent>
            )}
        </li>
    );
};

Accordeon.propTypes = articlePropType.isRequired;
export default Accordeon;
