import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useLocation } from 'react-router';

import PropTypes from 'prop-types';

import { Icon } from 'components/common/Icon/Icon';
import { Row } from 'components/common/Layout/Row';
import { Text } from 'components/common/Text/Text';
import InputAutoDetect from 'components/form/InputAutoDetect';
import Form from 'components/form-multi/Form';
import { validationOnRequiredAndReturnErrorFields } from 'components/form-multi/utils';
import Spinner from 'components/utils/Spinner';

import { toggleSpinner } from 'actions/appAction';
import { changeFormField, removeFields, setNewForm, updateFields } from 'actions/formAction';

import { ADD_REQUEST_ACQUIRING } from 'constants/formName';
import { STRAPI_PAYSEND_API } from 'constants/UrlConstants';

import { isObject } from 'helpers/utils';

import { DataSent, IconWrapper, ModalHeader } from './styles';
import { getEventNameFromUrl, sendGa } from '../../../../../analytics/analytics.js';
import ComplexPopup from '../../../../common/Popup/ComplexPopup';
import { getStrapiField } from '../../../../form-multi/strapiFormUtils';

const RequestFormPopup = (props) => {
    const [isFormReady, setIsFormReady] = useState(false);
    const [isFormDataSent, setIsFormDataSent] = useState(false);
    const [isShowSpinner] = useState(false);
    const [isDisabledButton, setIsDisabledButton] = useState(true);
    const location = useLocation();

    useEffect(() => {
        if (isObject(props.formFields)) {
            setIsFormReady(true);
            setIsFormDataSent(false);
        }
    }, []);

    useEffect(() => {
        props.setNewForm(ADD_REQUEST_ACQUIRING, getFields(props.formFields));
        if (isFormDataSent) {
            Object.values(props.formFields).forEach((field) => props.removeFields(ADD_REQUEST_ACQUIRING, '', field.name));
        }
        return () => {
            if (isObject(props.formFields) && isObject(props.formFields.fields)) {
                const clearFields = { ...props.formFields.fields };
                Object.values(clearFields).forEach((field) => {
                    field.value = '';
                    field.isError = false;
                });
                props.updateFields(ADD_REQUEST_ACQUIRING, clearFields);
            }
        };
    }, []);

    const getFields = (accountForm) => {
        const fields = {};
        if (Array.isArray(accountForm)) {
            accountForm.forEach((gField) => {
                if (gField) {
                    fields[gField.name] = getStrapiField(
                        gField.name,
                        gField.value,
                        gField.items,
                        gField.checkRegexp,
                        gField.mandatory,
                        gField.isError,
                        gField.label ? gField.label : '',
                        gField.type,
                        gField.visible,
                        gField.canonicalPath,
                        gField.order,
                    );
                }
            });
        }
        return fields;
    };
    const convertFields = (info) => {
        const data = {};
        Object.entries(info).forEach(([_key, value]) => {
            if (value.type === 'country') {
                const countryName = value.items.filter((item) => item.id === value.value)[0];
                data[value.name] = countryName.name_en ? countryName.name_en : countryName.name;
            } else {
                data[value.name] = value.value;
            }
        });
        data['sendFrom'] = props.sendFrom;
        return data;
    };

    const blurHandler = () => {
        const errorFields = validationOnRequiredAndReturnErrorFields(ADD_REQUEST_ACQUIRING, props.redux_form.fields, props.changeFormField);
        if (errorFields.length === 0) {
            setIsDisabledButton(false);
        } else {
            setIsDisabledButton(true);
        }
    };

    const submitHandler = (e) => {
        e.preventDefault();
        const fieldsValues = convertFields(props.redux_form.fields);
        const errorFields = validationOnRequiredAndReturnErrorFields(ADD_REQUEST_ACQUIRING, props.redux_form.fields, props.changeFormField);
        if (errorFields.length === 0) {
            props.toggleSpinner(true);
            fetch(`${STRAPI_PAYSEND_API}/api/v2/business-form`, {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                withCredentials: true,
                body: JSON.stringify(fieldsValues),
            })
                .then((resp) => {
                    if (resp.status === 200) {
                        sendGa('conversion', getEventNameFromUrl(location.pathname));
                        setIsFormDataSent(true);
                    }
                })
                .then(() => props.toggleSpinner(false))
                .catch((err) => console.error(err) && props.toggleSpinner(false));
        }
    };

    const closePopup = () => {
        props.popupRef.current?.close();
    };

    return (
        <ComplexPopup
            primaryButtonType="submit"
            primaryButtonForm={ADD_REQUEST_ACQUIRING}
            primaryButtonText={isFormDataSent ? props.closeBtn : props.sendBtn}
            onPrimaryButtonClick={isFormDataSent ? closePopup : submitHandler}
            primaryButtonDisabled={isDisabledButton}
            withoutCloseButton={true}
            popupRef={props.popupRef}
        >
            {isFormReady && !isFormDataSent && (
                <>
                    {isShowSpinner && <Spinner forceShow insidePopup isEmptyText />}
                    <Form formName={ADD_REQUEST_ACQUIRING}>
                        <ModalHeader>
                            {props.formTitle && (
                                <Text size="main" isPromo variant="paragraph">
                                    {props.formTitle}
                                </Text>
                            )}
                        </ModalHeader>
                        {Object.values(props.redux_form.fields).map((field) => {
                            return (
                                <Row indent={'24px'} key={field.name}>
                                    <InputAutoDetect key={field.name} formName={ADD_REQUEST_ACQUIRING} field={field} onBlurHandler={blurHandler} />
                                </Row>
                            );
                        })}
                    </Form>
                </>
            )}
            {isFormDataSent && (
                <DataSent>
                    <IconWrapper>
                        <Icon name="check-rounded-thin" size="80px" color="purple" />
                    </IconWrapper>
                    {props.formSendTitle && (
                        <Text size="main" isPromo variant="paragraph" indent="16px">
                            {props.formSendTitle}
                        </Text>
                    )}
                    {props.formSendText && (
                        <Text size="secondary" color="mediumGray" isPromo variant="paragraph">
                            {props.formSendText}
                        </Text>
                    )}
                </DataSent>
            )}
        </ComplexPopup>
    );
};

RequestFormPopup.propTypes = {
    popupRef: PropTypes.shape({ current: PropTypes.object }),
};
RequestFormPopup.defaultProps = {
    popupRef: null,
};

const mapStateToProps = (state) => {
    return {
        formTitle: state.extra?.strapiApi.request_form?.title,
        formSendTitle: state.extra?.strapiApi.request_form?.send_title,
        formSendText: state.extra?.strapiApi.request_form?.send_text,
        formFields: state?.extra?.strapiApi?.request_form?.fields,
        sendBtn: state?.extra?.strapiApi?.request_form?.send_btn,
        closeBtn: state?.extra?.strapiApi?.request_form?.close_btn,
        icon: state?.extra?.strapiApi?.request_form?.icon,
        redux_form: state?.form[ADD_REQUEST_ACQUIRING],
    };
};
export default connect(mapStateToProps, {
    setNewForm,
    updateFields,
    removeFields,
    toggleSpinner,
    changeFormField,
})(RequestFormPopup);
