import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import BaseLayoutPromo from './BaseLayoutPromo';
import media from '../../../theme/media';
import { DiskButton } from '../../common/DiskButton/DiskButton';
import { NavBar } from '../../common/Layout/NavBar/NavBar';

export const Container = styled.div`
    padding: ${(props) => (props.hasBottomPanel ? '72px 24px 128px 24px' : '72px 24px 48px 24px')};
    margin: 0 auto;
    /* content width 480 */
    max-width: 528px;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex: 1;

    ${media.tablet720} {
        padding: 128px 24px;
        /* content width 504px */
        max-width: 552px;
    }

    & > form {
        display: flex;
        flex-direction: column;
        flex: 1;
    }
`;

export const CommonPagePromo = (props) => {
    const setNavBarAlign = () => {
        if (props.rightHeaderComponent && props.backUrl) {
            return 'space-between';
        } else if (props.backUrl) {
            return 'left';
        } else {
            return 'right';
        }
    };

    return (
        <BaseLayoutPromo showPromoBanner={props.showPromoBanner}>
            <Container hasBottomPanel={props.hasBottomPanel}>{props.children}</Container>
            {(props.backUrl || props.rightHeaderComponent) && (
                <NavBar align={setNavBarAlign()}>
                    {props.backUrl && (
                        <DiskButton
                            type={props.isTagA ? 'tagA' : null}
                            to={props.backUrl}
                            href={props.backUrl}
                            onClick={props.onClickIcon}
                            iconName={props.leftCloseIcon ? 'cross' : 'arrow-back'}
                        />
                    )}
                    {props.rightHeaderComponent}
                </NavBar>
            )}
        </BaseLayoutPromo>
    );
};

CommonPagePromo.propTypes = {
    showPromoBanner: PropTypes.bool,
    backUrl: PropTypes.string,
    leftCloseIcon: PropTypes.bool, // если нужен крестик слева
    rightHeaderComponent: PropTypes.object,
    hasBottomPanel: PropTypes.bool, // если внизу на странице есть панель с кнопкой
    onClickIcon: PropTypes.func,
    isTagA: PropTypes.bool,
};
