import React from 'react';
import { connect } from 'react-redux';
import { useLocation } from 'react-router-dom';

import PropTypes from 'prop-types';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoIcon } from 'components/no-auth/common/PromoIcon/PromoIcon';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { PromoRow } from 'components/no-auth/common/PromoRow/PromoRow';
import { PromoText } from 'components/no-auth/common/PromoText/PromoText';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { HelpLink } from 'components/no-auth/help/styles';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

import { getLangCountryPrefix } from 'helpers/utils';

const NewHelpPage = (props) => {
    const location = useLocation();

    const getUrl = (section) => {
        if (section.isUrl && section.url) {
            return `${getLangCountryPrefix(props.lang, props.country)}${section.url}`;
        }
        return `${location.pathname.replace(/\/+$/, '')}/${section.slug}`;
    };

    return (
        <PromoPageContainer>
            <ColoredSectionContainer dimension="average" doubleTopPadding={!props.previewTitle}>
                {props.previewTitle && <HeadingSection title={props.previewTitle} text={props.previewText} />}
                <PromoRow indent="adaptive-32-48-64">
                    {props.items
                        .filter((item) => item !== null)
                        .map((section) => {
                            return (
                                <PromoRow indent="adaptive-24-32" key={section.id} lastChildNoMargin>
                                    <HelpLink href={getUrl(section)}>
                                        {section.title && (
                                            <PromoText tag="span" variant="subheader" color="baseColor">
                                                {section.title}
                                            </PromoText>
                                        )}
                                        <PromoIcon name="chevron-right" size="24px" color="baseColor" leftIndent="24px" />
                                    </HelpLink>
                                </PromoRow>
                            );
                        })}
                </PromoRow>
            </ColoredSectionContainer>
        </PromoPageContainer>
    );
};

const mapStateToProps = ({ extra }) => ({
    previewTitle: extra?.strapiApi?.title,
    previewText: extra?.strapiApi?.text,
    items: extra?.strapiApi?.items,
    lang: extra.noAuthFooter?.language?.lang,
    country: extra.noAuthFooter?.language?.country,
});

NewHelpPage.propTypes = {
    previewTitle: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    previewText: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    lang: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    country: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    items: PropTypes.oneOfType([PropTypes.array, PropTypes.oneOf([null])]),
};

export default connect(mapStateToProps)(NotAuthBasePage(NewHelpPage));
