import React from 'react';

import { PromoIcon } from 'components/no-auth/common/PromoIcon/PromoIcon';

import { AmountCurrencyFlag, AmountCurrencyLabel, AmountCurrencyText } from './styles';
import { FlagIcon } from '../../common/FlagIcon/FlagIcon';

const AmountCurrency = (props) => {
    const getDataTestAttr = (isLanding, isFrom) => {
        if (isLanding && isFrom) {
            return '-from';
        }
        if (isLanding && !isFrom) {
            return '-to';
        }
        return '';
    };

    return (
        <AmountCurrencyLabel
            data-testid={`amount-label${getDataTestAttr(props.isLanding, props.isFrom)}`}
            cursorPointer={props.isShowList}
            onClick={props.handlerToggleMenu}
        >
            <AmountCurrencyFlag>
                <FlagIcon name={props.countryBean.code} size="x-small" />
            </AmountCurrencyFlag>
            <AmountCurrencyText variant="subheader">{props.amountFieldBean.currency}</AmountCurrencyText>
            {props.isShowList && <PromoIcon name="chevron-down" color="baseColor" size="24px" />}
        </AmountCurrencyLabel>
    );
};

export default AmountCurrency;
