import React from 'react';
import { connect } from 'react-redux';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import FaqSection from 'components/no-auth/common/sections/FaqSection';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { HeroSection } from 'components/no-auth/common/sections/HeroSection';
import { HighlightedTilesSection } from 'components/no-auth/common/sections/HighlightedTilesSection';
import { PaymentSystemLogos } from 'components/no-auth/common/sections/PaymentSystemLogos';
import { QuoteSectionV2 } from 'components/no-auth/common/sections/QuoteSectionV2';
import { TilesSection } from 'components/no-auth/common/sections/TilesSection';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

import { CardsSection } from '../../../components/no-auth/common/sections/CardsSection';

const ConsultingPage = (props) => {
    return (
        <PromoPageContainer bgColor="eggshell02" noIndent>
            {props.heroTitle && (
                <HeroSection
                    title={props.heroTitle}
                    text={props.heroText}
                    img={props.heroImg}
                    btnVariant="primary"
                    noIconBtn
                    horizontalLayout
                    data={props.heroData}
                    fetchPriorityHigh
                />
            )}
            <ColoredSectionContainer bgColor="white">
                {props.contentRowTiles && <CardsSection data={props.contentRowTiles} titleGradient="blueberryBlue01" variant="contentRow" />}
            </ColoredSectionContainer>
            <ColoredSectionContainer>
                {props.headline1Title && (
                    <HeadingSection
                        title={props.headline1Title}
                        text={props.headline1Text}
                        btn={props.headline1Btn}
                        btnMobile={props.headline1BtnMobile}
                        isMobileBtn={props.headline1BtnIsMobileBtn}
                    />
                )}
                {props.tiles && <TilesSection data={props.tiles} loadingLazy />}
                {props.headline2Title && (
                    <HeadingSection
                        title={props.headline2Title}
                        text={props.headline2Text}
                        btn={props.headline2Btn}
                        btnMobile={props.headline2BtnMobile}
                        isMobileBtn={props.headline2BtnIsMobileBtn}
                    />
                )}
                {props.tiles2 && <TilesSection data={props.tiles2} loadingLazy />}
                {props.headline3Title && (
                    <HeadingSection
                        title={props.headline3Title}
                        text={props.headline3Text}
                        btn={props.headline3Btn}
                        btnMobile={props.headline3BtnMobile}
                        isMobileBtn={props.headline3BtnIsMobileBtn}
                    />
                )}
                {props.quote && <QuoteSectionV2 data={props.quote} loadingLazy />}
                {props.tiles_highlighted && <HighlightedTilesSection data={props.tiles_highlighted} />}
            </ColoredSectionContainer>

            {props.logos && (
                <ColoredSectionContainer>
                    <PaymentSystemLogos logos={props.logos} topIcon={props.topIcon} />
                </ColoredSectionContainer>
            )}

            {props.faqTitle && props.questions && <FaqSection title={props.faqTitle} questions={props.questions} />}

            {props.acquisitionTitle && (
                <ColoredSectionContainer bgColor="purple5" bottomPadding>
                    <HeadingSection
                        text={props.acquisitionText}
                        title={props.acquisitionTitle}
                        btn={props.acquisitionBtn}
                        btnMobile={props.acquisitionBtnMobile}
                        isMobileBtn={props.acquisitionIsMobileBtn}
                        sendFrom={'Acquiring page'}
                        noBottomIndent
                    />
                </ColoredSectionContainer>
            )}
        </PromoPageContainer>
    );
};
const mapStateToProps = (state) => {
    return {
        heroTitle: state.extra?.strapiApi?.hero_block?.title,
        heroText: state.extra?.strapiApi?.hero_block?.text,
        heroImg: state.extra?.strapiApi?.hero_block?.img,
        heroData: state.extra?.strapiApi?.hero_block,

        contentRowTiles: state.extra?.strapiApi?.cards,

        headline1Title: state.extra?.strapiApi?.headline1?.title,
        headline1Text: state.extra?.strapiApi?.headline1?.text,
        headline1Btn: state.extra?.strapiApi?.headline1?.btn,
        headline1BtnMobile: state.extra?.strapiApi?.headline1?.btn_mobile,
        headline1BtnIsMobileBtn: state.extra?.strapiApi?.headline1?.isMobileBtn,

        tiles: state.extra?.strapiApi?.tiles_section,

        headline2Title: state.extra?.strapiApi?.headline2?.title,
        headline2Text: state.extra?.strapiApi?.headline2?.text,
        headline2Btn: state.extra?.strapiApi?.headline2?.btn,
        headline2BtnMobile: state.extra?.strapiApi?.headline2?.btn_mobile,
        headline2BtnIsMobileBtn: state.extra?.strapiApi?.headline2?.isMobileBtn,

        tiles2: state.extra?.strapiApi?.tiles_section2,

        headline3Title: state.extra?.strapiApi?.headline3?.title,
        headline3Text: state.extra?.strapiApi?.headline3?.text,
        headline3Btn: state.extra?.strapiApi?.headline3?.btn,
        headline3BtnMobile: state.extra?.strapiApi?.headline3?.btn_mobile,
        headline3BtnIsMobileBtn: state.extra?.strapiApi?.headline3?.isMobileBtn,

        quote: state.extra?.strapiApi?.quote_section,

        tiles_highlighted: state.extra?.strapiApi?.tiles_highlighted,

        topIcon: state.extra?.strapiApi?.cards_icons,
        logos: state.extra?.strapiApi?.cards_icons2,

        faqTitle: state.extra?.strapiApi?.faq?.title,
        questions: state.extra?.strapiApi?.faq?.questions,

        acquisitionTitle: state.extra?.strapiApi?.headline4?.title,
        acquisitionText: state.extra?.strapiApi?.headline4?.text,
        acquisitionBtn: state.extra?.strapiApi?.headline4?.btn,
        acquisitionBtnMobile: state.extra?.strapiApi?.headline4?.btn_mobile,
        acquisitionBtnIsMobileBtn: state.extra?.strapiApi?.headline4?.isMobileBtn,

        lang: state.extra.noAuthFooter?.language?.lang,
        country: state.extra.noAuthFooter?.language?.country,
    };
};
export default connect(mapStateToProps)(NotAuthBasePage(ConsultingPage));
