const LandingPage = styled.div`
    ${MainContent} {
        margin: 0 auto;
        max-width: 480px;
    }

    ${media.tablet648} {
        ${MainContent} {
            max-width: 592px;
        }
    }
`;
const LibreNoAuthInfo = (props) => {
    const [isShowVideo, setShowVideo] = useState(false);
    const movieRef = useRef(null);

    const curIso = props.paymentInfo?.mobileInfo?.toCurIso;
    const amount = props.paymentInfo?.toAmtReal;
    const comment = props.paymentInfo?.payComment;
    const fromTitle = resolveArguments(props.fromTitleWithoutName, props.paymentInfo.extra.sender_first_name + ' ' + props.paymentInfo.extra.sender_last_name);

    useEffect(() => {
        const handleScroll = () => {
            const stickyPanel = document.getElementById('sticky_panel');
            if (window.scrollY >= stickyPanel.offsetHeight * 0.4) {
                stickyPanel.classList.add('fixed');
            } else {
                stickyPanel.classList.remove('fixed');
            }
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    useEffect(() => {
        const keyPressHandler = ({ keyCode }) => {
            if (keyCode === 27) {
                document.body.style.overflow = '';
                setShowVideo(false);
            }
        };
        if (__isBrowser__) {
            document.addEventListener('keydown', keyPressHandler);
        }
        return () => {
            if (__isBrowser__) {
                document.removeEventListener('keydown', keyPressHandler);
            }
        };
    }, []);

    const overlayClick = (e) => {
        if (isShowVideo && movieRef.current && !movieRef.current.contains(e.target)) {
            toggleVideo(e);
        }
    };

    const toggleVideo = (e) => {
        e.preventDefault();
        document.body.style.overflow = !isShowVideo ? 'hidden' : '';
        setShowVideo(!isShowVideo);
    };

    const getUserInfo = () => {
        return (
            <FrameTop>
                <Row indent="adaptive-16-24">
                    <ActivityIcon item={props.paymentInfo} options={{ failedCheque: false }} flagName="paysend" />
                </Row>

                <Text variant="div" color="darkGray" indent="4px" ellipsis>
                    {fromTitle}
                </Text>

                <Row indent="adaptive-32-48">
                    <Row indent="adaptive-8-16">
                        <Text size="display1" color="purple">
                            {amount}&nbsp;
                            <Text size="display1" smallCaps color="purple">
                                {curIso}
                            </Text>
                        </Text>
                    </Row>
                    {comment && (
                        <Text variant="div" color="lightGray">
                            {comment}
                        </Text>
                    )}
                </Row>

                <Button caption={props.receiveButton} raised="true" type={'link'} href={props.continueLink} />
            </FrameTop>
        );
    };
    const getUserInfoPanel = () => {
        return (
            <StickyPanel id={'sticky_panel'}>
                <MainContent>
                    <FlexContent>
                        <GroupLeft>
                            <IconWrapper>
                                <ActivityIcon diskSize={'small'} item={props.paymentInfo} options={{ failedCheque: false }} flagName="paysend" />
                            </IconWrapper>

                            <TextContent>
                                <Text color={'darkGray'} ellipsis indent={'4px'} isPromo size={'main'} variant={'div'}>
                                    {fromTitle}
                                </Text>
                                {comment && (
                                    <Text color={'mediumGray'} ellipsis isPromo size={'secondary'} variant={'div'}>
                                        {comment}
                                    </Text>
                                )}
                            </TextContent>
                        </GroupLeft>
                        <GroupRight>
                            <Text color={'purple'} indent={'4px'} isPromo size={'main'} variant={'div'}>
                                {amount}&nbsp;
                                <Text color={'purple'} isPromo size={'main'} smallCaps>
                                    {curIso}
                                </Text>
                            </Text>
                            <BadgeLink href={props.continueLink} title={props.receiveButton}>
                                <Badge caption={props.receiveButton} size={'fit'} variant={'primary'} />
                            </BadgeLink>
                        </GroupRight>
                    </FlexContent>
                </MainContent>
            </StickyPanel>
        );
    };

    const getVideoFrame = (
        <ModalPortal>
            <Overlay>
                <TopBar>
                    <Inner>
                        <IconWrapper onClick={toggleVideo}>
                            <Icon color={'darkGray'} name={'cross'} size={'24px'} />
                        </IconWrapper>
                    </Inner>
                </TopBar>
                <ModalOuter onClick={overlayClick}>
                    <ModalInner>
                        <MovieWrapper id="movieFrame" ref={movieRef}>
                            <iframe
                                src={props.howItWorksLink}
                                width="100%"
                                height="100%"
                                frameBorder="0"
                                allow="autoplay; encrypted-media;accelerometer; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen="1"
                                title={''}
                            />
                        </MovieWrapper>
                    </ModalInner>
                </ModalOuter>
            </Overlay>
        </ModalPortal>
    );

    const getVideoBlock = () => {
        return (
            <>
                <VideoPoster>
                    <Illustration className="illustration" id="illustration">
                        <picture>
                            <source className=" lazyloading" media="(min-width: 656px)" srcSet={`${props.imgA} 1x, ${props.imgA2x} 2x`} />
                            <source className=" lazyloading" srcSet={`${props.imgB} 1x, ${props.imgB2x} 2x`} />
                            <img className=" lazyloaded" loading="lazy" decoding="async" alt="poster" srcSet={props.imgA} />
                        </picture>
                    </Illustration>
                </VideoPoster>

                <ButtonContainer>
                    <PlayButton onClick={toggleVideo}>
                        <IconWrapper>
                            <Icon color={'purple'} name={'play'} size={'24px'} />
                        </IconWrapper>
                        <Text color={'purple'} isPromo size={'main'} variant={'span'}>
                            {props.howItWorksTitle}
                        </Text>
                    </PlayButton>
                    {isShowVideo && getVideoFrame}
                </ButtonContainer>
            </>
        );
    };

    const getHowItWorksList = () => {
        return (
            <AccordeonBlock>
                {props.howItWorksList.map((element) => {
                    return <Accordeon title={element.title} text={element.text} key={element.id} />;
                })}
            </AccordeonBlock>
        );
    };

    const getHelpCenterBlock = () => {
        return (
            <FrameBottom>
                <Text color={'darkGray'} indent={'16px'} isPromo size={'header'} variant={'div'}>
                    {props.helpCenter.title}
                </Text>
                <Text color={'darkGray'} isPromo size={'main'} variant={'div'}>
                    {props.helpCenter.text}
                </Text>
                <ButtonContainer>
                    <PlayButton href={props.helpCenter.link}>
                        <IconWrapper>
                            <Icon color={'purple'} name={'lifebuoy'} size={'24px'} />
                        </IconWrapper>
                        <Text color={'purple'} isPromo size={'main'} variant={'span'}>
                            {props.helpCenter.name_link}
                        </Text>
                    </PlayButton>
                </ButtonContainer>
            </FrameBottom>
        );
    };

    return (
        <LandingPage>
            <MainContent>
                {getUserInfo()}
                <FrameCenter>
                    {getVideoBlock()}
                    {getHowItWorksList()}
                </FrameCenter>
                {getHelpCenterBlock()}
            </MainContent>
            {getUserInfoPanel()}
            <div id="modal-root" />
        </LandingPage>
    );
};
LibreNoAuthInfo.propTypes = {
    list: PropTypes.oneOfType([PropTypes.array, PropTypes.oneOf([null])]),
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    url: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([null])]),
    paymentInfo: PropTypes.object,
};

const mapStateToProps = (state) => {
    return {
        imgB2x: state.extra?.strapiApi?.receiveMoneyMc?.[3].url,
        imgB: state.extra?.strapiApi?.receiveMoneyMc?.[2].url,
        imgA2x: state.extra?.strapiApi?.receiveMoneyMc?.[1].url,
        imgA: state.extra?.strapiApi?.receiveMoneyMc?.[0].url,
        receiveButton: state.extra?.strapiApi?.receiveButton,
        howItWorksTitle: state.extra?.strapiApi?.how_it_works_link?.title,
        howItWorksLink: state.extra?.strapiApi?.how_it_works_link?.link,
        howItWorksList: state.extra?.strapiApi?.how_it_works_list,
        helpCenter: state.extra?.strapiApi?.help_center,
        continueLink: state.extra?.strapiApi?.continueLink,
        fromTitleWithoutName: state.extra?.strapiApi?.fromTitle,
    };
};
export default connect(mapStateToProps)(NotAuthBasePage(LibreNoAuthInfo));
