import React, { useRef, useState } from 'react';
import { connect } from 'react-redux';

import { Input, InputContainer } from './styles';
import { addErrorMessageBody } from '../../../actions/appAction';
import { changeFormFieldValue, removeFieldError, setFieldError, updateFields } from '../../../actions/formAction';
import { Icon } from '../Icon/Icon';
import { Row } from '../Layout/Row';
import { Text } from '../Text/Text';

const UploadFile = (props) => {
    const inputFile = useRef(null);
    const file = useRef(null);
    const [isFieldTitle, setFieldTitle] = useState(props.field.placeholder);

    const onChangeHandler = (e) => {
        e.preventDefault();
        file.current = e.target.files[0];
        props.field.value = e.target.files[0];
        const size = file.current.size || file.current.fileSize;
        props.removeFieldError(props.formName, props.field.name);
        if (size > 5242880) {
            props.field.isError = true;
            props.field.errorMessage = 'Max file size : 5Mb';
            setFieldTitle(props.field.placeholder);
            props.setFieldError(props.formName, props.field.name, props.field.errorMessage);
            return;
        }
        props.changeFormFieldValue(props.formName, file.current, props.field.name);
        props.updateFields(props.formName, file.current);
        setFieldTitle(file.current.name);
    };
    return (
        <div onChange={onChangeHandler}>
            <Row indent="24px" formName={props.formName}>
                <Text size="subheader" color="mediumGray" />
            </Row>
            <InputContainer key={`file_${props.field.name}`}>
                <Row
                    onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        inputFile.current.click();
                    }}
                >
                    <Icon name="plus" color="purple" /> {isFieldTitle}
                </Row>
                <Input type="file" className="hidden" name={props.field.name} ref={inputFile} onChange={onChangeHandler} />
            </InputContainer>
            {props.field.isError && <div className="field__error_message error-message-no-margin color-red">{props.field.errorMessage}</div>}
        </div>
    );
};

const mapStateToProps = ({ form, extra, messages }, ownProps) => {
    return {
        fields: form[ownProps.formName].fields,
    };
};

export default connect(mapStateToProps, {
    addErrorMessageBody,
    removeFieldError,
    changeFormFieldValue,
    updateFields,
    setFieldError,
})(UploadFile);
