import styled from 'styled-components';

import media from '../../../../theme/media';
import zIndex from '../../../../theme/zIndex';

const getJustifyContent = (props) => {
    if (props.isSearchBar || props.align === 'center') {
        return 'center';
    } else if (props.align === 'space-between') {
        return 'space-between';
    } else {
        return props.align === 'left' ? 'start' : 'flex-end';
    }
};
const getPadding = (props) => {
    if (props.fullwidth) {
        return '56px 136px';
    } else if (props.isSearchBar) {
        return '0';
    } else if (props.insideStories) {
        return '42px 16px 16px 16px';
    } else {
        return '56px 16px';
    }
};

export const Container = styled.div`
    background-color: #fff;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    height: 0;
    pointer-events: none;
    z-index: ${zIndex.navBar};

    ${media.desktop1224} {
        left: ${(props) => (props.fullwidth ? '360px' : null)};
        display: ${(props) => (props.isHiddenDesktop ? 'none' : 'block')};
    }
`;
export const Inner = styled.div`
    box-sizing: border-box;
    margin: 0 auto;
    padding: ${(props) => (props.isSearchBar ? '0' : props.insideStories ? '24px 16px 16px 16px' : '16px')};
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    display: flex;
    justify-content: ${(props) => getJustifyContent(props)};
    max-width: 528px;

    ${media.tablet720} {
        padding: ${(props) => getPadding(props)};
        max-width: ${(props) => (props.fullwidth ? '100%' : '568px')};
    }
    ${media.tablet1080} {
        max-width: ${(props) => (props.fullwidth ? '1080px' : '856px')};
    }
    ${media.desktop1224} {
        margin: ${(props) => (props.fullwidth ? '0' : '0 auto')};
    }
`;
