import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { Route, Routes } from 'react-router-dom';

import { GlobalStyle } from './globalStyles';
import { getLangCountryPrefix } from '../../helpers/utils';
import Popup from '../common/Popup/Popup';
import RouterCrossProductsLanding from '../no-auth/crossProducts/Landing/RouterCrossProductsLanding';
import Footer from '../no-auth/footer/Footer';
import StrapiHeader from '../no-auth/header/StrapiHeader';
import Banks from '../no-auth/landing/Banks';
import Benefits from '../no-auth/landing/Benefits';
import CalculatorSection from '../no-auth/landing/CalculatorSection';
import DeliveryMethodsLanding from '../no-auth/landing/DeliveryMethodsLanding';
import FAQ from '../no-auth/landing/Faq';
import Geo from '../no-auth/landing/Geo';
import Mastercard from '../no-auth/landing/Mastercard';
import MediaQuote from '../no-auth/landing/MediaQuote';
import MobileApp from '../no-auth/landing/MobileApp';
import Trustpilot from '../no-auth/landing/Trustpilot';
import NotFound from '../NotFound';
import BasePage from '../utils/BasePage';
import MessageBodyService from '../utils/MessageBodyService';
import Spinner from '../utils/Spinner';

const Landing = (props) => {
    const lang = props.noAuthFooter?.language?.lang ? props.noAuthFooter.language.lang : 'en';
    const country = props.noAuthFooter?.language?.country ? props.noAuthFooter.language.country : 'gb';

    useEffect(() => {
        const link = document.querySelector('link[rel="preload"]');
        if (link) {
            link.rel = 'stylesheet';
        }
    }, []);
    const data = props.data;
    let utmSource = props.data.showSectionByUtmSource;

    return (
        <>
            <GlobalStyle />
            <Routes>
                <Route
                    path={'*'}
                    element={
                        props.notFound ? (
                            <NotFound />
                        ) : (
                            <>
                                <div id="modal-root" />
                                {Array.isArray(props.newMenuObject) && props.newMenuObject.length > 0 && (
                                    <StrapiHeader
                                        isShowNavModal={props.isShowNavModal}
                                        menuSection={props.stateOptions?.menuSection}
                                        isLanding={true}
                                        data={props.newMenuObject}
                                        loading={props.loading}
                                    />
                                )}
                                <div id="content" className="temp-spacing">
                                    <CalculatorSection isRequest={data.request} localeDependentURI={data.localeDependentURI} features={data.features} />
                                    {props.showMastercardPromoBlock ? <Mastercard data={data.mastercard} countryMenu={props.countryMenu} /> : ''}
                                    <Trustpilot data={data.trustpilot} />
                                    <MobileApp data={data.mobile} extra={props?.extra} isRequest={data.request} />
                                    {utmSource && <Banks data={props.extra.availableBanks} />}
                                    {utmSource && <DeliveryMethodsLanding data={props.extra.deliveryMethodsPromo} countryTo={props.countryMenu.countryTo} />}
                                    {utmSource && <Benefits data={data.benefits} />}
                                    <Geo
                                        data={props.extra.geo}
                                        localeDependentURI={`${getLangCountryPrefix(lang, country)}${data.localeDependentURI}`}
                                        countryFrom={props.countryMenu.countryFrom}
                                        countryTo={props.countryMenu.countryTo}
                                        isRequest={data.request}
                                    />
                                    {utmSource && <FAQ data={data.faq} extra={props?.extra} countryMenu={props?.countryMenu} isRequest={data.request} />}
                                    {(props.isBannerStrapi || props.isBannerJava) && <RouterCrossProductsLanding />}
                                    {utmSource && !data.request && <MediaQuote data={data.mediaQuote} />}
                                </div>
                                <Spinner />
                                {props.noAuthFooter && (
                                    <Footer data={props.noAuthFooter} lang={lang} country={country} isLanding={true} isRequest={data.request} />
                                )}
                                <MessageBodyService />
                                <Popup />
                                {props.isShowModal && props.modalComponent}
                            </>
                        )
                    }
                />
            </Routes>
        </>
    );
};

const mapStateToProps = ({ app, calculator, extra }) => ({
    noAuthFooter: extra.noAuthFooter,
    isShowPopup: app.isShowPopup,
    isShowNavModal: app.isShowNavModal,
    isShowModal: app.isShowModal,
    modalComponent: app.modalComponent,
    countryMenu: calculator.countryMenu,
    lang: extra.noAuthFooter?.language?.lang,
    country: extra.noAuthFooter?.language?.country,
    showMastercardPromoBlock: extra.showMastercardPromoBlock,
    extra: extra,
    notFound: extra.notFound,
    newMenuObject: extra.newNoAuthMenu?.header,
    isBannerStrapi: extra.newNoAuthMenu?.isBanner,
    isBannerJava: extra?.showBanners,
});

export default connect(mapStateToProps)(BasePage(Landing));
