export const amplitudeLogout = () => {
    amplitude.getInstance().clearUserProperties();
    amplitude.getInstance().setUserId(null);
    amplitude.getInstance().regenerateDeviceId();
};

// Global Account
export const amplitudeEventForGetCardFromCards = () => {
    amplitude.getInstance().logEvent(GET_CARD_SELECTED, {
        [FROM]: CARD,
    });
};

export const amplitudeEventForGetCardFromAccount = () => {
    amplitude.getInstance().logEvent(GET_CARD_SELECTED, {
        [FROM]: ACCOUNT,
    });
};

export const amplitudeEventForCompletedTopUp = (method) => {
    amplitude.getInstance().logEvent(TOP_UP_COMPLETED, {
        [METHOD]: method,
    });
};

export const getParamsForGlobalMethods = (sourcesArr) => {
    let paramArr = [];
    if (Array.isArray(sourcesArr) && sourcesArr.length > 0) {
        sourcesArr.forEach((source) => {
            if (source.id === 1001) {
                paramArr.push(transactionMethods.GLOBAL_SWIFT);
            } else if (source.id === 1002) {
                paramArr.push(transactionMethods.GLOBAL_CARD);
            }
        });
    }
    return paramArr;
};

export const getEventNameForDraftTransfer = (chequeExtra) => {
    if (chequeExtra.draftCompletedVerification) {
        return RESUME_TRANSFER_CLICKED;
    } else if (chequeExtra.draftOnfidoCheckError) {
        return TRY_VERIFICATION_AGAIN_CLICKED;
    } else if (chequeExtra.draftSkippedVerification) {
        return VERIFY_IDENTITY_CLICKED;
    } else if (chequeExtra.draftOnfidoTooManyAttempts) {
        return CHAT_BUTTON_CLICKED;
    }
};
