import { useEffect } from 'react';

import { PAGE_VIEW, REFERRAL_SCREEN_PRESS_ADD, VISIT } from 'constants/CleverTapEvents';
import {
    COUNTRY,
    LANGUAGE,
    REFERRAL_CODE,
    REFERRAL_CODE_VALID,
    TIME,
    UTM_CAMPAIGN,
    UTM_MEDIUM,
    UTM_SOURCE,
    WEB_NAME,
    WEB_URL,
} from 'constants/CleverTapParamNames';

import Cookies from 'universal-cookie';

const cookies = new Cookies();
const TRY_COUNTER = __isDev__ ? 0 : 5;
const CLEVER_TAP_FIRST_TIME_VISIT = 'clever_tap_first_visit';

export const initializeClevertap = (clevertap) => {
    const clevertapId = process.env.CLEVERTAP_ID;
    if (__isBrowser__ && clevertap && clevertapId) {
        window.initClevertap = () => {
            var wzrk = document.createElement('script');
            wzrk.type = 'text/javascript';
            wzrk.async = true;
            wzrk.src = ('https:' == document.location.protocol ? 'https://d2r1yp2w7bby2u.cloudfront.net' : 'http://static.clevertap.com') + '/js/a.js?v=0';
            var s = document.getElementsByTagName('script')[0];
            s.parentNode.insertBefore(wzrk, s);
        };

        if (window.clevertap === undefined) {
            window.clevertap = { event: [], profile: [], account: [], onUserLogin: [], notifications: [], privacy: [] };
            window.clevertap.account.push({ id: clevertapId });
            window.clevertap.privacy.push({ optOut: false });
            window.clevertap.privacy.push({ useIP: false });
        }

        window.initClevertap();
    }
};

export const cleverTapLogin = ({ Name, Email, Phone, ...args }) => {
    const Site = {
        ...args,
        Name,
        Email,
        Phone,
    };

    if (typeof clevertap !== 'undefined' && typeof clevertap.onUserLogin !== 'undefined') {
        if (__isDev__) {
            console.log('cleverTapLogin Site: ', Site);
        }

        login(Site);
    } else {
        setInterval(() => login(Site), 700);
    }
};

const login = (Site) => {
    if (typeof clevertap !== 'undefined' && typeof clevertap.onUserLogin !== 'undefined') {
        if (__isDev__) {
            console.log('login Site: ', Site);
        }

        clevertap.onUserLogin.push({ Site });
        setProfile(Site);
    }
};

const setProfile = (Site) => {
    if (typeof clevertap !== 'undefined' && typeof clevertap.profile !== 'undefined') {
        if (__isDev__) {
            console.log('profile Site: ', Site);
        }

        clevertap.profile.push({ Site });
    }
};

export const cleverTapPgaViewEvent = (url, pageTitle, app, auth, lang) => {
    if (auth && auth.ballId) {
        if (__isDev__) {
            console.log('use login in PGA event: ', auth.ballId);
        }

        login({
            Identity: auth.ballId,
        });
    }

    let timerId = null;
    let counter = 1;
    if (typeof clevertap !== 'undefined' && typeof clevertap.event !== 'undefined') {
        pushCleverTapPgaViewEvent(url, pageTitle, app, auth, lang);
    } else {
        timerId = setInterval(() => pushCleverTapPgaViewEvent(url, pageTitle, app, auth, lang, counter++, timerId), 700);
    }
};

const addUTMparams = (params) => {
    let urlParams = new URLSearchParams(window.location.search);

    if (urlParams.has('utm_source')) {
        params = { [UTM_SOURCE]: urlParams.get('utm_source'), ...params };
    }

    if (urlParams.has('utm_medium')) {
        params = { [UTM_MEDIUM]: urlParams.get('utm_medium'), ...params };
    }

    if (urlParams.has('utm_campaign')) {
        params = { [UTM_CAMPAIGN]: urlParams.get('utm_campaign'), ...params };
    }

    return params;
};

export const cleverTapEvent = (eventName, params = {}) => {
    let timerId = null;
    let counter = 1;

    params = addUTMparams(params);

    if (typeof clevertap !== 'undefined' && typeof clevertap.event !== 'undefined') {
        pushCleverTapEvent(eventName, params);
    } else {
        timerId = setInterval(() => pushCleverTapEvent(eventName, params, counter++, timerId), 700);
    }
};

const pushCleverTapPgaViewEvent = (url, pageTitle, app, auth, lang, counter, timerId) => {
    const { country } = app;
    let params = {
        [WEB_URL]: url,
        [WEB_NAME]: pageTitle,
        [LANGUAGE]: lang,
        [COUNTRY]: country,
        [TIME]: new Date().toString(),
    };

    params = addUTMparams(params);

    if (__isDev__) {
        console.log('run sendCleverTapPgaViewEvent: params %o', params);
        console.log('pushCleverTapPgaViewEvent: counter %o, timerId', counter, timerId);
    }

    if (typeof clevertap !== 'undefined' && typeof clevertap.event !== 'undefined') {
        clevertap.event.push(PAGE_VIEW, params);
        if (!cookies.get(CLEVER_TAP_FIRST_TIME_VISIT)) {
            const date = new Date();
            date.setDate(date.getDate() + 60 * 24 * 365);
            cookies.set(CLEVER_TAP_FIRST_TIME_VISIT, 1, { path: '/', secure: true, expires: date });
        } else if (!cookies.get(VISIT)) {
            const date = new Date();
            date.setDate(date.getDate() + 60 * 24 * 365);
            cookies.set(VISIT, 1, { path: '/', secure: true, expires: date });
            clevertap.event.push(VISIT, params);
        }
        clearInterval(timerId);
    } else if (counter >= TRY_COUNTER) {
        clearInterval(timerId);
    }
};

const pushCleverTapEvent = (eventName, params, counter, timerId) => {
    if (__isDev__) {
        console.log('pushCleverTapEvent: eventName: "%s", params: %o, counter %o, timerId', eventName, params, counter, timerId);
    }
    if (typeof clevertap !== 'undefined' && typeof clevertap.event !== 'undefined') {
        clevertap.event.push(eventName, params);
        clearInterval(timerId);
    } else if (counter >= TRY_COUNTER) {
        clearInterval(timerId);
    }
};

export const sendInviteCode = (value, isError) =>
    cleverTapEvent(REFERRAL_SCREEN_PRESS_ADD, {
        [REFERRAL_CODE]: value || '',
        [REFERRAL_CODE_VALID]: isError ? 'invalid' : 'valid',
    });

export function useEventOnFirstRender(func) {
    // eslint-disable-next-line react-hooks/exhaustive-deps
    useEffect(func, []);
}
