import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import PopupButton from '../common/Popup/PopupButton';
import SimplePopup from '../common/Popup/SimplePopup';

const FieldLongTitlePopup = (props) => {
    return (
        <SimplePopup text={props.info} popupRef={props.popupRef}>
            <PopupButton iconName="check" text={props.morePopupButtonClose} onClick={() => props.popupRef.current?.close()} />
        </SimplePopup>
    );
};

FieldLongTitlePopup.propTypes = {
    popupRef: PropTypes.shape({ current: PropTypes.object }),
};
FieldLongTitlePopup.defaultProps = {
    popupRef: null,
};

const mapStateToProps = (state) => {
    return {
        morePopupButtonClose: state.messages.pageMessages.morePopupButtonClose,
    };
};

export default connect(mapStateToProps)(FieldLongTitlePopup);
