import React from 'react';

import styled from 'styled-components';

import { PromoButton } from 'components/no-auth/common/PromoButton/PromoButton';
import { PromoDangerouslyHtml } from 'components/no-auth/common/PromoDangerouslyHtml/PromoDangerouslyHtml';
import { PromoRow } from 'components/no-auth/common/PromoRow/PromoRow';
import { PromoText } from 'components/no-auth/common/PromoText/PromoText';

import promoMedia from 'theme/promoMedia';

const VacancyDetailsContainer = styled.div`
    padding: 32px 0;

    ${promoMedia.tablet744} {
        padding: 48px 0;
    }

    ${promoMedia.desktop1440} {
        padding: 64px 0;
    }
`;

const Description = styled(PromoRow)`
    ol,
    ul {
        all: revert;
    }
`;

const VacancyDetailsMain = (props) => {
    return (
        <VacancyDetailsContainer>
            {props.title && (
                <PromoRow indent="adaptive-12-16">
                    <PromoText tag="h2" variant="h2">
                        {props.title}
                    </PromoText>
                </PromoRow>
            )}
            {props.location && (
                <PromoRow indent="adaptive-24-32">
                    <PromoText tag="div" variant="h4">
                        {props.location}
                    </PromoText>
                </PromoRow>
            )}
            {props.description && (
                <Description indent="adaptive-32-48-64">
                    <PromoDangerouslyHtml dangerouslyHtml={props.description} tag="div" />
                </Description>
            )}
            {props.jobId && props.btn && (
                <PromoButton
                    target="_blank"
                    href={`https://boards.greenhouse.io/paysend/jobs/${props.jobId}`}
                    caption={props.btn}
                    type="link"
                    variant="primary"
                />
            )}
        </VacancyDetailsContainer>
    );
};

export default VacancyDetailsMain;
