import React from 'react';

import { DropdownCountryCurrency, DropdownCountryName, DropdownListItem, DropdownListItemContent } from './styles';
import { FlagIcon } from '../../common/FlagIcon/FlagIcon';

const CountryItem = (props) => {
    const country = props.country;
    const prefix = props.prefix;

    const currency = (currencies) => {
        return currencies.map((currency) => {
            return (
                <DropdownCountryCurrency variant="subheader" color="gray40" ellipsis key={currency.id}>
                    {currency.code}
                </DropdownCountryCurrency>
            );
        });
    };

    return (
        // нужен ли тут id?
        <DropdownListItem id={`${prefix}-${country.code}`} onClick={(e) => props.onClickHandler(e, country, country.currencies[0])}>
            <DropdownListItemContent>
                <FlagIcon name={country.code} />
                <DropdownCountryName variant="subheader" color="gray80">
                    {country.name}
                </DropdownCountryName>
            </DropdownListItemContent>
            {props.showCurrencies && <>{currency(country.currencies)}</>}
        </DropdownListItem>
    );
};

export default CountryItem;
