import React from 'react';

import PropTypes from 'prop-types';

import { Icon } from '../../../../common/Icon/Icon';
import { Panel } from '../../../../common/Panel/Panel';
import { PanelContent, PanelFrame, PanelRow, PanelText } from '../../../../common/Panel/styles';
import { Text } from '../../../../common/Text/Text';

export const PanelWithCheck = (props) => {
    const isSelected = props.isSelected || (props.id && props.id === props.selectedPanelId);
    return (
        <Panel onClick={props.onClick} isHoverable negativeMargin isSelectedPanel={isSelected}>
            <PanelFrame>
                <PanelContent>
                    <PanelRow>
                        <PanelText multiline>{props.mainText}</PanelText>
                    </PanelRow>
                    <PanelRow>
                        <Text color="mediumGray" size="secondary">
                            {props.secondaryText}
                        </Text>
                    </PanelRow>
                </PanelContent>
                {isSelected && <Icon name="check" color="purple" leftIndent="8px" />}
            </PanelFrame>
        </Panel>
    );
};

PanelWithCheck.propTypes = {
    id: PropTypes.string,
    onClick: PropTypes.func,
    mainText: PropTypes.string,
    secondaryText: PropTypes.string,
    selectedPanelId: PropTypes.string,
    isSelected: PropTypes.bool,
};
