import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { REGISTRATION } from '../../../constants/UrlConstants';
import { Button } from '../../common/Button/Button';
import { DiskBadge } from '../../common/DiskBadge/DiskBadge';
import { FixedBottomPanel } from '../../common/Layout/FixedBottomPanel/FixedBottomPanel';
import { Row } from '../../common/Layout/Row';
import { Separator } from '../../common/Layout/Separator';
import { Text } from '../../common/Text/Text';

const ReqAndMoneyInfo = (props) => {
    const { extra } = props;
    const { avatar, country_code, headerText, currencyCode, comment, formatedAmount, promo_text, continueWeb, downloadLink, getApp, fromFi } = extra;
    const firstLetter = extra.fromName[0];

    return (
        <>
            <Row indent="48px">
                <DiskBadge src={avatar} diskText={!avatar ? firstLetter : null} diskSize="large" flagName={country_code} />
            </Row>
            <Text variant="div" color="darkGray">
                {`${headerText} ${fromFi}`}
            </Text>
            <Text size="display1" color="darkGray" ellipsis>
                {`${formatedAmount} `}
                <Text size="display1" smallCaps color="darkGray">
                    {currencyCode.toLowerCase()}
                </Text>
            </Text>
            <Text variant="div" color="lightGray" indent="4px">
                {comment}
            </Text>
            <Separator />
            <Text variant="div" color="darkGray">
                {promo_text}
            </Text>
            <Button type="link" variant="row" caption={continueWeb} href={REGISTRATION} negativeMargin />
            <FixedBottomPanel isRightAligned>
                <Button type="link" raised={true} href={downloadLink} caption={getApp} target="_blank" />
            </FixedBottomPanel>
        </>
    );
};
const mapStateToProps = (state) => {
    return {
        extra: state.extra.strapiApi,
    };
};
ReqAndMoneyInfo.propTypes = {
    extra: PropTypes.object.isRequired,
    renderOptions: PropTypes.string.isRequired,
};

export default connect(mapStateToProps)(ReqAndMoneyInfo);
