import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { cleverTapEvent } from 'analytics/cleverTap';

import { SEARCH_NO_COUNTRY_FROM, SEARCH_NO_COUNTRY_TO } from 'constants/CleverTapEvents';
import { AMOUNT, COUNTRY, LANGUAGE, WHAT_WAS_TYPED } from 'constants/CleverTapParamNames';

import { countryPropType } from 'helpers/customPropTypes';
import { toLowerCase } from 'helpers/utils';

import { usePageMessages } from 'hooks/selectors';
import { useMatchMedia } from 'hooks/useMatchMedia';
import promoBreakpoints from 'theme/promoBreakpoints';

import CountryItem from './CountryItem';
import CurrencyList from './CurrencyList';
import { CountryListDropdown, DropdownList, DropdownListHeader, DropdownListInner, DropdownNotFound, SearchInput, SearchInputWrapper } from './styles';
import { DiskButton } from '../../common/DiskButton/DiskButton';
import { PromoText } from '../../no-auth/common/PromoText/PromoText';

const CountryList = (props) => {
    const isDesktop = useMatchMedia(promoBreakpoints.desktop1440);
    const DEFAULT_COUNTRY_HEIGHT = isDesktop ? 68 : 56;
    const NO_RESULT_HEIGHT = 70;
    const COUNT_ITEM_FOR_RESIZE = 5;
    const DEFAULT_SCROLL_HEIGHT = DEFAULT_COUNTRY_HEIGHT * COUNT_ITEM_FOR_RESIZE;

    const { gotIt, notificationText, favoriteCurrenciesCaption, allCurrenciesCaption, selectFromCountry, selectToCountry, searchNoResults, selectCurrency } =
        usePageMessages();
    const inputRef = useRef(null);

    const [countriesState, setCountriesState] = useState({ countryList: props.countryList, showFavorite: true, searchValue: '' });
    const [scrollHeight, setScrollHeight] = useState(DEFAULT_SCROLL_HEIGHT);

    useEffect(() => {
        if (inputRef.current) {
            inputRef.current.focus();
        }

        return () => {
            sendClevertapEvent();
        };
    }, []);

    const sendClevertapEvent = () => {
        if (props.countryMenu && countriesState.countryList && countriesState.countryList.length === 0) {
            const { countryFrom, countryTo } = props.countryMenu;
            const lang = props.lang;
            const amountField = props.amountField;

            if (props.isFrom && amountField) {
                cleverTapEvent(SEARCH_NO_COUNTRY_FROM, {
                    [WHAT_WAS_TYPED]: countriesState.searchValue,
                    [COUNTRY]: countryFrom.code,
                    [AMOUNT]: amountField.value,
                    [LANGUAGE]: lang,
                });
            } else {
                cleverTapEvent(SEARCH_NO_COUNTRY_TO, {
                    [WHAT_WAS_TYPED]: countriesState.searchValue,
                    [COUNTRY]: countryTo.code,
                    [AMOUNT]: amountField.value,
                    [LANGUAGE]: lang,
                });
            }
        }
    };

    const favoriteCountry = (countries, prefix) => {
        return (
            <>
                <DropdownListHeader>{favoriteCurrenciesCaption}</DropdownListHeader>
                {countries
                    .filter((c) => c.favorite)
                    .map((c) => (
                        <CountryItem
                            key={`f-${c.code}`}
                            country={c}
                            prefix={prefix}
                            gotIt={gotIt}
                            notificationText={notificationText}
                            showCurrencies={props.showCurrencies}
                            onClickHandler={props.handlerSelectCountry}
                        />
                    ))}
                <DropdownListHeader>{allCurrenciesCaption}</DropdownListHeader>
            </>
        );
    };

    const search = (e) => {
        const term = e.target.value;
        const showFavorite = term === '' && !countriesState.showFavorite;
        const resultList = props.countryList.filter((country) => {
            const byNameOrSynonym =
                term === undefined || toLowerCase(country.name).indexOf(toLowerCase(term)) >= 0 || toLowerCase(country.synonym).indexOf(toLowerCase(term)) >= 0;

            const byCurrencyCode =
                Array.isArray(country.currencies) &&
                country.currencies.find((currency) => {
                    return toLowerCase(currency.code).indexOf(toLowerCase(term)) >= 0;
                });

            return byNameOrSynonym || byCurrencyCode;
        });

        const getScrollHeight = () => {
            if (resultList.length <= COUNT_ITEM_FOR_RESIZE && resultList.length !== 0) {
                return DEFAULT_COUNTRY_HEIGHT * resultList.length;
            } else {
                return resultList.length === 0 ? NO_RESULT_HEIGHT : DEFAULT_SCROLL_HEIGHT;
            }
        };

        setScrollHeight(getScrollHeight);

        setCountriesState({
            countryList: resultList,
            showFavorite: showFavorite,
            searchValue: term,
        });
    };

    const hasFavorite = countriesState.countryList.find((country) => country.favorite);
    const isFrom = props.isFrom;
    const prefix = isFrom ? 'from' : 'to';
    const showCountryList = props.showCountryList;
    const selectedCountry = props.selectedCountry;
    const searchNoResultsFound = Array.isArray(countriesState.countryList) && !countriesState.countryList.length;

    return (
        <CountryListDropdown>
            <SearchInputWrapper htmlFor={`field_${prefix}`}>
                {showCountryList ? (
                    <SearchInput
                        placeholder={isFrom ? selectFromCountry : selectToCountry}
                        autoComplete="off"
                        tabIndex="3"
                        type="search"
                        ref={inputRef}
                        id={`field_${prefix}`}
                        onChange={search}
                    />
                ) : (
                    <>
                        <SearchInput readOnly={true} defaultValue={selectedCountry.name} type="search" />
                        <DiskButton
                            onClick={(e) => props.handlerCloseButtonCurrencyCountry(e, selectedCountry)}
                            iconName="cross"
                            type="button"
                            raised={false}
                            marginRight="8px"
                        />
                    </>
                )}
            </SearchInputWrapper>

            {showCountryList ? (
                <DropdownList scrollHeight={scrollHeight} tabIndex="0">
                    <DropdownListInner>
                        {hasFavorite && countriesState.showFavorite && favoriteCountry(countriesState.countryList, prefix)}
                        {countriesState.countryList.map((country) => (
                            <CountryItem
                                key={country.code}
                                country={country}
                                prefix={prefix}
                                gotIt={gotIt}
                                notificationText={notificationText}
                                showCurrencies={props.showCurrencies}
                                onClickHandler={props.handlerSelectCountry}
                            />
                        ))}
                        {searchNoResultsFound && (
                            <DropdownNotFound>
                                <PromoText tag="div" variant="subheader" color="gray40" ellipsis>
                                    {searchNoResults}
                                </PromoText>
                            </DropdownNotFound>
                        )}
                    </DropdownListInner>
                </DropdownList>
            ) : (
                <CurrencyList header={selectCurrency} country={selectedCountry} onClickHandler={props.handlerSelectCountry} />
            )}
        </CountryListDropdown>
    );
};

CountryList.defaultProps = {
    showCurrencies: true,
};

CountryList.propTypes = {
    isFrom: PropTypes.bool.isRequired,
    countryList: PropTypes.arrayOf(countryPropType),
    handlerSelectCountry: PropTypes.func.isRequired,
};
const mapStateToProps = ({ language, calculator }) => ({
    lang: language.lang,
    countryMenu: calculator.countryMenu,
});

export default connect(mapStateToProps)(CountryList);
