import React from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';

import { GlobalStyle } from './globalStyles';
import { AUTH_SEND_MAIN_LINK, LOGIN, REGISTRATION } from '../../constants/UrlConstants';
import NotFound from '../NotFound';

const App = (props) => {
    const authUser = props.authUser;
    const redirectTo = props.redirectTo;

    return (
        <>
            <GlobalStyle />
            <Routes>
                {props.routes.map(({ path, exact, component: Component, stateOptions, renderOptions, ...rest }) => {
                    const isRedirect =
                        (authUser && (path === LOGIN || path === REGISTRATION)) || (!authUser && rest.isAuthZone) || (redirectTo && redirectTo !== path);
                    const redirectUrl = redirectTo ? redirectTo : isRedirect && authUser ? AUTH_SEND_MAIN_LINK : LOGIN;
                    return (
                        <Route
                            key={path}
                            path={path}
                            exact={exact}
                            element={
                                isRedirect ? (
                                    <Navigate to={redirectUrl} />
                                ) : (
                                    <Component reloadData={isRedirect} renderOptions={renderOptions} stateOptions={stateOptions} data={props.data} />
                                )
                            }
                        />
                    );
                })}
                <Route path="*" element={<NotFound />} />
            </Routes>
        </>
    );
};
App.defaultProps = {
    routes: [],
};
export default App;
