import React, { useEffect, useState } from 'react';

import PropTypes from 'prop-types';

import { resolveArguments } from '../../helpers/utils';
import { useExtra, usePageMessages } from '../../hooks/selectors';
import { DangerouslyHtml } from '../common/DangerouslyHtml/DangerouslyHtml';
import PopupButton from '../common/Popup/PopupButton';
import SimplePopup from '../common/Popup/SimplePopup';

const CountryOfResidencePopup = ({ selectedCountry, loginCountryCode, residenceCountryId, popupRef, closeDropbox }) => {
    const messages = usePageMessages();
    const { countries } = useExtra();
    const [loginCountryId, setLoginCountryId] = useState();
    const resCountryId = parseInt(residenceCountryId);

    useEffect(() => {
        countries.forEach((country) => {
            if (country.code === loginCountryCode) {
                setLoginCountryId(country.id.toString());
            }
        });
    }, [countries]);

    const residenceLocalizedCountry = `specialResidentCountry${resCountryId}`;
    const loginLocalizedCountry = `specialResidentCountry${loginCountryId}`;
    const popupResidenceCountry = messages[residenceLocalizedCountry];
    const popupLoginCountry = messages[loginLocalizedCountry];

    const getText = (homeland, otherCountry) => {
        let text;
        let args;
        if (selectedCountry === loginCountryId) {
            text = homeland;
            args = popupLoginCountry;
        } else {
            text = otherCountry;
            args = [popupResidenceCountry, popupLoginCountry];
        }
        return <DangerouslyHtml dangerouslyHtml={resolveArguments(text, args)} />;
    };

    const onClick = () => {
        if (popupRef?.current) {
            popupRef.current?.close();
        }
        closeDropbox();
    };

    return (
        <SimplePopup text={getText(messages.homelandPopup, messages.otherCountryPopup)} popupRef={popupRef}>
            <PopupButton iconName="check" text={messages.gotIt} onClick={onClick} />
        </SimplePopup>
    );
};

export default CountryOfResidencePopup;

CountryOfResidencePopup.propTypes = {
    onClick: PropTypes.func,
    popupRef: PropTypes.shape({ current: PropTypes.object }),
};
CountryOfResidencePopup.defaultProps = {
    popupRef: null,
};
