import React from 'react';

import Checkbox from 'components/form/Checkbox';
import CountryDropBox from 'components/form/CountryDropBox';
import InputAccount from 'components/form/InputAccount';
import InputAutoCompleteAddress from 'components/form/InputAutoCompleteAddress';
import InputBik from 'components/form/InputBik';
import InputDate from 'components/form/InputDate';
import InputDropBox from 'components/form/InputDropBox';
import InputTextArea from 'components/form/InputTextArea';
import InputText from 'components/form-multi/InputText';

import {
    ATTACHMENT,
    AUTOCOMPLETE_ADDRESS,
    AUTOCOMPLETE_POST_CODE,
    BANK_ACCOUNT,
    BIK,
    CHECK_BOX,
    COUNTRY,
    CVV,
    DATE,
    DROP_BOX,
    EXP_DATE,
    FILE,
    HIDDEN,
    PAN,
    PHONE,
    PHONE_COUNTRY,
    PHONE_STRICT,
    SBP_DROP_BOX,
    TEXTAREA,
    UPLOAD_DOC,
} from 'constants/FieldType';
import { USA } from 'constants/WellKnownCountries';

import InputAutoCompletePostCode from './InputAutoCompletePostCode';
import InputHidden from './InputHidden';
import InputPhoneWithDropdown from './InputPhoneWithDropdown';
import UploadDocImg from './UploadDocImg';
import UploadFile from '../common/UploadFile/UploadFile';

const InputAutoDetect = (props) => {
    const detectFieldType = (field, type, country = {}) => {
        if (type === PHONE || type === PHONE_STRICT) {
            return (
                <InputPhoneWithDropdown
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    defaultCountry={props.defaultCountry}
                    iso2={props.defaultCountry}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                    onChangeHandler={props.onChangeHandler}
                    onBlurHandler={props.onBlurHandler}
                    isPhoneTypeStrict={type === PHONE_STRICT}
                    isNonEditableCountry
                />
            );
        } else if (type === TEXTAREA) {
            return (
                <InputTextArea
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                    isNewInput
                />
            );
        } else if (type === PAN || type === CVV || type === EXP_DATE) {
            return false;
        } else if (type === CHECK_BOX) {
            return (
                <Checkbox
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                />
            );
        } else if (type === COUNTRY && field.isReadOnly) {
            return (
                <CountryDropBox
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                />
            );
        } else if (type === COUNTRY) {
            return (
                <CountryDropBox
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                    customValue={props.customValue}
                />
            );
        } else if (type === DATE) {
            return (
                <InputDate
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    usaDateFormat={country.id === USA}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                    isNewInput
                />
            );
        } else if (type === BIK) {
            return (
                <InputBik
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                    isClearErrorOnFocus={props.isClearErrorOnFocus}
                    onBlurHandler={props.onBlurHandler}
                    isNewInput
                />
            );
        } else if (type === BANK_ACCOUNT) {
            return (
                <InputAccount
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                    onChangeHandler={props.onChangeHandler}
                    onBlurHandler={props.onBlurHandler}
                    isNewInput
                    isFormattedInput
                />
            );
        } else if (type === DROP_BOX || type === SBP_DROP_BOX) {
            return (
                <InputDropBox
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                    selectHandler={props.selectHandler}
                />
            );
        } else if (type === PHONE_COUNTRY) {
            return (
                <CountryDropBox
                    readOnly={true}
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                />
            );
        } else if (type === AUTOCOMPLETE_ADDRESS) {
            return (
                <InputAutoCompleteAddress
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    countryCode={country.code}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                    disabledAddressInput={props.disabledAddressInput}
                />
            );
        } else if (type === AUTOCOMPLETE_POST_CODE) {
            return (
                <InputAutoCompletePostCode
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    isClearErrorOnFocus={props.isClearErrorOnFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                />
            );
        } else if (type === UPLOAD_DOC || type === ATTACHMENT) {
            return (
                <UploadDocImg
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                />
            );
        } else if (type === FILE) {
            return (
                <UploadFile
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                />
            );
        } else if (type === HIDDEN) {
            return (
                <InputHidden
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    isClearErrorOnFocus={props.isClearErrorOnFocus}
                    onChange={props.onChange}
                    onBlur={props.onBlur}
                />
            );
        } else {
            return (
                <InputText
                    field={field}
                    hintButtonClick={props.hintButtonClick}
                    setMaxLength={props.setMaxLength}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={props.onFocus}
                    isClearErrorOnFocus={true}
                    onChange={props.onChange}
                    onChangeHandler={props.onChangeHandler}
                    onBlur={props.onBlur}
                    onBlurHandler={props.onBlurHandler}
                    isNewInput
                />
            );
        }
    };

    const field = props.field;

    return detectFieldType(field, field.type, props.country);
};

InputAutoDetect.defaultProps = {
    usaDateFormat: false,
};

export default InputAutoDetect;
