import React from 'react';

import PropTypes from 'prop-types';

import { filterXSS } from 'xss';

import { Container, RowButton, SubTitle, Title } from './styles';
import { XSS_FILTER_OPTIONS } from '../../../../constants/Constants';

export const HeaderPanel = (props) => {
    return (
        <Container indentCompact={props.indentCompact}>
            <Title isCentered={props.isCentered}>{props.title}</Title>
            {props.subtitle && <SubTitle dangerouslySetInnerHTML={{ __html: filterXSS(props.subtitle, XSS_FILTER_OPTIONS) }} />}
            {!props.isGlobalFlow && props.buttonRowCaption && (
                <RowButton
                    type={props.buttonRowType}
                    variant="row"
                    size={props.buttonRowSizeFit ? 'fit' : 'tight'}
                    negativeMargin
                    caption={props.buttonRowCaption}
                    onClick={props.buttonRowOnClick}
                    href={props.buttonRowHref}
                    addTopMargin={props.addTopMargin}
                />
            )}
        </Container>
    );
};
HeaderPanel.defaultProps = {
    isGlobalFlow: false,
};

HeaderPanel.propTypes = {
    isGlobalFlow: PropTypes.bool,
    indentCompact: PropTypes.bool, // отступ до контента 32 на мобильных (когда после заголовка идет текст)
    isCentered: PropTypes.bool,
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    subtitle: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    buttonRowType: PropTypes.string,
    buttonRowCaption: PropTypes.string,
    buttonRowOnClick: PropTypes.func,
    buttonRowHref: PropTypes.string,
    addTopMargin: PropTypes.bool,
    buttonRowSizeFit: PropTypes.bool,
};
