import React from 'react';
import { connect } from 'react-redux';

import { InfoPage } from 'components/common/Layout/InfoPage/InfoPage';
import BasePage from 'components/utils/BasePage';

import { AUTH_SEND_MAIN_LINK } from 'constants/UrlConstants';

import { getLangCountryPrefix } from 'helpers/utils';

import BaseLayoutPromo from '../common/BaseLayoutPromo';

const EmailConfirm = (props) => {
    return (
        <BaseLayoutPromo>
            <InfoPage
                iconName={props.confirm ? 'check-rounded-thin' : 'cross-rounded-thin'}
                iconColor={props.confirm ? 'purple' : 'red'}
                headerText={props.confirm ? props.emailConfirmSuccessText : props.emailNotConfirmText}
                primaryButtonType="link"
                primaryButtonCaption={props.emailConfirmHomePageButton}
                primaryButtonLink={props.authUser ? AUTH_SEND_MAIN_LINK : getLangCountryPrefix(props.lang, props.country)}
            />
        </BaseLayoutPromo>
    );
};
const mapStateToProps = (state) => {
    return {
        emailConfirmSuccessText: state.messages.pageMessages.emailConfirmSuccessText,
        emailConfirmHomePageButton: state.messages.pageMessages.emailConfirmHomePageButton,
        emailNotConfirmText: state.messages.pageMessages.emailNotConfirmText,
        confirm: state.extra.confirm,
        authUser: state.auth.authUser,
        lang: state.language.lang,
        country: state.app.country,
    };
};
export default connect(mapStateToProps)(BasePage(EmailConfirm));
