import React, { Component } from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import { PromoRow } from 'components/no-auth/common/PromoRow/PromoRow';

import { URL_IMAGES_I } from 'constants/UrlConstants';

import promoMedia from 'theme/promoMedia';

const TrustpilotWidget = styled.div`
    position: relative;
    margin-bottom: 32px;
    height: 300px;

    ${promoMedia.tablet744} {
        margin-bottom: 48px;
    }

    ${promoMedia.desktop1440} {
        margin-bottom: 64px;
        height: 140px;
    }

    .trustpilot-widget {
        height: 100%;
    }
`;

export default class TrustpilotSection extends Component {
    componentDidMount() {
        const script = document.createElement('script');
        script.src = '//widget.trustpilot.com/bootstrap/v5/tp.widget.bootstrap.min.js';
        script.defer = true;

        document.body.appendChild(script);
    }

    render() {
        if (__isDev__) {
            return false;
        } else {
            return (
                <>
                    <TrustpilotWidget>
                        <div
                            data-schema-type="Organization"
                            data-stars="5"
                            data-theme="light"
                            data-style-width="100%"
                            data-style-height="100%"
                            data-businessunit-id="59295ddd0000ff0005a34b26"
                            data-template-id="53aa8912dec7e10d38f59f36"
                            data-tags={this.props.lang}
                            data-locale={this.props.lang}
                            className="trustpilot-widget"
                        >
                            <iframe
                                frameBorder="0"
                                scrolling="no"
                                title="Customer reviews powered by Trustpilot"
                                loading="lazy"
                                src="https://widget.trustpilot.com/trustboxes/53aa8912dec7e10d38f59f36/index.html?businessunitId=59295ddd0000ff0005a34b26&amp;templateId=53aa8912dec7e10d38f59f36#schemaType=Organization&amp;stars=5&amp;theme=light&amp;styleWidth=100%25&amp;styleHeight=250px&amp;tags=ru&amp;locale=ru"
                            />
                        </div>
                    </TrustpilotWidget>
                    <PromoRow textAlign="center" indent="adaptive-32-48-64">
                        <img src={`${URL_IMAGES_I}/ft-logo.svg`} alt="FT logo" width="274" height="72" />
                    </PromoRow>
                </>
            );
        }
    }
}

TrustpilotSection.propTypes = {
    lang: PropTypes.string.isRequired,
};
