import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';

import InputText from 'components/form-multi/InputText';

import { changeFormField, changeFormFieldValue } from 'actions/formAction';

import update from 'helpers/Immutability';

import CountryDropBoxList from './CountryDropBoxList';
import ModalComponent from '../utils/ModalComponent';

const CountryDropBox = (props) => {
    const [customField, setField] = useState({ ...props.field });
    const [isOpenedDropbox, toggleDropbox] = useState(false);

    const REMOVE_POST_CODE_FOR_COUNTRY = [784];

    useEffect(() => {
        if (props.field.value && props.country_list) {
            props.country_list.forEach((item) => {
                if (item.id === parseInt(props.field.value)) {
                    setField(update(customField, { value: { $set: item.name } }));
                }
            });
        }
    }, []);

    const selectItem = (e, item) => {
        const hideIndex = REMOVE_POST_CODE_FOR_COUNTRY.find(function (element) {
            return parseInt(item.id) === element;
        });
        if (props.fieldPostCode && hideIndex) {
            props.changeFormField(
                props.formName,
                update(props.fieldPostCode, {
                    mandatory: { $set: false },
                    visible: { $set: false },
                    value: { $set: '   ' },
                }),
            );
        } else if (props.fieldPostCode && !props.fieldPostCode.visible) {
            props.changeFormField(
                props.formName,
                update(props.fieldPostCode, {
                    mandatory: { $set: true },
                    visible: { $set: true },
                    value: { $set: '' },
                }),
            );
        }
        props.changeFormFieldValue(props.formName, item.id, props.field.name);
        setField(update(props.field, { value: { $set: item.name } }));
        closeDropbox();
    };

    const showDropbox = () => {
        toggleDropbox(true);
    };

    const closeDropbox = () => {
        toggleDropbox(false);
    };

    return (
        <>
            <InputText
                isReadOnly={props.readOnly}
                onClick={showDropbox}
                field={customField}
                customValue={props.customValue}
                formName={props.formName}
                autoFocus={props.focus}
                onFocus={props.onFocus}
                onChange={props.onChange}
                validateOnBlur={false}
                onBlur={props.onBlur}
                isNewInput
                isPseudoSelect
            />
            {isOpenedDropbox && (
                <ModalComponent canCloseModal={false}>
                    <CountryDropBoxList
                        selectItem={selectItem}
                        searchPlaceholder={props.searchPlaceholder}
                        searchDropBoxEmpty={props.searchDropBoxEmpty}
                        items={props.field.strapiField ? props.field.items : props.country_list}
                        isStrapiField={props.field.strapiField}
                        id={props.field.id}
                        name={props.field.name}
                        onCloseButtonClick={closeDropbox}
                    />
                </ModalComponent>
            )}
        </>
    );
};
const mapStateToProps = (state, ownProps) => {
    return {
        fieldPostCode: state.form[ownProps.formName].fields['post_code'],
        searchPlaceholder: state.messages.jsMessages['site.field.search.placeholder'],
        searchDropBoxEmpty: state.messages.jsMessages['site.search.dropdox.empty'],
        country_list: state.extra.country_list,
    };
};
export default connect(mapStateToProps, {
    changeFormFieldValue,
    changeFormField,
})(CountryDropBox);
