import React, { useEffect, useMemo, useRef, useState } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { getLang, isDaDataAPI, serializeItem } from 'components/form-multi/utils';

import { resolveArguments } from 'helpers/utils';

import { SearchBar } from './components/SearchBar/SearchBar';
import { PanelWithCheck } from '../auth/common/components/PanelWithCheck/PanelWithCheck';
import { EmptyPage } from '../common/Layout/EmptyPage/EmptyPage';
import { Row } from '../common/Layout/Row';

const DADATA_URL = `/api/dadata/suggestions`;
const GOOGLE_MAP_PREDICTIONS = `/api/google-maps/predictions`;

function useDebounce(value, delay) {
    const [debouncedValue, setDebouncedValue] = useState(value);

    useEffect(() => {
        const handler = setTimeout(() => {
            setDebouncedValue(value);
        }, delay);

        return () => {
            clearTimeout(handler);
        };
    }, [value]);

    return debouncedValue;
}

const fetchDaData = ({ query, language, country }) => {
    const body = {
        query,
        language,
        from_bound: { value: 'street' },
        to_bound: { value: 'house' },
        restrict_value: false,
        locations: [
            {
                country_iso_code: country,
            },
        ],
    };

    const bodyInParams = new URLSearchParams();
    bodyInParams.append('data', JSON.stringify(body));

    return fetch(DADATA_URL, {
        method: 'POST',
        mode: 'cors',
        credentials: 'include',
        headers: { Accept: 'application/json' },
        body: bodyInParams,
    })
        .then((response) => response.json())
        .catch((error) => console.warn('error', error));
};

const fetchGooglePredictions = (countryCode, lang, term) => {
    const url = `${GOOGLE_MAP_PREDICTIONS}?requestLang=${lang}&country=${countryCode}&query=${term}`;
    try {
        return fetch(url, {
            method: 'POST',
            mode: 'cors',
            credentials: 'include',
            headers: { Accept: 'application/json' },
            body: {},
        })
            .then((response) => {
                return response.json().then((res) => res);
            })
            .catch((error) => {
                return console.warn('error', error);
            });
    } catch (e) {
        console.log(e);
    }
};

function googleApiSearch(searchRef, props, setItems) {
    const setPredictions = (predictions) => {
        if (predictions !== null && !!predictions.length) {
            setItems(predictions);
            props.setItems(predictions);
        } else {
            setItems([]);
            props.setItems([]);
        }
    };

    const searchWithApi = (term) => {
        const countryCode = props.countryCode ? props.countryCode : 'ca';

        if (term.length > 3) {
            const lang = getLang(countryCode, props.lang)?.trim().toLowerCase();
            fetchGooglePredictions(countryCode, lang, term).then((list) => {
                setPredictions(list);
            });
        } else if (term.length === 0) {
            setItems([]);
            props.setItems([]);
        }
    };

    const selectItem = (e, item) => {
        const { mainText } = serializeItem(props.countryCode, item);
        searchRef.current.value = mainText.trim();
        props.selectItem(e, item);
    };

    return [selectItem, searchWithApi];
}

function daDataApiSearch(searchRef, props, setItems) {
    const searchWithApi = (query) => {
        const countryCode = props.countryCode ? props.countryCode : 'ca';

        if (query) {
            fetchDaData({
                query,
                language: getLang(countryCode, props.lang)?.trim().toLowerCase(),
                country: props.countryCode.trim().toUpperCase(),
            }).then((json) => {
                if (json && json['suggestions']) {
                    const places = json['suggestions'];
                    setItems(places);
                    props.setItems(places);
                } else {
                    setItems([]);
                    props.setItems([]);
                }
            });
        }
    };

    const selectItem = (e, item) => {
        const { mainText } = serializeItem(props.countryCode, item);
        const { fieldPostCode, fieldCity } = props;
        searchRef.current.value = mainText.trim();
        props.selectItem(e, item, fieldPostCode, fieldCity);
    };

    return [selectItem, searchWithApi];
}

function useApiSearch(searchRef, props, setItems) {
    const { fieldPostCode, fieldCity } = props;
    const { isError: isErrFieldPostCode } = fieldPostCode;
    const { isError: isErrFieldCity } = fieldCity;

    const isDefault = useMemo(() => !isDaDataAPI(props.countryCode), [props.countryCode]);

    return useMemo(() => {
        return isDefault ? googleApiSearch(searchRef, props, setItems) : daDataApiSearch(searchRef, props, setItems);
    }, [isDefault, isErrFieldPostCode, isErrFieldCity, props.countryCode]);
}

function useDelayForSearch(searchFunction, query, delay = 300) {
    const debouncedQuery = useDebounce(query, delay);

    useEffect(() => {
        if (debouncedQuery) {
            searchFunction(debouncedQuery);
        }
    }, [debouncedQuery]);
}

const InputAutoCompleteAddressList = (props) => {
    const searchRef = useRef(null);

    const [itemsList, setItems] = useState(props.items);

    const [query, setQuery] = useState(props.field.value);
    const [selectItem, searchWithApi] = useApiSearch(searchRef, props, setItems);

    useDelayForSearch(searchWithApi, query, 500);

    const onChange = (e) => {
        e.preventDefault();
        setQuery(e.target.value);
    };

    const setCurrentValue = (e) => {
        e.persist();
        props.selectInputClient(e, query.trim());
    };

    const selectNewItem = (e, item) => {
        e.persist();
        selectItem(e, item);
    };

    const notFoundBlock = (addressText) => {
        const text = itemsList.length > 0 ? props.textInfoNotFound : props.textNotFound;
        return resolveArguments(text, addressText);
    };

    return (
        <>
            <SearchBar
                placeholder={props.searchPlaceholder}
                onChange={onChange}
                id={`search_field_${props.field.name}_${props.field.id}`}
                onCloseButtonClick={props.onCloseButtonClick}
                searchRef={searchRef}
                value={query}
                field={props.field}
                formName={props.formName}
            />
            <Row indent="adaptive-32-48">
                {itemsList.length > 0 &&
                    itemsList.map((item, index) => {
                        const { id, mainText, secondaryText } = serializeItem(props.countryCode, item);
                        return (
                            <PanelWithCheck
                                key={`address_list_${index}`}
                                onClick={(e) => selectNewItem(e, item)}
                                mainText={mainText}
                                secondaryText={secondaryText}
                                id={id}
                                selectedPanelId={props.selectedAddress.id}
                            />
                        );
                    })}
            </Row>
            {(query || itemsList.length > 0) && (
                <EmptyPage
                    iconName={itemsList.length === 0 ? 'ghost' : ''}
                    emptyText={notFoundBlock(query)}
                    emptyButtonClick={setCurrentValue}
                    emptyButtonCaption={props.buttonNotFound}
                />
            )}
        </>
    );
};

InputAutoCompleteAddressList.propTypes = {
    countryCode: PropTypes.string.isRequired,
};

const mapStateToProps = ({ messages: { pageMessages }, form, language }, ownProps) => ({
    textNotFound: pageMessages.textNotFound,
    textInfoNotFound: pageMessages.textInfoNotFound,
    buttonNotFound: pageMessages.buttonNotFound,
    fieldPostCode: form[ownProps.formName].fields['post_code'],
    fieldCity: form[ownProps.formName].fields.city,
    lang: language.lang,
});

export default connect(mapStateToProps)(InputAutoCompleteAddressList);
