import { Link } from 'react-router-dom';

import styled, { css } from 'styled-components';

import colors from '../../../theme/colors';
import media from '../../../theme/media';

const getJustify = (props) => {
    switch (props.justify) {
        case 'start':
            return 'flex-start';
        case 'center':
            return 'center';
        case 'end':
            return 'flex-end';
        case 'between':
            return 'space-between';
        default:
            return 'start';
    }
};
const getPanelRowJustify = (props) => {
    if (props.alignStart) {
        return 'start';
    }
    return props.alignEnd ? 'end' : 'space-between';
};

const getTextColor = (props) => {
    if (props.purpletext && props.isDisabled) {
        return colors.superLightPurple;
    } else if (props.purpletext) {
        return colors.purple;
    } else {
        return colors.codGray;
    }
};

export const DiskButtonWrapper = styled.div`
    display: none;
    margin-left: 4px;
`;

const CommonCss = css`
    display: flex;
    justify-content: ${(props) => getJustify(props)};
    align-items: center;
    cursor: ${(props) => (props.$isDefaultCursor ? 'default' : 'pointer')};
    padding: ${(props) => (props.$isBordered || props.$hasIcon ? '24px' : '16px 24px')};
    border: ${(props) => (props.$isBordered ? `1px solid ${colors.greyRgba28}` : 'none')};
    border-radius: ${(props) => (props.$hasBorderRadius ? '12px' : '0')};
    margin: ${(props) => (props.$isNegativeMargin ? '0 -24px' : null)};
    width: ${(props) => (props.$isNegativeMargin ? 'calc(100% + 48px)' : '100%')};
    background: ${(props) => (props.$isSelectedPanel ? colors.purpleRgba08 : 'none')};
    box-shadow: none;
    color: inherit;
    box-sizing: border-box;
    pointer-events: ${(props) => (props.$isDisabled ? 'none' : null)};
    position: relative;
    /* to reset css on button */
    font-family: inherit;
    line-height: inherit;
    font-size: inherit;
    text-align: left;

    ${DiskButtonWrapper} {
        display: ${(props) => (props.$showDeleteIcon ? 'block' : 'none')};
    }

    ${media.tablet720} {
        &:hover {
            ${DiskButtonWrapper} {
                display: block;
            }
        }
    }

    ${media.desktop1920} {
        padding: ${(props) => (props.$hasIcon ? '36px 32px' : '20px 32px')};
        margin: ${(props) => (props.$isNegativeMargin ? '0 -32px' : null)};
        border-radius: ${(props) => (props.$hasBorderRadius ? '16px' : null)};
        width: ${(props) => (props.$isNegativeMargin ? 'calc(100% + 64px)' : '100%')};
    }

    &:hover,
    &:active {
        background: ${(props) => (props.$isHoverable ? colors.superLightGray : 'none')};
    }
`;

export const PanelContainerBlock = styled.div`
    ${CommonCss}
`;
export const PanelContainerLink = styled.a`
    ${CommonCss}
`;
export const PanelContainerReactLink = styled(Link)`
    ${CommonCss}
`;
export const PanelContainerButton = styled.button`
    ${CommonCss}
`;
export const PanelContainerButtonSubmit = styled.button.attrs({ type: 'submit' })`
    ${CommonCss}
`;

/* inside panel */
export const PanelText = styled.span`
    font-size: 17px;
    line-height: 24px;
    overflow: ${(props) => (props.multiline ? null : 'hidden')};
    text-overflow: ${(props) => (props.multiline ? null : 'ellipsis')};
    white-space: ${(props) => (props.multiline ? null : 'nowrap')};
    color: ${(props) => getTextColor(props)};

    ${media.desktop1920} {
        font-size: 20px;
        line-height: 32px;
    }
`;
export const PanelRow = styled.div`
    display: flex;
    align-items: center;
    flex-grow: 1;
    overflow: hidden;
    justify-content: ${(props) => getPanelRowJustify(props)};
`;
export const PanelFrame = styled.div`
    display: flex;
    align-items: center;
    width: 100%;

    ${PanelRow} {
        margin-bottom: 4px;

        &:last-of-type {
            margin-bottom: 0;
        }
    }
`;
export const IconWrapper = styled.span`
    display: inline-flex;
    margin-right: 16px;
    position: relative;

    ${media.desktop1920} {
        margin-right: 24px;
    }
`;
export const DiskBadgeWrapper = styled.span`
    display: inline-flex;
    margin-right: 16px;
    position: relative;

    ${media.desktop1920} {
        margin-right: 24px;
    }
`;
export const SelectedDiskBadge = styled.span`
    position: absolute;
    left: 0;
    top: 0;
`;
export const PanelContent = styled.div`
    width: 100%;
    overflow: hidden;
`;
export const PanelLeftBlock = styled.div`
    overflow: hidden;
`;
export const PanelRightBlock = styled.div`
    margin-left: 16px;
    text-align: right;
    white-space: nowrap;
    display: ${(props) => (props.flex ? 'flex' : null)};
    align-items: ${(props) => (props.flex ? 'center' : null)};

    ${media.desktop1920} {
        margin-left: 24px;
    }
`;
export const CardAccountRow = styled.span`
    display: flex;
    flex: 1;
    align-items: center;
    max-width: 100%;

    ${media.tablet937} {
        flex-direction: column;
        align-items: stretch;
    }

    ${media.desktop1224} {
        flex-direction: row;
    }

    ${media.desktop1296} {
        flex-direction: column;
    }
`;
export const CardAccountSubrow = styled.span`
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    ${media.tablet937} {
        margin-top: 24px;
    }

    ${media.desktop1224} {
        margin-top: 0;
    }

    ${media.desktop1296} {
        margin-top: 24px;
    }
`;
