import React, { useMemo, useState } from 'react';
import { connect } from 'react-redux';

import { clearErrorField, getLang, isDaDataAPI, serializeItem, validationAndUpdateValue } from 'components/form-multi/utils';

import { changeFormField, changeFormFieldValue, removeFieldError } from 'actions/formAction';

import InputAutoCompleteAddressList from './InputAutoCompleteAddressList';
import InputText from '../form-multi/InputText';
import ModalComponent from '../utils/ModalComponent';

const GOOGLE_MAPS_DETAILS = `/api/google-maps/details`;
const POSTAL_CODE = 'postal_code';

const findCurrentItem = (items, countryCode, value) => {
    if (items.length > 0) {
        const currentItemArray = items.filter((item) => serializeItem(countryCode, item).mainText === value);
        if (currentItemArray.length > 0) {
            return serializeItem(countryCode, currentItemArray[0]).mainText;
        }
    }

    return '';
};

const fetchDetails = (countryCode, lang, placeId) => {
    const url = `${GOOGLE_MAPS_DETAILS}?id=${placeId}&requestLang=${getLang(countryCode, lang)}`;
    return fetch(url, {
        method: 'POST',
        mode: 'cors',
        credentials: 'include',
        headers: { Accept: 'application/json' },
        body: {},
    })
        .then((response) => {
            return response.json().then((res) => res);
        })
        .catch((error) => {
            return console.warn('error', error);
        });
};

function googleApiSelect(props, countryCode, setSelectedAddress, closeMenu) {
    const setPlace = (place) => {
        if (place) {
            const postalElm = place.addressComponents.find((elm) => elm.types[0].toLowerCase() === POSTAL_CODE);
            if (!postalElm) {
                props.changeFormFieldValue(props.formName, '', props.fieldPostCode.name);
            }

            place.addressComponents.forEach((element) => {
                if (postalElm && element.types[0].toLowerCase() === POSTAL_CODE) {
                    props.changeFormFieldValue(props.formName, element.long_name, props.fieldPostCode.name);
                } else {
                    if (props.fieldPostCode.isError) {
                        clearErrorField(props.formName, props.fieldPostCode, props.changeFormField);
                    }
                    if (props.fieldPostCode.value) {
                        props.changeFormFieldValue(props.formName, '', props.fieldPostCode.name);
                    }
                }
            });
        }
    };

    const setPostalCode = (prediction) => {
        try {
            fetchDetails(countryCode, props.lang, prediction.placeId).then((place) => {
                setPlace(place);
            });
        } catch (e) {
            console.error(e);
        }
    };

    const selectItem = (e, prediction) => {
        if (props.fieldCity) {
            let cityName = '';
            if (prediction.structuredFormatting.secondaryText) {
                cityName = prediction.structuredFormatting.secondaryText.split(',')[0];
            }
            if (props.fieldCity.isError) {
                clearErrorField(props.formName, props.fieldCity, props.changeFormField);
            }
            props.changeFormFieldValue(props.formName, cityName.trim(), props.fieldCity.name);
        }

        if (props.fieldPostCode) {
            setPostalCode(prediction);
        }

        const value = serializeItem(countryCode, prediction).mainText.trim();
        validationAndUpdateValue(props.formName, value, props.field, props.changeFormField).then(() => {
            closeMenu();
            setSelectedAddress(value, prediction.placeId);
        });
    };

    return [selectItem];
}

function daDataApiSelect(props, countryCode, setSelectedAddress, closeMenu) {
    const setPostalCode = (postalCode, fieldPostCode) => {
        props.changeFormFieldValue(props.formName, postalCode || '', fieldPostCode.name);

        if (fieldPostCode.isError) {
            props.removeFieldError(props.formName, fieldPostCode.name);
        }
    };

    const selectItem = (e, item, fieldPostCode, fieldCity) => {
        if (props.fieldCity) {
            let cityName = '';

            const place = item['data']['city'] ? item['data']['city'] : item['data']['settlement_with_type'];
            if (place) {
                cityName = place;
            }

            if (fieldCity.isError) {
                props.removeFieldError(props.formName, fieldCity.name);
            }

            props.changeFormFieldValue(props.formName, cityName.trim(), props.fieldCity.name);
        }

        if (props.fieldPostCode) {
            const postalCode = item?.data?.postal_code;
            setPostalCode(postalCode, fieldPostCode);
        }

        const value = serializeItem(countryCode, item).mainText.trim();
        validationAndUpdateValue(props.formName, value, props.field, props.changeFormField).then(() => {
            closeMenu();
            setSelectedAddress(value, item?.data?.fias_id);
        });
    };

    return [selectItem];
}

function useApiSearch(props, countryCode, setSelectedAddress, closeMenu) {
    const isDefault = useMemo(() => !isDaDataAPI(countryCode), [countryCode]);
    return useMemo(
        () =>
            isDefault ? googleApiSelect(props, countryCode, setSelectedAddress, closeMenu) : daDataApiSelect(props, countryCode, setSelectedAddress, closeMenu),
        [isDefault],
    );
}

function useCountryCodeSetter(props) {
    if (!!props.country_list && !!props.country_list.length && props.residentCountry?.value) {
        const country = props.country_list.find((country) => country.id.toString() === props.residentCountry.value.toString());
        if (!!country && country.code) {
            return country.code;
        }
    } else if (!!props.allCountries && props.residentCountry?.value) {
        //Ukraine and Belarus case
        const country = props.allCountries[props.residentCountry.value.toString()];
        if (!!country && country.code) {
            return country.code;
        }
    }

    return props.countryCode;
}

const InputAutoCompleteAddress = (props) => {
    const [items, setItems] = useState([]);
    const [selectedAddress, setSelectedAddress] = useState({
        address: '',
        id: '',
    });

    const setSelectedAddressFunc = (address, id = '') => {
        setSelectedAddress({ address, id });
    };

    const countryCode = useCountryCodeSetter(props);

    const [isOpenedDropbox, toggleDropbox] = useState(false);
    const openDropbox = () => toggleDropbox(true);
    const closeDropbox = () => toggleDropbox(false);

    const [selectItem] = useApiSearch(props, countryCode, setSelectedAddressFunc, closeDropbox);

    const selectInputClient = (e, value) => {
        const isCurrentValueEqualItem = value.trim() === findCurrentItem(items, props.phoneCountry.code, value.trim());
        if (!isCurrentValueEqualItem) {
            if (props.fieldCity.value || props.fieldCity.isError) {
                const newField = { ...props.fieldCity, value: '', isError: false };
                props.changeFormField(props.formName, newField);
            }
            if (props.fieldPostCode.value || props.fieldPostCode.isError) {
                const newField = { ...props.fieldPostCode, value: '', isError: false };
                props.changeFormField(props.formName, newField);
            }
        }

        validationAndUpdateValue(props.formName, value, props.field, props.changeFormField).then(closeDropbox);
    };

    return (
        <>
            {!isOpenedDropbox ? (
                <InputText
                    onClick={openDropbox}
                    field={props.field}
                    formName={props.formName}
                    autoFocus={props.focus}
                    onFocus={openDropbox}
                    onChange={props.onChange}
                    isClearErrorOnFocus={true}
                    validateOnBlur={false}
                    onBlur={props.onBlur}
                    isNewInput
                    isReadOnly={props.disabledAddressInput}
                />
            ) : (
                <ModalComponent canCloseModal={false}>
                    <InputAutoCompleteAddressList
                        selectItem={selectItem}
                        selectInputClient={selectInputClient}
                        items={items}
                        setItems={setItems}
                        field={props.field}
                        countryCode={countryCode}
                        selectedAddress={selectedAddress}
                        setSelectedAddress={setSelectedAddressFunc}
                        searchPlaceholder={props.searchPlaceholder}
                        searchDropBoxEmpty={props.searchDropBoxEmpty}
                        formName={props.formName}
                        onCloseButtonClick={closeDropbox}
                    />
                </ModalComponent>
            )}
        </>
    );
};

const mapStateToProps = ({ form, language, messages, extra }, ownProps) => ({
    lang: language.lang,
    fieldCity: form[ownProps.formName].fields.city,
    fieldPostCode: form[ownProps.formName].fields['post_code'],
    residentCountry: form[ownProps.formName].fields['resident_country'],
    searchPlaceholder: messages.jsMessages['site.field.search.placeholder'],
    searchDropBoxEmpty: messages.jsMessages['site.search.dropdox.empty'],
    phoneCountry: extra.phoneCountry,
    country_list: extra.country_list,
    allCountries: extra.allCountries,
});

export default connect(mapStateToProps, {
    changeFormField,
    changeFormFieldValue,
    removeFieldError,
})(InputAutoCompleteAddress);
