export const getCaptchaURL = () => {
    return `/api/i/captcha.check?r=${Math.floor(Math.random() * 100000000000000000)}`;
};

export const isDaDataAPI = (countryCode) => countryCode.trim().toLowerCase() === 'ru';

export const getLang = (countryCode, defaultLang) => {
    return countryCode === 'ca' || countryCode === 'us' ? 'en' : defaultLang;
};

export const getSortFields = (fields) => {
    const fieldsArray = Array.isArray(fields) ? fields : Object.values(fields);
    fieldsArray.sort((a, b) => {
        if (a.order < b.order) {
            return -1;
        }
        if (b.order < a.order) {
            return 1;
        }
        return 0;
    });
    return fieldsArray.filter((field) => {
        const dependentFiledName = field?.showOnFieldSelectionName;
        if (dependentFiledName) {
            const dependentField = fieldsArray.find((f) => f.name === dependentFiledName);
            return dependentField?.value === field?.showOnFieldSelectionValue;
        }
        return true;
    });
};

export const validateUserNumber = (value, iso2) => {
    let newValue = value;
    const countryIso2 = countryPrefixes[iso2];
    const countryCode = countryPrefixes[iso2]?.[0];
    if (countryIso2) {
        countryIso2.forEach((regExp, i) => {
            if ((i === 1 || (i > 1 && __isProd__)) && regExp && value.search(regExp) === 0) {
                newValue = newValue.replace(regExp, countryCode);
            }
        });
    }
    return newValue;
};

export const validationAndUpdateValue = (formName, value, field, changeFormField, binInfoError) => {
    let isError = false;
    let trimValue = typeof value === 'string' || value instanceof String ? value.trim() : value;
    if (field?.type === PAN && (binInfoError || (field.errorCode && field.errorCode !== 0))) {
        isError = true;
    } else if (field.mandatory === true && !field.checkRegexp) {
        isError = !trimValue;
    } else if (field.mandatory === true) {
        const valid = new RegExp(field.checkRegexp);
        isError = !valid.test(trimValue);
    } else if (field.mandatory === false && field.checkRegexp && trimValue) {
        const valid = new RegExp(field.checkRegexp);
        isError = !valid.test(trimValue);
    }

    let newField = update(field, {
        value: { $set: value },
        isError: { $set: isError },
    });

    if (changeFormField) {
        changeFormField(formName, newField);
    }
    return Promise.resolve(newField);
};

export const notValidateUpdateValue = (formName, value, field, changeFormField) => {
    const newField = update(field, {
        value: { $set: value },
    });
    changeFormField(formName, newField);
    return Promise.resolve(newField);
};

export const clearErrorField = (formName, oldField, changeFormField) => {
    const errorMessage = oldField.errorMessage;
    let newField;
    if (errorMessage) {
        newField = update(oldField, {
            isError: { $set: false },
            errorMessage: { $set: null },
            errorCode: { $set: null },
        });
    } else {
        newField = update(oldField, {
            isError: { $set: false },
        });
    }

    changeFormField(formName, newField);
    return Promise.resolve(newField);
};

export const changePinToFields = () => {
    const fields = {};

    fields.pin = getField('pin', '');
    return fields;
};

export const validationOnRequiredAndReturnErrorFields = (formName, fields, changeFormField, checkNotVisibleField = true) => {
    const errorFields = [];
    getSortFields(fields).forEach((field) => {
        if (checkNotVisibleField || field.visible) {
            if (field.isError) {
                errorFields.push(field);
                if (__isDev__) {
                    console.log('field "%s" \t is error!: ', field.name, field);
                }
            } else if (field.mandatory && !field.value) {
                let newField = update(field, {
                    $auto: { isError: { $set: true } },
                });
                if (field.parentField && Array.isArray(field.items) && !field.items.length) {
                    newField = update(field, {
                        $auto: { isError: { $set: false } },
                    });
                } else {
                    errorFields.push(field);
                    if (__isDev__) {
                        console.log('field "%s" \t is empty!: ', field.name, field);
                    }
                }
                changeFormField(formName, newField);
            }
        }
    });
    return errorFields;
};

export const getEmptyMandatoryOrFieldsWithError = (fields, checkNotVisibleField = true, addLogInfo = true) => {
    const errorFields = [];
    getSortFields(fields).forEach((field) => {
        if (checkNotVisibleField || field.visible) {
            if (field.isError || (field.errorCode && field.errorCode !== 0)) {
                errorFields.push(field);
                if (__isDev__ && addLogInfo) {
                    console.log('field "%s" \t is error!: ', field.name, field);
                }
            } else if (field.mandatory && !field.value) {
                if (__isDev__ && addLogInfo) {
                    console.log('field "%s" \t is empty!: ', field.name, field);
                }
                const newField = update(field, {
                    $auto: { isError: { $set: true } },
                });
                errorFields.push(newField);
            }
        }
    });
    return errorFields;
};
export const getEmptyNotValidFields = (fields, checkNotVisibleField = true, addLogInfo = true) => {
    const errorFields = [];
    getSortFields(fields).forEach((field) => {
        if (checkNotVisibleField || field.visible) {
            if (field.isError || (field.errorCode && field.errorCode !== 0)) {
                errorFields.push(field);
                if (__isDev__ && addLogInfo) {
                    console.log('field "%s" \t is error!: ', field.name, field);
                }
            } else if (!field.value && (field.mandatory || (!field.mandatory && field.checkRegexp))) {
                if (__isDev__ && addLogInfo) {
                    console.log('field "%s" \t is empty!: ', field.name, field);
                }
                const newField = update(field, {
                    $auto: { isError: { $set: true } },
                });
                errorFields.push(newField);
            } else if (field.checkRegexp && field.value) {
                let trimValue = typeof field.value === 'string' || field.value instanceof String ? field.value.trim() : field.value;
                const valid = new RegExp(field.checkRegexp);
                if (!valid.test(trimValue)) {
                    errorFields.push(field);
                    if (__isDev__ && addLogInfo) {
                        console.log('field "%s" \t is error!: ', field.name, field);
                    }
                }
            }
        }
    });
    return errorFields;
};

export const convertFieldsToSearchParams = (fields = {}, isGetCanonicalPath = false) => {
    const data = new URLSearchParams();
    Object.values(fields).forEach((field) => {
        let trimValue = typeof field.value === 'string' || field.value instanceof String ? field.value.trim() : field.value;
        if (trimValue && (field.canonicalPath || field.name)) {
            data.append(isGetCanonicalPath && field.canonicalPath ? field.canonicalPath : field.name, trimValue);
        }
    });
    return data;
};

export const getField = (
    name = 'name',
    value = '',
    checkRegexp = '',
    mandatory = false,
    isError = false,
    placeholder = '',
    type = 'text',
    visible = true,
    canonicalPath = false,
    order = null,
) => {
    return {
        name: name,
        value: value,
        checkRegexp: checkRegexp,
        mandatory: mandatory,
        isError: isError,
        placeholder: placeholder,
        type: type,
        visible: visible,
        canonicalPath: canonicalPath ? `toValues[${name}]` : null,
        order: order,
    };
};

