const amountToFields = (toValues = [], amountField = {}, countryId, isFrom = true) => {
    const fields = {};
    if (amountField.name) {
        fields[amountField.name] = getField(
            amountField.name,
            toValues[amountField.name] ? toValues[amountField.name] : '',
            '(\\d{1,3} ){0,5}\\d{1,3}(\\.\\d{0,2}){0,1}',
            true,
            false,
            amountField.placeholder,
            'tel',
            true,
            true,
            isFrom ? -2 : -1,
        );
    }
    if (amountField.currencyName) {
        fields[amountField.currencyName] = getField(amountField.currencyName, amountField.currencyId, '', true, false, '', 'number', true, true);
    }
    const cnrIdName = `__sys_pay_${isFrom ? 'from' : 'to'}_cnr_id`;
    fields[cnrIdName] = getField(cnrIdName, countryId, '', true, false, '', 'number', true, true);
    return fields;
};
export const getFieldsForCreateRequest = (data, messages = {}) => {
    return getFieldsForRequest(true, data, messages);
};

const getFieldsForRequest = (isCreate = true, data, messages = {}) => {
    const requestForm = data.requestForm ? data.requestForm : {};
    const requestInfo = data.requestInfo ? data.requestInfo : {};
    const sourceCodeFrom = getExtra(requestForm.sourceCodeFrom, 'type') === EXTERNAL ? null : requestForm.sourceCodeFrom;

    const fromFields = fromAmountToFields(requestForm.toValues, requestForm.fromAmount, requestInfo.countryFromId);
    Object.values(fromFields).forEach((field) => (field.mandatory = false));

    let cardFields = {};
    const method = isCreate ? requestForm.deliveryMethod : requestForm.paymentSourceFromMethod;
    if (method === PAYMENT_BANK_CARD) {
        cardFields = cardToFields(requestForm.toValues, requestInfo.card);
        Object.values(cardFields).forEach((field) => (field.visible = true));
    }
    if (method === BONUS) {
        cardFields = cardToFields(requestForm.toValues, requestInfo.card);
    }
    let accountFields = {};
    if (method === PAYMENT_BANK_ACCOUNT) {
        accountFields = fieldsToObject(requestForm.toValues, requestInfo.account);
        Object.values(accountFields).forEach((field) => (field.visible = true));
    }

    const fields = {
        ...toAmountToFields(requestForm.toValues, requestForm.toAmount, requestInfo.countryToId),
        ...fromFields,
        ...cardFields,
        ...accountFields,
        ...(isCreate ? requestInfo.fieldsTo : {}),
        ...(!isCreate ? requestInfo.fieldsFrom : {}),
        ...(!isCreate ? {} : { [PHONE]: getField(PHONE, '', DEFAULT_PHONE_REGEX, true, false, messages.requestPhonePlaceholder, 'tel', true, false, -3) }),
        ...{ [BILL_ISSUER_COMMENT]: getField(BILL_ISSUER_COMMENT, '', '', false, false, messages.requestCommentPlaceholder, 'text', true, false) },
        ...(isCreate ? {} : { [SOURCE]: getField(SOURCE, sourceCodeFrom, null, false, false, null, 'text', false, true) }),
        ...{
            [SOURCE_CODE_FROM]: getField(SOURCE_CODE_FROM, requestForm.sourceCodeFrom),
        },
        ...{
            [PAYMENT_METHOD]: getField(PAYMENT_METHOD, requestForm.paymentSourceFromMethod),
        },
        ...{
            [DELIVERY_METHOD]: getField(DELIVERY_METHOD, requestForm.deliveryMethod),
        },
        ...{
            [ACTUAL_TILL]: getField(
                ACTUAL_TILL,
                requestForm.toValues ? requestForm.toValues[ACTUAL_TILL] : null,
                requestForm.actualTill ? requestForm.actualTill?.checkRegexp : null,
                requestForm.actualTill?.mandatory,
                false,
                messages.requestActualTillPlaceholder,
                'date',
                true,
                true,
            ),
        },
        ...{
            [FROM_PRV_ID]: getField(FROM_PRV_ID, requestForm[FROM_PRV_ID]),
        },
        ...{
            [TO_PRV_ID]: getField(TO_PRV_ID, requestForm[TO_PRV_ID]),
        },
        ...{
            [BILL_ISSUER]: getField(BILL_ISSUER, requestForm[BILL_ISSUER]),
        },
        ...{
            [ID]: getField(ID, requestForm[ID]),
        },
        ...{
            [ACCEPTED_PAYMENT]: getField(ACCEPTED_PAYMENT, requestForm[ACCEPTED_PAYMENT]),
        },
    };
    addFieldToFields(fields, requestForm.actualTill);

    return fields;
};

export const getFieldsForSend = (data) => {
    const paymentInfo = data.paymentInfo ? data.paymentInfo : {};
    const paymentForm = data.paymentForm;
    const countryMenu = data.countryMenu;
    const sourceCodeFrom = getExtra(paymentForm?.sourceCodeFrom, 'type') === EXTERNAL ? null : paymentForm?.sourceCodeFrom;
    let fields = {
        ...toAmountToFields(paymentForm.toValues, paymentForm.toAmount, countryMenu.countryTo.id),
        ...fromAmountToFields(paymentForm.toValues, paymentForm.fromAmount, countryMenu.countryFrom.id),
        ...fieldsToObject(paymentForm.toValues, paymentInfo.fieldsTo),
        ...fieldsToObject(paymentForm.toValues, paymentInfo.fieldsFrom),
        ...cardToFields(paymentForm.toValues, paymentInfo.card, paymentInfo),
        ...cardToFields(paymentForm.toValues, paymentInfo.cardTo),
        ...fieldsToObject(paymentForm.toValues, paymentInfo.account),
        ...fieldsToObject(paymentForm.toValues, paymentInfo.accountTo),
        ...{
            [KEEP_AMOUNT]: getField(KEEP_AMOUNT, true),
        },
        ...{
            [DELIVERY_METHOD]: getField(DELIVERY_METHOD, paymentForm.deliveryMethod),
        },
        ...{
            [PAYMENT_METHOD]: getField(PAYMENT_METHOD, paymentForm.paymentSourceFromMethod),
        },
        ...{
            [TO_PRV_ID]: getField(TO_PRV_ID, paymentForm.toPrvId),
        },
        ...{
            [FROM_PRV_ID]: getField(FROM_PRV_ID, paymentForm.fromPrvId),
        },
        ...{
            [SOURCE_CODE_FROM]: getField(SOURCE_CODE_FROM, sourceCodeFrom),
        },
        ...{
            [SOURCE]: getField(SOURCE, sourceCodeFrom, null, false, false, '', 'text', false, true),
        },
    };
    if (paymentForm.toValues[INVITE_CODE_FIELD]) {
        fields = update(fields, {
            [INVITE_CODE_FIELD]: { $set: getField(INVITE_CODE_FIELD, paymentForm.toValues[INVITE_CODE_FIELD], null, false, false, null, 'text', false, true) },
        });
    }
    if (paymentForm.toValues[PROMO_CODE_FIELD]) {
        fields = update(fields, {
            [PROMO_CODE_FIELD]: { $set: getField(PROMO_CODE_FIELD, paymentForm.toValues[PROMO_CODE_FIELD], null, false, false, null, 'text', false, true) },
        });
    }
    return fields;
};

