export const fieldsToObject = (toValues, fields) => {
    const p = {};
    if (fields) {
        if (Array.isArray(fields)) {
            fields.forEach((field) => {
                addFieldToFields(toValues, p, field);
            });
        } else if (fields) {
            Object.keys(fields).forEach((key) => {
                addFieldToFields(toValues, p, fields[key]);
            });
        }
    }
    return p;
};

const addFieldToFields = (toValues, fields, field) => {
    if (field) {
        if (toValues[field.name]) {
            field.value = toValues[field.name];
        }
        fields[field.name] = { ...field, visible: true };
    }
};

const extraToObject = (stringExtra) => {
    const extra = {};
    if (stringExtra) {
        stringExtra.split(';').forEach((e) => {
            const se = e.split('=');
            if (se[0] && se[0].length > 0) {
                extra[se[0]] = se[1];
            }
        });
    }
    return extra;
};

export const cardToFields = (toValues, card, paymentInfo = {}) => {
    const p = {};
    if (card) {
        const extra = extraToObject(toValues[SOURCE]);
        addFieldToFields(toValues, p, card.pan);
        addFieldToFields(toValues, p, card.cardExpired);
        addFieldToFields(toValues, p, card.cvv);
        if (extra.type === DC && card.pan && card.pan.name.indexOf('from_') !== -1) {
            if (card.pan && p[card.pan.name]) {
                p[card.pan.name].isReadOnly = true;
                p[card.pan.name].value = paymentInfo.sources?.from?.[paymentInfo.sourceFrom]?.account ?? '';
            }
            if (card.cardExpired && p[card.cardExpired.name]) {
                p[card.cardExpired.name].isReadOnly = true;
                p[card.cardExpired.name].value = paymentInfo.sources?.from?.[paymentInfo.sourceFrom]?.cardExpired ?? '';
            }
        }
        p[SOURCE] = getField(SOURCE, card.source, '', false, false, '', 'text', true, true);
    }

    return p;
};

