import React, { Children, useRef } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import PopupNew from 'components/common/Popup/PopupNew';

import { REQ_ERROR, VALID_ERROR } from 'constants/Constants';

import FieldLongTitlePopup from './FieldLongTitlePopup';
import { DiskButtonContainer, FloatingLabel, HintButton, HintContainer, InputContainer, InputLabel, InputRightElement } from './styles';
import { ERR_NOT_ALLOWED_PAYMENT_TO_COUNTRY, ERR_NOT_ALLOWED_SWIFT } from '../../constants/WellKnownErrorCodes';
import { DiskButton } from '../common/DiskButton/DiskButton';
import { Icon } from '../common/Icon/Icon';
import PopupPortal from '../common/Popup/PopupPortal';

const NewInputWrapper = (props) => {
    const popupRef = useRef(null);

    const errorMessage = props.isEmptyValue ? props.reqError : props.regexpErrorMessage ? props.regexpErrorMessage : props.validError;

    const isShowErrorHintButton = (errorCode) => {
        if (props.isLoginInputError && errorCode === 78) {
            return true;
        } else if (errorCode === ERR_NOT_ALLOWED_PAYMENT_TO_COUNTRY || errorCode === ERR_NOT_ALLOWED_SWIFT) {
            return true;
        } else {
            return false;
        }
    };

    const getPopupComponent = () => {
        const info = props.longTitle ? props.longTitle : props.commentLong;
        return <FieldLongTitlePopup info={info} popupRef={popupRef} />;
    };

    //TODO rewrite readOnly state
    return (
        <>
            <InputContainer
                ref={props.forwardedRef}
                isError={props.isError}
                isFilled={props.isFilled}
                isReadOnly={props.isReadOnly}
                isFocused={props.isFocused}
                isLinedInput={props.isLinedInput}
                isCalculatorInput={props.isCalculatorInput}
            >
                <InputLabel
                    htmlFor={props.name}
                    onClick={(e) => props.setFocus(e)}
                    isLinedInput={props.isLinedInput}
                    isPseudoSelect={props.isPseudoSelect}
                    isSmsInput={props.isSmsInput}
                    isCenteredInput={props.isCenteredInput}
                    isError={props.isError}
                    isCalculatorInput={props.isCalculatorInput}
                >
                    {props.leftInputComponent}
                    {Children.count(props.children) > 1 ? props.children[0] : props.children}
                    <FloatingLabel
                        isError={props.isError}
                        isFilled={props.isFilled}
                        isReadOnly={props.isReadOnly}
                        isFocused={props.isFocused}
                        isLinedInput={props.isLinedInput}
                        isCaptcha={props.isCaptcha}
                        isCenteredInput={props.isCenteredInput}
                    >
                        {props.labelText}
                    </FloatingLabel>
                </InputLabel>
                {props.hasArrowButton && (
                    <DiskButtonContainer>
                        <DiskButton
                            variant="secondary"
                            iconName="arrow-forward-rounded"
                            iconColor="purple"
                            type="button"
                            raised={false}
                            onClick={props.arrowButtonClick}
                        />
                    </DiskButtonContainer>
                )}
                {props.isPseudoSelect && (
                    <InputRightElement>
                        <Icon name="chevron-down" />
                    </InputRightElement>
                )}
                {props.longTitle && (
                    <InputRightElement isInfoButton>
                        <DiskButton
                            size="small"
                            type="button"
                            variant="tertiary"
                            iconName="info-rounded"
                            iconColor="mediumGray"
                            raised={false}
                            onClick={() => popupRef.current?.open()}
                        />
                    </InputRightElement>
                )}
                {props.children &&
                    Children.count(props.children) > 1 &&
                    props.children.map((children, index) => {
                        if (index !== 0) {
                            return children;
                        }
                    })}
            </InputContainer>
            {props.comment && !props.isError && (
                <HintContainer variant="div" color="mediumGray" size="secondary" isLinedInput={props.isLinedInput}>
                    {props.comment}
                    {props.commentLong && (
                        <HintButton>
                            <DiskButton
                                size="small"
                                type="button"
                                variant="tertiary"
                                iconName="info-rounded"
                                iconColor="mediumGray"
                                raised={false}
                                onClick={() => popupRef.current?.open()}
                            />
                        </HintButton>
                    )}
                </HintContainer>
            )}
            {props.isError && !props.isDetached && (
                <HintContainer variant="div" color="red" size="secondary" isLinedInput={props.isLinedInput}>
                    {props.errorMessage || errorMessage}
                    {isShowErrorHintButton(props.errorCode) && (
                        <HintButton>
                            <DiskButton
                                size="small"
                                type="button"
                                variant="errorHint"
                                iconName="info-rounded"
                                iconColor="red"
                                raised={false}
                                onClick={() => {
                                    if (props.hintButtonClick) {
                                        props.hintButtonClick(props.errorCode, props.name);
                                    }
                                }}
                            />
                        </HintButton>
                    )}
                </HintContainer>
            )}
            <PopupNew ref={popupRef} getPopupComponent={getPopupComponent} as={PopupPortal} />
        </>
    );
};
NewInputWrapper.defaultProps = {
    isDetached: false,
};

NewInputWrapper.propTypes = {
    errorMessage: PropTypes.string,
    name: PropTypes.string.isRequired,
    isReadOnly: PropTypes.bool,
    labelText: PropTypes.string,
    isFilled: PropTypes.bool,
    isError: PropTypes.bool,
    hasArrowButton: PropTypes.bool,
    isFocused: PropTypes.bool,
    leftInputComponent: PropTypes.object,
    arrowButtonClick: PropTypes.func,
    isLinedInput: PropTypes.bool,
    isPseudoSelect: PropTypes.bool,
    isCaptcha: PropTypes.bool,
    isSmsInput: PropTypes.bool,
    isCenteredInput: PropTypes.bool,
    hintButtonClick: PropTypes.func,
    longTitle: PropTypes.string,
    comment: PropTypes.string,
    commentLong: PropTypes.string,
    isInfoButton: PropTypes.bool,
};

const mapStateToProps = (state) => {
    return {
        validError: state.messages.jsMessages[VALID_ERROR],
        reqError: state.messages.jsMessages[REQ_ERROR],
    };
};

export default connect(mapStateToProps)(NewInputWrapper);
