import { toggleSpinner, updateMessages, toggleCloudFlareModal } from 'actions/appAction';

import { AUTH_REQUEST_CANCEL_LINK } from 'constants/UrlConstants';

import { apiFetchData } from 'helpers/api';

import { UPDATE_PAGE_MESSAGES } from './messageAction';

export const RENAME_PAYMENT = 'RENAME_PAYMENT';
export const ADD_NEXT_PAYMENTS = 'ADD_NEXT_PAYMENTS';
export const ADD_NEXT_BILLS = 'ADD_NEXT_BILLS';
export const UPDATE_PAYMENT_BY_FILTER = 'UPDATE_PAYMENT_BY_FILTER';
export const UPDATE_REQUEST_BY_FILTER = 'UPDATE_REQUEST_BY_FILTER';
export const UPDATE_RECORDS = 'UPDATE_RECORDS';
export const GET_CHEQUE = 'GET_CHEQUE';
export const GET_REQUEST_CHEQUE = 'GET_REQUEST_CHEQUE';

export const closeRequest = (billId) => {
    return (dispatch) => {
        dispatch(toggleSpinner(true));
        return Promise.resolve(
            apiFetchData(`${AUTH_REQUEST_CANCEL_LINK}?billId=${billId}`)
                .then((json) => {
                    dispatch(updateMessages(json.messageBodyMessages));
                    dispatch(updateRequestByFilter(json.billHistory));
                })
                .finally(() => {
                    dispatch(toggleSpinner(false));
                }),
        );
    };
};
export const getCheque = (paymentId) => {
    return (dispatch) => {
        dispatch(toggleSpinner(true));
        const dispatchToggleCloudFlareModal = (isShow, path, options) => dispatch(toggleCloudFlareModal(isShow, path, options));
        return Promise.resolve(
            apiFetchData(`/auth/cheque/${paymentId}`, null, null, { toggleCloudFlareModal: dispatchToggleCloudFlareModal })
                .then((json) => {
                    dispatchCheque(dispatch, json);
                })
                .finally(() => {
                    dispatch(toggleSpinner(false));
                }),
        );
    };
};
export const setCheque = (cheque) => {
    return (dispatch) => {
        return Promise.resolve(dispatchCheque(dispatch, cheque));
    };
};
export const dispatchCheque = (dispatch, json = {}) => {
    if (json?.messages) {
        dispatch({
            type: UPDATE_PAGE_MESSAGES,
            pageMessages: json.messages,
        });
    }
    return dispatch({
        type: GET_CHEQUE,
        cheque: json,
    });
};
export const getRequestCheque = (requestId, setSeen = true) => {
    return (dispatch) => {
        dispatch(toggleSpinner(true));
        return Promise.resolve(
            apiFetchData(`/auth/request/cheque/${requestId}?history=true&setSeen=${setSeen}`)
                .then((json) => {
                    dispatch({
                        type: GET_REQUEST_CHEQUE,
                        cheque: json,
                    });
                })
                .finally(() => {
                    dispatch(toggleSpinner(false));
                }),
        );
    };
};
export const updateRequestByFilter = (paymentHistory) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: UPDATE_REQUEST_BY_FILTER,
                paymentHistory: paymentHistory,
            }),
        );
    };
};
export const updatePaymentsByFilter = (paymentHistory) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: UPDATE_PAYMENT_BY_FILTER,
                paymentHistory: paymentHistory,
            }),
        );
    };
};
export const addNextPayments = (payments) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: ADD_NEXT_PAYMENTS,
                paymentHistory: payments,
            }),
        );
    };
};
export const addNextBills = (billHistory) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: ADD_NEXT_BILLS,
                billHistory: billHistory,
            }),
        );
    };
};
export const renamePayment = (paymentId, name = '') => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: RENAME_PAYMENT,
                paymentId: paymentId,
                name: name,
            }),
        );
    };
};

export const updateRecords = (paymentId, status, paymentIndex, recordIndex, newRecord) => {
    return (dispatch) => {
        return Promise.resolve(
            dispatch({
                type: UPDATE_RECORDS,
                paymentId,
                status,
                paymentIndex,
                recordIndex,
                newRecord,
            }),
        );
    };
};
