import React from 'react';
import { connect } from 'react-redux';

import SupportPopup from 'components/auth/utils/popup/SupportPopup';

import { togglePopup } from 'actions/appAction';

import { AUTH_INVITE_CODE_PROMO, AUTH_SEND_MAIN_LINK } from 'constants/UrlConstants';

import { DangerouslyHtml } from '../../common/DangerouslyHtml/DangerouslyHtml';
import { InfoPage } from '../../common/Layout/InfoPage/InfoPage';

const Failed = (props) => {
    return (
        <InfoPage
            iconName="cross-rounded-thin"
            iconColor="red"
            headerText={props.headerTitle}
            secondaryText={<DangerouslyHtml dangerouslyHtml={props.statusText} />}
            buttonRowCaption={props.statusSupportTitle}
            buttonRowOnClick={(e) => {
                e.preventDefault();
                props.togglePopup(true, <SupportPopup />);
            }}
            primaryButtonCaption={props.statusCancelTitle}
            primaryButtonType="link"
            primaryButtonLink={props.status && props.status.showInviteCodeReferralButton ? AUTH_INVITE_CODE_PROMO : AUTH_SEND_MAIN_LINK}
        />
    );
};

Failed.defaultProps = {
    cancelUrl: AUTH_SEND_MAIN_LINK,
};

const mapStateToProps = (state) => {
    return {
        headerTitle: state.messages.pageMessages.headerTitle,
        statusText: state.messages.pageMessages.statusText,
        statusSupportTitle: state.messages.pageMessages.statusSupportTitle,
        statusCancelTitle: state.messages.pageMessages.statusCancelTitle,
    };
};

export default connect(mapStateToProps, { togglePopup })(Failed);
