import styled from 'styled-components';

import promoColors from 'theme/promoColors';

import { PromoText } from '../PromoText/PromoText';

const getBackground = (props) => {
    if (props.isDisabled) {
        return promoColors.gray40;
    } else if (props.isSelected) {
        return promoColors.primary;
    } else {
        return promoColors.purple5;
    }
};
const getColor = (props) => {
    if (props.isDisabled || props.isSelected) {
        return promoColors.white;
    } else if (props.isInactive) {
        return promoColors.gray80;
    } else {
        return promoColors.primary;
    }
};

export const Container = styled(PromoText)`
    background-color: ${(props) => getBackground(props)};
    color: ${(props) => getColor(props)};
    display: inline-block;
    padding: 8px 16px;
    border-radius: 4px;
    white-space: nowrap;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: ${(props) => (props.isDisabled || props.isInactive || props.isSelected ? 'default' : 'pointer')};

    &:hover {
        background: ${(props) => (props.isDisabled || props.isInactive || props.isSelected ? null : promoColors.purple20)};
    }
`;
