import styled from 'styled-components';

import colors from '../../../theme/colors';
import media from '../../../theme/media';

const getSize = (props) => {
    switch (props.diskSize) {
        case 'x-small':
            return '24px';
        case 'small':
            return '40px';
        case 'fixed-small':
            return '40px';
        case 'large':
            return '80px';
        default:
            return '40px';
    }
};
const getDesktopSize = (props) => {
    switch (props.diskSize) {
        case 'x-small':
            return '24px';
        case 'small':
            return '56px';
        case 'fixed-small':
            return '40px';
        case 'large':
            return '112px';
        default:
            return '56px';
    }
};
const getImgSize = (props) => {
    switch (props.diskSize) {
        case 'x-small':
            return '24px';
        case 'small':
            return props.variant === 'hasBorderedImg' ? '38px' : '40px';
        case 'large':
            return props.variant === 'hasBorderedImg' ? '78px' : '80px';
        default:
            return props.variant === 'hasBorderedImg' ? '38px' : '40px';
    }
};
const getImgDesktopSize = (props) => {
    switch (props.diskSize) {
        case 'x-small':
            return '24px';
        case 'small':
            return props.variant === 'hasBorderedImg' ? '54px' : '56px';
        case 'large':
            return props.variant === 'hasBorderedImg' ? '110px' : '112px';
        default:
            return props.variant === 'hasBorderedImg' ? '54px' : '56px';
    }
};
const getBackground = (props) => {
    switch (props.variant) {
        case 'primary':
            return props.isDisabled ? colors.purpleRgba24 : colors.purple;
        case 'secondary':
            return props.isDisabled ? colors.white : colors.purpleRgba08;
        case 'tertiary':
            return props.isDisabled ? 'transparent' : colors.superLightGray;
        case 'letterIndex':
            return colors.white;
        case 'selectedPassive':
            return colors.whiteRgba96;
        case 'transparent':
            return 'transparent';
        case 'hasBorderedImg':
            return colors.white;
        default:
            return colors.white;
    }
};
const getBorder = (props) => {
    if (props.variant !== 'hasBorderedImg' && props.noBorder) {
        return 'transparent';
    } else {
        switch (props.variant) {
            case 'primary':
                return props.isDisabled ? 'transparent' : colors.purple;
            case 'secondary':
                return props.isDisabled ? colors.purpleRgba16 : 'transparent';
            case 'tertiary':
                return props.isDisabled ? colors.greyRgba28 : colors.superLightGray;
            case 'letterIndex':
                return props.isDisabled ? colors.white : colors.codGray;
            case 'selectedPassive':
                return colors.purpleRgba40;
            case 'transparent':
                return 'transparent';
            case 'hasBorderedImg':
                return colors.greyRgba28;
            default:
                return colors.white;
        }
    }
};

export const DiskBadgeContainer = styled.span`
    display: inline-flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    border-radius: 50%;
    width: ${(props) => getSize(props)};
    height: ${(props) => getSize(props)};
    background: ${(props) => getBackground(props)};
    border: ${(props) => (props.variant === 'letterIndex' ? '2px' : '1px')} solid ${(props) => getBorder(props)};
    position: relative;

    ${media.desktop1920} {
        width: ${(props) => getDesktopSize(props)};
        height: ${(props) => getDesktopSize(props)};
    }
`;

export const ImgWrap = styled.span`
    width: ${(props) => getImgSize(props)};
    height: ${(props) => getImgSize(props)};
    flex-shrink: 0;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    opacity: ${(props) => (props.isDisabled ? '0.24' : props.isInactive ? '0.48' : null)};
    overflow: hidden;
    border-radius: ${(props) => (props.withoutRadius ? null : '50%')};

    ${media.desktop1920} {
        width: ${(props) => getImgDesktopSize(props)};
        height: ${(props) => getImgDesktopSize(props)};
    }

    img {
        width: 100%;
        max-width: 100%;
        border-radius: ${(props) => (props.withoutRadius ? null : '50%')};
    }
`;

export const FlagIconWrap = styled.span`
    position: absolute;
    right: -4px;
    bottom: -4px;
    border: 2px solid ${colors.white};
    border-radius: 50%;
    display: inline-flex;
    background: ${colors.white};
    z-index: 1;

    ${media.desktop1920} {
        right: -2px;
        bottom: -2px;
    }
`;
export const FlagIconContainer = styled.span`
    display: inline-flex;
    opacity: ${(props) => (props.isDisabled ? '0.24' : props.isInactive ? '0.48' : null)};
`;
export const SecondFlagIconWrap = styled.span`
    position: absolute;
    right: -14px;
    bottom: -4px;
    border: 2px solid ${colors.white};
    border-radius: 50%;
    display: inline-flex;
    background: ${colors.white};

    ${media.desktop1920} {
        right: -12px;
        bottom: -2px;
    }
`;
