import React, { useState } from 'react';

import PropTypes from 'prop-types';

import { DiskBadgeContainer, FlagIconContainer, FlagIconWrap, ImgWrap, SecondFlagIconWrap } from './styles';
import { FlagIcon } from '../FlagIcon/FlagIcon';
import { Icon } from '../Icon/Icon';
import { Text } from '../Text/Text';

const getIconColor = (props) => {
    switch (props.variant) {
        case 'primary':
            return 'white';
        case 'secondary':
            return props.isDisabled ? 'purpleRgba24' : props.isInactive ? 'purpleRgba40' : 'purple';
        case 'tertiary':
            return props.isDisabled ? 'lightGray' : props.isInactive ? 'mediumGray' : 'darkGray';
        case 'selectedPassive':
            return 'purple';
        case 'transparent':
            return 'purple';
        default:
            return 'darkGray';
    }
};

export const DiskBadge = (props) => {
    const [errorImageLoading, setErrorImageLoading] = useState(false);
    return (
        <DiskBadgeContainer
            variant={props.variant}
            diskSize={props.diskSize}
            isDisabled={props.isDisabled}
            isInactive={props.isInactive}
            noBorder={!!props.src}
        >
            {props.iconName && <Icon name={props.iconName} color={getIconColor(props)} size={props.diskSize === 'large' ? '48px' : '24px'} />}
            {/*Загрузка аватарки*/}
            {props.src && !errorImageLoading && (
                <ImgWrap
                    isDisabled={props.isDisabled}
                    isInactive={props.isInactive}
                    withoutRadius={props.withoutRadius}
                    diskSize={props.diskSize}
                    variant={props.variant}
                >
                    <img src={props.src} alt={props.alt} onError={(e) => (props.onError ? props.onError(e) : setErrorImageLoading(true))} />
                </ImgWrap>
            )}
            {/*Текст вместо иконки или аватарки*/}
            {props.diskText && (
                <Text size={props.diskSize === 'large' ? 'header' : 'main'} color={getIconColor(props)}>
                    {props.diskText}
                </Text>
            )}
            {/*обработка ошибок загрузки изображения*/}
            {errorImageLoading && (
                <Text size={props.diskSize === 'large' ? 'header' : 'main'} color={getIconColor(props)}>
                    {props.logoSymbol}
                </Text>
            )}
            {props.flagName && (
                <FlagIconWrap>
                    <FlagIconContainer isDisabled={props.isDisabled} isInactive={props.isInactive}>
                        <FlagIcon name={props.flagName} size={props.diskSize === 'large' ? 'x-small' : '16px'} />
                    </FlagIconContainer>
                </FlagIconWrap>
            )}
            {props.secondFlagName && (
                <SecondFlagIconWrap>
                    <FlagIconContainer isDisabled={props.isDisabled} isInactive={props.isInactive}>
                        <FlagIcon name={props.secondFlagName} size={props.diskSize === 'large' ? 'x-small' : '16px'} />
                    </FlagIconContainer>
                </SecondFlagIconWrap>
            )}
        </DiskBadgeContainer>
    );
};

DiskBadge.defaultProps = {
    variant: 'tertiary',
    diskSize: 'small',
    logoSymbol: '',
};

DiskBadge.propTypes = {
    variant: PropTypes.oneOf(['primary', 'secondary', 'tertiary', 'letterIndex', 'selectedPassive', 'transparent', 'hasBorderedImg']),
    diskSize: PropTypes.oneOf(['x-small', 'small', 'fixed-small', 'large']),
    isDisabled: PropTypes.bool,
    isInactive: PropTypes.bool,
    iconName: PropTypes.string,
    flagName: PropTypes.string,
    secondFlagName: PropTypes.string,
    diskText: PropTypes.string,
    src: PropTypes.string,
    alt: PropTypes.string,
    logoSymbol: PropTypes.string,
    withoutRadius: PropTypes.bool,
    onError: PropTypes.func,
};
