import React from 'react';
import { connect } from 'react-redux';

import PopupButton from 'components/common/Popup/PopupButton';
import SimplePopup from 'components/common/Popup/SimplePopup';

import { togglePopup, toggleSpinner } from 'actions/appAction';
import { addNewFields, changeFormFieldValue, setFieldError } from 'actions/formAction';
import { updatePaymentForm } from 'actions/sendAction';

import AddPromoOrInviteCode from './AddPromoOrInviteCode';
import { DangerouslyHtml } from '../common/DangerouslyHtml/DangerouslyHtml';

const AliPayReduceCommission = (props) => {
    return (
        <SimplePopup text={<DangerouslyHtml dangerouslyHtml={props.textReduceCommission} />}>
            <PopupButton
                iconName="arrow-forward"
                text={props.transferUsingMastercardPromo}
                onClick={(e) => {
                    e.preventDefault();
                    if (props.closeHandler) {
                        props.closeHandler(e);
                    } else {
                        props.togglePopup(false);
                    }
                }}
            />
            <PopupButton
                iconName="plus"
                text={props.addInviteCode}
                onClick={() =>
                    props.togglePopup(
                        true,
                        <AddPromoOrInviteCode
                            formName={props.formName}
                            updateRedux={props.updateRedux}
                            canUsePromoCode={props.canUsePromoCode}
                            canUseInviteCode={props.canUseInviteCode}
                        />,
                    )
                }
            />
        </SimplePopup>
    );
};

const mapStateToProps = (state) => {
    return {
        lang: state.language.lang,
        country: state.app.country,
        addInviteCode: state.messages.pageMessages.addInviteCode,
        transferUsingMastercardPromo: state.messages.pageMessages.transferUsingMastercardPromo,
        textReduceCommission: state.messages.pageMessages.textAliPayReduceCommission,
    };
};
export default connect(mapStateToProps, {
    addNewFields,
    setFieldError,
    updatePaymentForm,
    changeFormFieldValue,
    togglePopup,
    toggleSpinner,
})(AliPayReduceCommission);
