import React from 'react';

import PropTypes from 'prop-types';

import { NavBar } from '../../components/common/Layout/NavBar/NavBar';
import { CommonPagePromo } from '../../components/no-auth/common/CommonPagePromo';
import LogoPurple from '../../components/no-auth/header/LogoPurple';
import NotFoundInfo from '../../components/no-auth/info/NotFoundInfo';
import ReqAndMoneyInfo from '../../components/no-auth/info/ReqAndMoneyInfo';
import { useExtra } from '../../hooks/selectors';

const RouterForInfoPage = (props) => {
    const extra = useExtra();

    const render = () => (extra[props.renderOptions]?.status === 'success' ? <ReqAndMoneyInfo renderOptions={props.renderOptions} /> : <NotFoundInfo />);

    return (
        <CommonPagePromo>
            <NavBar align="left">
                <LogoPurple />
            </NavBar>
            {render()}
        </CommonPagePromo>
    );
};
RouterForInfoPage.propTypes = {
    renderOptions: PropTypes.string.isRequired,
};
export default RouterForInfoPage;
