import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import colors from '../../theme/colors';
import media from '../../theme/media';
import zindex from '../../theme/zIndex';
import { Row } from '../common/Layout/Row';
import { SpinnerCircle } from '../common/SpinnerCircle/SpinnerCircle';
import { Text } from '../common/Text/Text';

const SpinnerOverlay = styled.div`
    bottom: 0;
    left: 0;
    min-width: 288px;
    overflow: auto;
    position: ${(props) => (props.insideChat || props.insidePopup ? 'absolute' : 'fixed')};
    right: 0;
    top: 0;
    background-color: ${(props) => (props.insideChat ? 'transparent' : colors.whiteRgba96)};
    z-index: ${zindex.spinnerOverlay};
`;
const SpinnerContainer = styled.div`
    /* content width 480 */
    max-width: 528px;
    box-sizing: border-box;
    padding: 128px 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100%;
    margin: 0 auto;

    ${media.tablet720} {
        /* content width 504px */
        max-width: 552px;
    }
`;

const Spinner = (props) => {
    if (props.isShowSpinner || props.forceShow) {
        return (
            <SpinnerOverlay insideChat={props.insideChat} insidePopup={props.insidePopup}>
                <SpinnerContainer>
                    <Row indent={props.isEmptyText ? '0' : '24px'}>
                        <SpinnerCircle />
                    </Row>
                    {!props.isEmptyText && <Text variant="paragraph">{props.customSpinnerText ? props.customSpinnerText : props.siteSpinnerText}</Text>}
                </SpinnerContainer>
            </SpinnerOverlay>
        );
    } else {
        return false;
    }
};
Spinner.defaultProps = {
    forceShow: false,
};
Spinner.propTypes = {
    insideChat: PropTypes.bool,
    insidePopup: PropTypes.bool,
    isEmptyText: PropTypes.bool,
    customSpinnerText: PropTypes.string,
};
const mapStateToProps = (state) => {
    return {
        isShowSpinner: state.app.isShowSpinner,
        siteSpinnerText: state.messages?.pageMessages?.siteSpinnerText,
    };
};

export default connect(mapStateToProps)(Spinner);
