import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import { URL_IMAGES_I } from '../../../../constants/UrlConstants';
import promoColors from '../../../../theme/promoColors';
import promoMedia from '../../../../theme/promoMedia';

const Container = styled.a`
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: ${(props) => (props.isInverted ? promoColors.white : promoColors.baseColor)};
    border-radius: 8px;
    width: 136px;
    height: 40px;

    ${promoMedia.desktop1440} {
        width: 208px;
        height: 56px;
    }
`;
const AppImg = styled.img`
    width: 110px;
    height: 32px;

    ${promoMedia.desktop1440} {
        width: 160px;
        height: 44px;
    }
`;

export const AppButton = (props) => {
    const getImgLink = (props) => {
        if (props.variant === 'apple') {
            return props.isInverted ? 'store-apple-inverted.svg' : 'store-apple.svg';
        } else if (props.variant === 'google') {
            return props.isInverted ? 'store-google-inverted.svg' : 'store-google.svg';
        } else if (props.variant === 'huawei') {
            return props.isInverted ? 'store-huawei-inverted.svg' : 'store-huawei.svg';
        }
    };
    return (
        <Container href={props.href} variant={props.variant} rel="noreferrer" target="_blank" aria-label={props.areaLabel} isInverted={props.isInverted}>
            <AppImg src={`${URL_IMAGES_I}/${getImgLink(props)}`} loading="lazy" decoding="async" alt={props.areaLabel} />
        </Container>
    );
};

AppButton.propTypes = {
    href: PropTypes.string,
    variant: PropTypes.oneOf(['apple', 'google', 'huawei']),
    areaLabel: PropTypes.oneOf(['App Store', 'Google Play', 'AppGallery']),
    isInverted: PropTypes.bool,
};
