import React, { useState } from 'react';
import { connect } from 'react-redux';

import { InfoPage } from 'components/common/Layout/InfoPage/InfoPage';

import { toggleModal } from 'actions/appAction';

import { SMS_ON_SUCCESS_PAY_FORM_NAME } from 'constants/formName';
import { AUTH_INVITE_CODE_PROMO, AUTH_MASTERCARD_PROMO, AUTH_MY_CARDS_URL, AUTH_SEND_MAIN_LINK } from 'constants/UrlConstants';

import { REACHING_LIMIT_SCREEN, UPGRADE_LIMIT_SCREEN, WAITING_SCREEN } from './Constants';
import PromoReachingLimit from './PromoReachingLimit';
import PromoUpgradeLimit from './PromoUpgradeLimit';
import SendSMS from './SendSMS';
import { useExtra } from '../../../hooks/selectors';

const Success = (props) => {
    const { cardPan } = useExtra();
    const [screen, setScreen] = useState(WAITING_SCREEN);

    const getPrimaryButtonLink = () => {
        if (props.status?.showUpgradeLimitPromo) {
            return '#';
        } else if (props.status?.showMastercardPromoBlock) {
            return `${AUTH_MASTERCARD_PROMO}?id=${props.status.id}`;
        } else if (cardPan) {
            return AUTH_MY_CARDS_URL;
        } else if (props.status?.showInviteCodeReferralButton) {
            return AUTH_INVITE_CODE_PROMO;
        } else {
            return AUTH_SEND_MAIN_LINK + '?screen_source=transfer_status';
        }
    };

    const primaryButtonOnClick = (e) => {
        if (props.status?.showUpgradeLimitPromo) {
            e.preventDefault();
            setScreen(REACHING_LIMIT_SCREEN);
        }
    };
    const isShowNotifyRecipient = props.notFromML && props.smsOnSuccessPay && !props.smsOnSuccessPay.sended && props.showSendSMSNotificationBlock;

    const renderScreen = (currentScreen) => {
        switch (currentScreen) {
            case REACHING_LIMIT_SCREEN:
                return <PromoReachingLimit primaryButtonOnClick={() => setScreen(UPGRADE_LIMIT_SCREEN)} />;
            case UPGRADE_LIMIT_SCREEN:
                return <PromoUpgradeLimit />;
            default:
                return (
                    <InfoPage
                        iconName="check-rounded-thin"
                        iconColor="purple"
                        hasBorder={!!isShowNotifyRecipient}
                        mainText={props.headerTitle}
                        buttonRowCaption={isShowNotifyRecipient ? props.statusSmsTitle : null}
                        buttonRowOnClick={() => props.toggleModal(true, <SendSMS formName={SMS_ON_SUCCESS_PAY_FORM_NAME} />)}
                        primaryButtonCaption={props.statusDoneTitle}
                        primaryButtonType="link"
                        primaryButtonLink={getPrimaryButtonLink()}
                        primaryButtonOnClick={primaryButtonOnClick}
                    />
                );
        }
    };

    return renderScreen(screen);
};

const mapStateToProps = (state) => {
    return {
        headerTitle: state.messages.pageMessages.headerTitle,
        statusText: state.messages.pageMessages.statusText,
        statusReferenceTitle: state.messages.pageMessages.statusReferenceTitle,
        statusSmsTitle: state.messages.pageMessages.statusSmsTitle,
        statusDoneTitle: state.messages.pageMessages.statusDoneTitle,
        notFromML: state.extra.notFromML,
    };
};

export default connect(mapStateToProps, { toggleModal })(Success);
