/* styles for common section (components inside it) */
export const CommonSection = styled.section`
    background: ${(props) => promoColors[props.bgColor]};
    padding-bottom: 32px;

    ${promoMedia.tablet744} {
        padding-bottom: 48px;
    }

    ${promoMedia.desktop1440} {
        padding-bottom: 64px;
    }
`;

/* carousel dots */
export const CarouselDotGroup = styled.div`
    width: 100%;
    position: ${(props) => (props.isInsideTile ? 'absolute' : null)};
    bottom: ${(props) => (props.isInsideTile ? '40px' : null)};

    ${promoMedia.tablet744} {
        bottom: ${(props) => (props.isInsideTile ? '48px' : null)};
    }

    ${promoMedia.desktop1440} {
        bottom: ${(props) => (props.isInsideTile ? '56px' : null)};
    }

    & > div {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 8px;
    }

    button {
        background-color: ${promoColors.gray20};
        padding: 0;
        cursor: pointer;
        border-radius: 50%;
        height: 8px;
        width: 8px;
    }

    button:disabled {
        background-color: ${promoColors.primary};
        cursor: pointer;
    }
`;

