import React, { useState } from 'react';

import PropTypes from 'prop-types';

import { PromoIcon } from 'components/no-auth/common/PromoIcon/PromoIcon';

import { AccordionContent, AccordionHeader, AccordionIcon, AccordionList } from './styles';

export const Accordion = (props) => {
    const [openedIndex, setOpenedIndex] = useState(null);
    const toggleItem = (e, index) => {
        e.preventDefault();
        if (index === openedIndex) {
            setOpenedIndex(null);
        } else {
            setOpenedIndex(index);
        }
    };

    return (
        <AccordionList>
            {props.items
                .filter((item) => item !== null)
                .map((item, index) => {
                    return (
                        <li key={index}>
                            <AccordionHeader onClick={(e) => toggleItem(e, index)}>
                                {item.header}
                                <AccordionIcon>
                                    <PromoIcon name={openedIndex === index ? 'minus' : 'plus'} size="24px" color="baseColor" />
                                </AccordionIcon>
                            </AccordionHeader>
                            {openedIndex === index && <AccordionContent>{item.content}</AccordionContent>}
                        </li>
                    );
                })}
        </AccordionList>
    );
};

Accordion.propTypes = {
    items: PropTypes.array,
};
