import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import { PromoSectionContainer } from 'components/no-auth/common/PromoSectionContainer';

import promoColors from 'theme/promoColors';
import promoMedia from 'theme/promoMedia';

const Container = styled.div`
    background: ${(props) => promoColors[props.bgColor]};
    overflow: hidden;
    padding-top: ${(props) => (props.doubleTopPadding ? '64px' : '32px')};
    padding-bottom: ${(props) => (props.bottomPadding ? '32px' : null)};

    ${promoMedia.tablet744} {
        padding-top: ${(props) => (props.doubleTopPadding ? '96px' : '48px')};
        padding-bottom: ${(props) => (props.bottomPadding ? '48px' : null)};
    }

    ${promoMedia.desktop1440} {
        padding-top: ${(props) => (props.doubleTopPadding ? '128px' : '64px')};
        padding-bottom: ${(props) => (props.bottomPadding ? '64px' : null)};
    }
`;

const ColoredSectionContainer = (props) => {
    return (
        <Container bgColor={props.bgColor} doubleTopPadding={props.doubleTopPadding} bottomPadding={props.bottomPadding}>
            <PromoSectionContainer dimension={props.dimension}>{props.children}</PromoSectionContainer>
        </Container>
    );
};

ColoredSectionContainer.propTypes = {
    bgColor: PropTypes.oneOf(['baseColor', 'white', 'purple5', 'gray5', 'aubergine01', 'eggshell01', 'eggshell02', 'eggshell03', 'transparent']),
    doubleTopPadding: PropTypes.bool,
    bottomPadding: PropTypes.bool,
    dimension: PropTypes.oneOf(['wide', 'narrow', 'average']),
};

export default ColoredSectionContainer;
