import React, { useEffect, useRef } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { FixedBottomPanel } from 'components/common/Layout/FixedBottomPanel/FixedBottomPanel';

import { togglePopup, toggleSpinner, updateAuthPromoBlock, updateMessages, toggleCloudFlareModal } from 'actions/appAction';
import { addExtras } from 'actions/extraAction';
import { changeFormField, changeFormFieldValue, scrollToField, setFieldError } from 'actions/formAction';
import { updatePageMessages } from 'actions/messageAction';
import { updateCountryMenu } from 'actions/sendAction';

import { fieldPropType } from 'helpers/customPropTypes';

import { AUTOCOMPLETE_POST_CODE, FILE } from '../../constants/FieldType';
import { FormService } from '../../services/FormService';

const Form = (props) => {
    const controller = useRef();
    const formService = useRef(new FormService(props.getCanonicalPath, controller, props.formName));

    useEffect(() => {
        controller.current = __isBrowser__ ? new AbortController() : {};
        formService.current.setUpdateRedux(
            props.updateMessages,
            props.updateAuthPromoBlock,
            props.updatePageMessages,
            props.addExtras,
            props.updateCountryMenu,
        );
        formService.current.setAppAction(
            props.setFieldError,
            props.changeFormField,
            props.scrollToField,
            props.togglePopup,
            props.toggleSpinner,
            props.toggleCloudFlareModal,
        );
    }, []);

    useEffect(() => {
        formService.current.setHandlers(props.beforeRedirect, props.beforeSubmit, props.onErrorHandler, props.onSubmitHandler);
    }, [props.beforeRedirect, props.beforeSubmit, props.onErrorHandler, props.onSubmitHandler]);

    useEffect(() => {
        formService.current.setAction(props.reduxAction, props.action);
    }, [props.reduxAction, props.action]);

    useEffect(() => {
        formService.current.setFormProps(props.reduxValidateForm, props.validateForm, props.checkNotVisibleField);
    }, [props.reduxValidateForm, props.validateForm, props.checkNotVisibleField]);

    const handleKeyDown = (e) => {
        if (!props.submitByEnter && e.keyCode === 13) {
            e.preventDefault();
        }
    };

    return (
        <form
            id={props.id}
            data-testid={props.dataTestId}
            method="POST"
            action={formService.current.getAction()}
            className={props.className}
            onKeyDown={handleKeyDown}
            onSubmit={(e) => {
                let fieldsForSubmit = props.fields;
                if (Array.isArray(props.fieldsForSubmit) && props.fieldsForSubmit.length > 0) {
                    fieldsForSubmit = {};
                    props.fieldsForSubmit.forEach((field) => {
                        fieldsForSubmit[field.name] = props.fields[field.name];
                    });
                }
                formService.current.handlerSubmit(e, fieldsForSubmit);
            }}
        >
            {props.children}
            {props.buttons && <FixedBottomPanel>{props.buttons}</FixedBottomPanel>}
        </form>
    );
};

Form.defaultProps = {
    getCanonicalPath: false,
    validateForm: true,
    checkNotVisibleField: true,
    submitByEnter: true,
    fieldsForSubmit: null,
};
Form.propTypes = {
    formName: PropTypes.string.isRequired,
    action: PropTypes.string,
    getCanonicalPath: PropTypes.bool,
    onSubmitHandler: PropTypes.func,
    className: PropTypes.string,
    buttons: PropTypes.object,
    fieldsForSubmit: PropTypes.array,
    fields: PropTypes.objectOf(
        fieldPropType([
            'textarea',
            'drop_box',
            'sbp_drop_box',
            'tel',
            'email',
            'number',
            'password',
            'search',
            'text',
            'short_text',
            'attachment',
            'url',
            'date',
            'hidden',
            'pan',
            'cvv',
            'exp_date',
            'checkbox',
            'bik',
            'bank_account',
            'phone',
            'phone_strict',
            'autocomplete_address',
            AUTOCOMPLETE_POST_CODE,
            'phone_country',
            'upload_doc',
            'digital',
            'country',
            FILE,
        ]),
    ),
};

const mapStateToProps = ({ form }, ownProps) => ({
    fields: form[ownProps.formName]?.fields,
    reduxAction: form[ownProps.formName]?.action,
    reduxValidateForm: form[ownProps.formName]?.validateForm,
    checkOnlyVisibleFields: false,
});

export default connect(mapStateToProps, {
    changeFormFieldValue,
    changeFormField,
    scrollToField,
    togglePopup,
    toggleSpinner,
    updateMessages,
    updateAuthPromoBlock,
    updatePageMessages,
    updateCountryMenu,
    setFieldError,
    addExtras,
    toggleCloudFlareModal,
})(Form);
