import React, { useEffect } from 'react';

import { useAuth } from '../../hooks/selectors';

const WSChat = () => {
    const { jwt } = useAuth();

    useEffect(() => {
        if (__isBrowser__ && typeof zE === 'function' && !jwt) {
            zE('messenger', 'loginUser', function (callback) {
                callback(jwt);
            });
        }
    }, [jwt]);

    return !jwt ? <div id="ze-snippet" /> : null;
};

export default WSChat;
