import React from 'react';
import { connect } from 'react-redux';

import { togglePopup } from 'actions/appAction';

import PopupButton from '../common/Popup/PopupButton';
import SimplePopup from '../common/Popup/SimplePopup';

const ServerErrorPopup = (props) => {
    const getText = (text) => {
        return <p dangerouslySetInnerHTML={{ __html: text }} />;
    };

    return (
        <SimplePopup popupRef={props.popupRef} text={getText(props.errorText)}>
            <PopupButton
                iconName="check"
                text={props.buttonClose}
                onClick={() => {
                    if (props.popupRef?.current) {
                        props.popupRef.current?.close();
                    } else {
                        props.togglePopup(false);
                    }
                }}
            />
        </SimplePopup>
    );
};

export default connect(null, { togglePopup })(ServerErrorPopup);
