import React from 'react';

import InputBase from 'components/form-multi/InputBase';

import { inputPropType } from 'helpers/customPropTypes';

const InputBik = (props) => {
    const field = props.field;
    if (field.value === null) {
        field.value = '';
    }
    return (
        <input
            id={field.name}
            maxLength={props.setMaxLength && field.maxLength ? field.maxLength : ''}
            name={field.canonicalPath ? field.canonicalPath : field.name}
            value={field.value || ''}
            type="text"
            readOnly={field.isReadOnly || false}
            className={props.isNewInput ? null : 'field field-duo'}
            autoComplete="off"
            autoFocus={props.focus}
            onFocus={props.onFocus}
            onChange={props.onChange}
            onBlur={props.onBlur}
        />
    );
};

InputBik.propTypes = inputPropType(['bik']);

export default InputBase(InputBik, false);
