import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import { validationAndUpdateValue } from 'components/form-multi/utils';

import { changeFormField } from 'actions/formAction';

import { inputPropType } from 'helpers/customPropTypes';

import colors from '../../theme/colors';
import { Icon } from '../common/Icon/Icon';
import { Text } from '../common/Text/Text';

const Placeholder = styled(Text)`
    margin-left: 16px;
`;
const CheckboxInput = styled.input.attrs({ type: 'checkbox' })`
    opacity: 0;
    position: absolute;
    z-index: -1;
`;
const CheckboxIndicator = styled.span`
    border: 2px solid ${colors.lightGray};
    border-radius: 3px;
    display: inline-block;
    height: 20px;
    width: 20px;
    min-width: 20px;
    box-sizing: border-box;
    position: relative;
`;
const CheckboxTickWrap = styled.span`
    position: absolute;
    left: -2px;
    top: -2px;
    display: none;
`;
const CheckboxLabel = styled.label`
    display: inline-flex;
    cursor: ${(props) => (props.disabled ? 'default' : 'pointer')};

    &:hover {
        ${CheckboxIndicator} {
            border-color: ${colors.mediumGray};
        }
        ${CheckboxInput}:checked + ${CheckboxIndicator} {
            background: ${colors.lightPurple};
            border-color: ${colors.lightPurple};
        }
    }

    ${CheckboxInput}:checked + ${CheckboxIndicator} {
        background: ${colors.purple};
        border-color: ${colors.purple};

        ${CheckboxTickWrap} {
            display: inline-block;
        }
    }

    ${CheckboxInput}[disabled] + ${CheckboxIndicator} {
        border-color: ${colors.superLightGray};
    }

    ${CheckboxInput}[disabled]:checked + ${CheckboxIndicator} {
        background: ${colors.purpleRgba40};
        border-color: transparent;
    }
`;

const Checkbox = (props) => {
    const handlerChange = (e) => {
        validationAndUpdateValue(props.formName, e.target.checked, props.field, props.changeFormField).then(() => {
            if (props.onChangeHandler) {
                props.onChangeHandler(props.field.value, props.field.name);
            }
        });
    };

    const field = props.field;
    return (
        <CheckboxLabel htmlFor={field.name} disabled={props.disabled}>
            <CheckboxInput
                id={field.name}
                name={field.canonicalPath ? field.canonicalPath : field.name}
                type="checkbox"
                checked={field.value}
                onChange={handlerChange}
                disabled={props.disabled}
            />
            <CheckboxIndicator>
                <CheckboxTickWrap>
                    <Icon name="check" size="20px" color="white" />
                </CheckboxTickWrap>
            </CheckboxIndicator>
            {field.placeholder && <Placeholder color={props.disabled ? 'lightGray' : 'darkGray'}>{field.placeholder}</Placeholder>}
        </CheckboxLabel>
    );
};
Checkbox.propTypes = inputPropType(['checkbox']);
Checkbox.propTypes = {
    disabled: PropTypes.bool,
};

const mapStateToProps = (state) => {
    return { fields: state.form.fields };
};
export default connect(mapStateToProps, { changeFormField })(Checkbox);
