import React, { useState } from 'react';
import { useSwipeable } from 'react-swipeable';

import PropTypes from 'prop-types';

import { PanelContainerBlock, PanelContainerButton, PanelContainerButtonSubmit, PanelContainerLink, PanelContainerReactLink } from './styles';

const getComponent = (type) => {
    switch (type) {
        case 'div':
            return PanelContainerBlock;
        case 'link':
            return PanelContainerLink;
        case 'reactLink':
            return PanelContainerReactLink;
        case 'button':
            return PanelContainerButton;
        case 'submit':
            return PanelContainerButtonSubmit;
        default:
            return PanelContainerBlock;
    }
};

export const Panel = (props) => {
    const Wrapper = getComponent(props.type);
    const [showDeleteIcon, setShowDeleteIcon] = useState(false);

    const handlers = useSwipeable({
        onSwipedLeft: () => setShowDeleteIcon(true),
        onSwipedRight: () => setShowDeleteIcon(false),
        preventDefaultTouchmoveEvent: true,
        trackTouch: true,
    });

    return (
        <Wrapper
            data-testid={props.dataTestId}
            $isHoverable={props.isHoverable}
            $isBordered={props.isBordered}
            $hasBorderRadius={props.hasBorderRadius}
            $hasIcon={props.hasIcon}
            justify={props.justify}
            href={props.href}
            to={props.to}
            onClick={(e) => props.onClick?.(e)}
            $isNegativeMargin={props.negativeMargin}
            id={props.id}
            target={props.target}
            $isDisabled={props.isDisabled}
            $isDefaultCursor={props.isDefaultCursor}
            $showDeleteIcon={showDeleteIcon}
            $isSelectedPanel={props.isSelectedPanel}
            type={props.type === 'button' ? 'button' : null}
            {...handlers}
        >
            {props.children}
        </Wrapper>
    );
};

Panel.propTypes = {
    type: PropTypes.oneOf(['div', 'link', 'reactLink', 'button', 'submit']),
    isHoverable: PropTypes.bool,
    isBordered: PropTypes.bool,
    hasBorderRadius: PropTypes.bool,
    hasIcon: PropTypes.bool,
    justify: PropTypes.oneOf(['start', 'center', 'end', 'between']),
    href: PropTypes.string,
    to: PropTypes.string,
    onClick: PropTypes.func,
    negativeMargin: PropTypes.bool,
    id: PropTypes.string,
    target: PropTypes.string,
    isDisabled: PropTypes.bool,
    isDefaultCursor: PropTypes.bool,
    dataTestId: PropTypes.string,
};
