import React from 'react';
import { connect } from 'react-redux';

import styled from 'styled-components';

import { cleverTapEvent, useEventOnFirstRender } from 'analytics/cleverTap';

import CalculatorForLanding from 'components/calculator/CalculatorForLanding';

import { NEW_TRANSFER_BUTTON_CLICKED, SEND_MONEY_FROM_DEFAULT, SEND_MONEY_TO_DEFAULT } from 'constants/CleverTapEvents';
import { DEFAULT_AMOUNT, DEFAULT_COUNTRY, DEFAULT_CURRENCY, LANGUAGE } from 'constants/CleverTapParamNames';
import { REQUEST_FORM_NAME, SEND_FORM_NAME } from 'constants/formName';
import { LOGIN } from 'constants/UrlConstants';

import Illustration from './Illustration';
import { Icon } from '../../common/Icon/Icon';

function useClevertap({ commission, countryMenu, lang }) {
    useEventOnFirstRender(() => {
        const {
            from: amountFrom,
            to: amountTo,
            restrictionFrom: { ccyIso: ccyIsoFrom },
            restrictionTo: { ccyIso: ccyIsoTo },
        } = commission;

        const { countryFrom, countryTo } = countryMenu;

        cleverTapEvent(SEND_MONEY_FROM_DEFAULT, {
            [LANGUAGE]: lang,
            [DEFAULT_CURRENCY]: ccyIsoFrom,
            [DEFAULT_COUNTRY]: countryFrom ? countryFrom.code : '',
            [DEFAULT_AMOUNT]: amountFrom,
        });

        cleverTapEvent(SEND_MONEY_TO_DEFAULT, {
            [LANGUAGE]: lang,
            [DEFAULT_CURRENCY]: ccyIsoTo,
            [DEFAULT_COUNTRY]: countryTo ? countryTo.code : '',
            [DEFAULT_AMOUNT]: amountTo,
        });
    });
}

export const CalculatorTempContainer = styled.div`
    @media screen and (min-width: 1352px) {
        width: 46.4286%;
        position: absolute;
        right: 0;
        top: 0;
    }
`;

const CalculatorSection = (props) => {
    useClevertap(props);

    const countryToCode = props.countryMenu.countryTo.code;
    const promoSendHeadline = props.promoSendHeadline;

    const getPromotionUrlBlock = (features, feeLandingInfo, promoAdditionalInfo) => {
        if (promoAdditionalInfo && promoAdditionalInfo !== '') {
            return <p className="smallcopy" dangerouslySetInnerHTML={{ __html: promoAdditionalInfo }} />;
        } else {
            return '';
        }
    };

    return (
        <>
            <div className={`section-calculator image-country_${countryToCode}`} id="calculator">
                <div className="box-1280">
                    <div className="inner-content">
                        <div className="inner-inner-content">
                            <div id="component-headline" className="lead-text  no-spinner">
                                <h1 className="title0">{props.promoSendHeadline}</h1>
                                <p className="title3" dangerouslySetInnerHTML={{ __html: props.promoSendAnnotation }} />

                                {getPromotionUrlBlock(props.features, props.feeLandingInfo, props.messages.promoAdditionalInfo)}
                            </div>
                            <CalculatorTempContainer>
                                <CalculatorForLanding
                                    formName={props.isRequest ? REQUEST_FORM_NAME : SEND_FORM_NAME}
                                    isSend={!props.isRequest}
                                    showDeliveryMethods={false}
                                    localeDependentURI={props.localeDependentURI}
                                    bntCaption={props.getStarted}
                                    href={`${LOGIN}?from_page=${props.isRequest ? 'request' : 'send'}`}
                                    onClick={() => cleverTapEvent(!props.isRequest ? NEW_TRANSFER_BUTTON_CLICKED : null)}
                                    dataTestid="calculator-block-btn-float"
                                />
                            </CalculatorTempContainer>
                            {!props.isMobileBrowser && (
                                <div className="no-spinner" id="landing-block-features">
                                    <ul className="header-features smallcopy">
                                        <li>
                                            <Icon name="check-rounded-thin" color="purple" />
                                            <p className="value">{props.promoSendDelivery}</p>
                                        </li>
                                        <li>
                                            <Icon name="heart" color="purple" />
                                            <p className="value">{props.promoSendSecurity}</p>
                                        </li>
                                        <li>
                                            <Icon name="lifebuoy-thin" color="purple" />
                                            <p className="value">{props.feeLandingReduceFee}</p>
                                        </li>
                                    </ul>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
            {!props.isMobileBrowser && <Illustration countryToCode={countryToCode} promoSendHeadline={promoSendHeadline} />}
        </>
    );
};

const mapStateToProps = ({ messages, calculator, app, language }) => ({
    promoSendHeadline: messages.pageMessages.promoSendHeadline,
    promoSendAnnotation: messages.pageMessages.promoSendAnnotation,
    promoSendDelivery: messages.pageMessages.promoSendDelivery,
    promoSendSecurity: messages.pageMessages.promoSendSecurity,
    getStarted: messages.pageMessages.getStarted,
    feeLandingInfo: messages.pageMessages.feeLandingInfo,
    feeLandingReduceFee: messages.pageMessages.feeLandingReduceFee,
    countryMenu: calculator.countryMenu,
    commission: calculator.commission,
    countryFrom: calculator.countryFrom,
    countryTo: calculator.countryTo,
    messages: messages.pageMessages,
    isMobileBrowser: app.isMobileBrowser,
    lang: language.lang,
    country: app.country,
});

export default connect(mapStateToProps)(CalculatorSection);
