import { Link } from 'react-router-dom';

import styled, { css } from 'styled-components';

import colors from '../../../theme/colors';
import media from '../../../theme/media';

const getColor = (props) => {
    switch (props.variant) {
        case 'secondary':
            return props.disabled ? colors.purpleRgba40 : colors.purple;
        case 'tertiary':
            return props.isInverted ? colors.white : props.disabled && !props.isInverted ? colors.lightGray : colors.baseColor;
        case 'error':
            return props.disabled ? colors.wildWatermelon40 : colors.red;
        case 'row':
            return props.disabled ? colors.purpleRgba40 : colors.purple;
        default:
            return colors.white;
    }
};

const getVisitedLinkColor = (props) => {
    switch (props.variant) {
        case 'secondary':
            return props.disabled ? colors.purpleRgba40 : colors.purple;
        case 'tertiary':
            return props.isInverted ? colors.white : props.disabled && !props.isInverted ? colors.lightGray : colors.baseColor;
        case 'error':
            return props.disabled ? colors.wildWatermelon40 : colors.red;
        case 'row':
            return props.disabled ? colors.purpleRgba40 : colors.purple;
        default:
            return colors.white;
    }
};

const disabledLinearGradient = `linear-gradient(0deg, ${colors.purpleRgba24}, ${colors.purpleRgba24}), ${colors.white}`;

const getBackground = (props) => {
    if (props.variant === 'primary' || !props.variant) {
        return props.disabled ? disabledLinearGradient : colors.purple;
    }
    if (props.variant === 'danger') {
        return props.disabled ? colors.superLightRed : colors.red;
    }
    if (props.isInverted) {
        return 'transparent';
    }
    return colors.white;
};

const getHoveredBackground = (props) => {
    switch (props.variant) {
        case 'secondary':
            return props.disabled ? colors.white : colors.purpleLight08;
        case 'tertiary':
            return props.isInverted ? 'transparent' : props.disabled && !props.isInverted ? colors.white : colors.porcelain;
        case 'error':
            return props.disabled ? colors.white : colors.wildWatermelon08;
        case 'danger':
            return props.disabled ? colors.superLightRed : colors.lightRed;
        case 'row':
            return props.disabled ? colors.white : colors.purpleRgba08;
        default:
            return props.disabled ? disabledLinearGradient : `linear-gradient(0deg, ${colors.whiteRgba24}, ${colors.whiteRgba24}), ${colors.purple}`;
    }
};

const getBoxShadow = (props) => {
    const raised = props.raised && !props.disabled;
    if (props.variant === 'primary' || !props.variant) {
        return raised ? `0px 8px 8px ${colors.purpleRgba16}` : 'none';
    }
    if (props.variant === 'danger') {
        return raised ? `0 8px 24px ${colors.wildWatermelon12}` : 'none';
    }
    return raised ? `0 0 1px ${colors.codGray12}, 0 0 8px ${colors.codGray04}, 0 8px 8px ${colors.codGray04}` : 'none';
};

const getMobilePadding = (props) => {
    if (props.variant === 'row') {
        return props.size === 'tight' ? '16px 24px' : '24px';
    }
    return props.size === 'tight' ? '8px 16px' : '16px 32px';
};
const getDesktopPadding = (props) => {
    if (props.variant === 'row') {
        return props.size === 'tight' ? '20px 32px' : '32px';
    }
    return props.size === 'tight' ? '12px 24px' : '20px 40px';
};
const getMobileRadius = (props) => {
    if (props.variant === 'row') {
        return '0';
    }
    return props.size === 'tight' ? '32px' : '56px';
};
const getDesktopRadius = (props) => {
    if (props.variant === 'row') {
        return '0';
    }
    return props.size === 'tight' ? '56px' : '72px';
};
const getMobileWidth = (props) => {
    return props.variant === 'row' && props.negativeMargin ? 'calc(100% + 48px)' : props.variant === 'row' ? '100%' : 'auto';
};
const getDesktopWidth = (props) => {
    return props.variant === 'row' && props.negativeMargin ? 'calc(100% + 64px)' : props.variant === 'row' ? '100%' : 'auto';
};

const CommonCss = css`
    font-family: GerberaRegular, 'Segoe UI', 'Droid Sans', Roboto, Helvetica, Arial, sans-serif;
    border: none;
    width: ${(props) => getMobileWidth(props)};
    box-sizing: border-box;
    display: ${(props) => (props.variant === 'hidden' ? 'none' : 'inline-block')};
    outline: none;
    text-align: ${(props) => (props.variant === 'row' && !props.textCenter ? 'left' : 'center')};
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    position: relative;
    vertical-align: top;
    cursor: ${(props) => (props.disabled ? 'default' : 'pointer')};
    pointer-events: auto;
    font-size: 17px;
    line-height: 24px;
    border-radius: ${(props) => getMobileRadius(props)};
    padding: ${(props) => getMobilePadding(props)};
    color: ${(props) => getColor(props)};
    background: ${(props) => getBackground(props)};
    box-shadow: ${(props) => getBoxShadow(props)};
    visibility: ${(props) => (props.hidden ? 'hidden' : 'visible')};
    margin: ${(props) => (props.negativeMargin ? '0 -24px' : null)};

    ${media.desktop1920} {
        font-size: 20px;
        line-height: 32px;
        border-radius: ${(props) => getDesktopRadius(props)};
        padding: ${(props) => getDesktopPadding(props)};
        margin: ${(props) => (props.negativeMargin ? '0 -32px' : null)};
        width: ${(props) => getDesktopWidth(props)};
    }

    &:link,
    &:visited,
    &:focus {
        color: ${(props) => getVisitedLinkColor(props)};
    }

    &:hover,
    &:active,
    &:focus {
        background: ${(props) => getHoveredBackground(props)};
        color: ${(props) => (props.variant === 'tertiary' && props.isInverted ? colors.whiteRgba48 : null)};
    }
`;

export const CommonButton = styled.button`
    ${CommonCss}
`;

export const LinkButton = styled.a`
    ${CommonCss}
`;

export const ReactLinkButton = styled(Link)`
    ${CommonCss}
`;
