import React from 'react';

import PropTypes from 'prop-types';

import { Container, ContentContainer, ImgContainer, StepText, TileNote, TileTitle } from './styles';
import { PromoDangerouslyHtml } from '../PromoDangerouslyHtml/PromoDangerouslyHtml';
import PromoIllustration from '../widgets/Illustration/PromoIllustration';

export const CarouselTile = (props) => {
    return (
        <Container bgColor={props.bgColor} variant={props.variant}>
            <>
                {props.img && (
                    <ImgContainer>
                        <PromoIllustration img={props.img} />
                    </ImgContainer>
                )}
                <ContentContainer>
                    {props.title ? (
                        <>
                            {props.title && (
                                <StepText tag="label" variant="label" gradientColor={props.titleGradient}>
                                    {props.title}
                                </StepText>
                            )}
                            {props.mainTitle && (
                                <TileTitle tag="h3" variant="h3" color="gray80">
                                    {props.mainTitle}
                                </TileTitle>
                            )}
                            {props.subtitle && <PromoDangerouslyHtml tag="div" variant="subheader" color="gray40" dangerouslyHtml={props.subtitle} />}
                            {props.note && (
                                <TileNote tag="div" variant="note" color="gray40">
                                    {props.note}
                                </TileNote>
                            )}
                        </>
                    ) : (
                        <>
                            {props.mainTitle && (
                                <TileTitle tag="div" variant="div" color="gray40">
                                    {props.mainTitle}
                                </TileTitle>
                            )}
                            {props.subtitle && <PromoDangerouslyHtml tag="h3" variant="h3" color="gray80" dangerouslyHtml={props.subtitle} />}
                        </>
                    )}
                </ContentContainer>
            </>
        </Container>
    );
};

CarouselTile.defaultProps = {
    bgColor: 'white',
    titleGradient: 'passionFruit01',
};

CarouselTile.propTypes = {
    bgColor: PropTypes.oneOf([
        'baseColor',
        'white',
        'primary',
        'primaryB2B',
        'purple80',
        'purple40',
        'purple20',
        'purple5',
        'gray80',
        'gray40',
        'gray20',
        'gray5',
        'aubergine01',
        'aubergine02',
        'aubergine03',
        'eggshell01',
        'eggshell02',
        'eggshell03',
        'passionFruit01',
        'passionFruit02',
        'passionFruit03',
        'oceanBlue01',
        'oceanBlue02',
        'oceanBlue03',
        'blueberryBlue01',
        'blueberryBlue02',
        'blueberryBlue03',
        'jade01',
        'jade02',
        'jade03',
    ]),
    img: PropTypes.array,
    title: PropTypes.string,
    titleGradient: PropTypes.oneOf([
        'aubergine01',
        'aubergine02',
        'aubergine03',
        'passionFruit01',
        'passionFruit02',
        'passionFruit03',
        'oceanBlue01',
        'oceanBlue02',
        'oceanBlue03',
        'blueberryBlue01',
        'blueberryBlue02',
        'blueberryBlue03',
        'jade01',
        'jade02',
        'jade03',
    ]),
    mainTitle: PropTypes.string,
    subtitle: PropTypes.string,
};
