import React from 'react';

import PropTypes from 'prop-types';

import { DiskBadge } from '../../../../common/DiskBadge/DiskBadge';
import { FlagIcon } from '../../../../common/FlagIcon/FlagIcon';
import { Panel } from '../../../../common/Panel/Panel';
import { IconWrapper, PanelRightBlock, PanelRow, PanelText, SelectedDiskBadge } from '../../../../common/Panel/styles';
import { Text } from '../../../../common/Text/Text';

export const CountryItemPanel = (props) => {
    return (
        <Panel onClick={props.onClick} type={props.to ? 'reactLink' : 'div'} to={props.to} isHoverable negativeMargin>
            <PanelRow>
                <PanelRow alignStart>
                    <IconWrapper>
                        {props.getCurrencyIcon ? (
                            <DiskBadge src={props.getCurrencyIcon(props.flagIconName)} alt={props.flagIconName} onError={props.onError} />
                        ) : (
                            <FlagIcon name={props.flagIconName} size="small" />
                        )}
                        {props.isSelectedCountry && (
                            <SelectedDiskBadge>
                                <DiskBadge variant="selectedPassive" iconName="check" />
                            </SelectedDiskBadge>
                        )}
                    </IconWrapper>
                    <PanelText>{props.countryName}</PanelText>
                </PanelRow>
                {props.showCurrency && (
                    <PanelRightBlock>
                        <Text color="mediumGray" smallCaps>
                            {props.countryCurrency || props.countryPrefix}
                        </Text>
                    </PanelRightBlock>
                )}
            </PanelRow>
        </Panel>
    );
};

CountryItemPanel.defaultProps = {
    showCurrency: true,
};

CountryItemPanel.propTypes = {
    onClick: PropTypes.func,
    to: PropTypes.string,
    flagIconName: PropTypes.string,
    countryName: PropTypes.string,
    countryCurrency: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    countryPrefix: PropTypes.string,
    isSelectedCountry: PropTypes.bool,
    getCurrencyIcon: PropTypes.func,
};
