import React from 'react';

import PropTypes from 'prop-types';

import { FlagIcon } from 'components/common/FlagIcon/FlagIcon';

import { resolveArguments } from 'helpers/utils';

import { CountryName, FlagWrapper } from './styles';

const GeoCountry = (props) => {
    const country = props.country;
    const countryFrom = props.countryFrom;
    const countryTo = props.countryTo;

    let uri = props.isRequest
        ? resolveArguments(props.uri, [country.seoNameFrom, countryTo.seoNameTo])
        : resolveArguments(props.uri, [countryFrom.seoNameFrom, country.seoNameTo]);

    return (
        <li>
            <a href={uri}>
                <FlagWrapper>
                    <FlagIcon name={country.code} size={'24px'} />
                </FlagWrapper>
                <CountryName ellipsis isPromo nowrap size={'main'}>
                    {country.name}
                </CountryName>
            </a>
        </li>
    );
};
GeoCountry.propTypes = {
    country: PropTypes.shape({
        name: PropTypes.string.isRequired,
        seoNameTo: PropTypes.string,
        seoNameFrom: PropTypes.string,
        code: PropTypes.string.isRequired,
        paySystems: PropTypes.array.isRequired,
    }).isRequired,
    countryFrom: PropTypes.shape({
        name: PropTypes.string.isRequired,
        seoNameTo: PropTypes.string,
        seoNameFrom: PropTypes.string,
        code: PropTypes.string.isRequired,
        paySystems: PropTypes.array.isRequired,
    }).isRequired,
    onlyMessages: PropTypes.shape({
        debit: PropTypes.string.isRequired,
        mastercard: PropTypes.string.isRequired,
        visa: PropTypes.string.isRequired,
        unionpay: PropTypes.string.isRequired,
    }).isRequired,
    uri: PropTypes.string.isRequired,
};
export default GeoCountry;
