import React from 'react';
import { useSSR } from 'react-i18next';
import { Provider } from 'react-redux';
import { BrowserRouter } from 'react-router-dom';

import 'components/i18n';
import { ThemeProvider } from 'styled-components';

import getStore from 'redux/store';

import ErrorBoundary from 'components/utils/ErrorBoundary';

import { hydrateRoot } from 'react-dom/client';
import 'static/css/icons.css';
import 'static/css/promo.css';
import 'static/css/main_fix.css';
import 'static/css/media-quotes.css';
import 'pure-react-carousel/dist/react-carousel.es.css';
import promoRoutes from 'routes/promoRoutes';

import App from '../components/App/App';
import { getShortActiveRoute } from '../routes/routesNoAuthUtils';
import { isShortActiveUrl, pushPath } from '../routes/routesUtils';
import theme from '../theme';

const preloadedState = window.__INITIAL_STATE__;
delete window.__INITIAL_STATE__;
const store = getStore(preloadedState);
const state = store.getState();
const isShortLink = isShortActiveUrl(location.pathname);

const preprocessRoutes = isShortLink ? getShortActiveRoute(location.pathname) : pushPath(promoRoutes, []);

const PromoHydryte = (props) => {
    useSSR(window.initialI18nStore, window.initialLanguage);
    return (
        <BrowserRouter>
            <ThemeProvider theme={theme}>
                <Provider store={store}>
                    <ErrorBoundary>
                        <App authUser={state.auth.authUser} redirectTo={state.app.redirectTo} routes={preprocessRoutes} />
                    </ErrorBoundary>
                </Provider>
            </ThemeProvider>
        </BrowserRouter>
    );
};
const container = document.getElementById('app');
hydrateRoot(container, <PromoHydryte />);
