import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import { PromoText } from 'components/no-auth/common/PromoText/PromoText';

import { togglePopup } from 'actions/appAction';

import { SELECT_REGION } from 'constants/UrlConstants';

import { getLangCountryPrefix } from 'helpers/utils';

import promoColors from 'theme/promoColors';

import { FlagIcon } from '../../common/FlagIcon/FlagIcon';

const Region = styled(PromoText)`
    white-space: nowrap;
    display: flex;
    align-items: center;
    padding: 10px 0;
    color: ${promoColors.gray80};

    &:link,
    &:visited {
        color: ${promoColors.gray80};
    }

    &:hover {
        color: ${promoColors.gray40};
    }
`;

const CountryName = styled.span`
    display: inline-block;
    margin-left: 8px;
`;

const FooterCountry = (props) => {
    const { countryCode, countryName, country } = props;
    return (
        <Region tag="link" href={`${getLangCountryPrefix(props.lang, country)}${SELECT_REGION}`} data-testid="footer-country-link-region">
            <FlagIcon name={countryCode} size="24px" />
            <CountryName>{countryName}</CountryName>
        </Region>
    );
};

FooterCountry.propTypes = {
    lang: PropTypes.string.isRequired,
    country: PropTypes.string.isRequired,
    countryCode: PropTypes.string.isRequired,
    countryName: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
    return {
        lang: state.language.lang,
    };
};

export default connect(mapStateToProps, { togglePopup })(FooterCountry);
