export const getStrapiField = (
    name = 'name',
    value = '',
    items = '',
    checkRegexp = '',
    mandatory = false,
    isError = false,
    placeholder = '',
    type = 'text',
    visible = true,
    order = null,
    strapiField = true,
) => {
    return {
        name: name,
        value: value,
        items: items,
        checkRegexp: checkRegexp,
        mandatory: mandatory,
        isError: isError,
        placeholder: placeholder,
        type: type,
        visible: visible,
        strapiField: strapiField,
        order: order,
    };
};
