import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import { Accordion } from 'components/no-auth/common/Accordion/Accordion';
import { PromoDangerouslyHtml } from 'components/no-auth/common/PromoDangerouslyHtml/PromoDangerouslyHtml';
import { PromoSectionContainer } from 'components/no-auth/common/PromoSectionContainer';
import { PromoText } from 'components/no-auth/common/PromoText/PromoText';

import promoColors from 'theme/promoColors';
import promoMedia from 'theme/promoMedia';

import HeadingSection from './HeadingSection';
import { TextComponent } from './styles';

const Container = styled.section`
    background: ${(props) => promoColors[props.bgColor]};
    padding: 32px 0 64px;

    ${promoMedia.tablet744} {
        padding: 48px 0 96px;
    }

    ${promoMedia.desktop1440} {
        padding: 64px 0 128px;
    }
`;

const FaqSection = (props) => {
    const getItems = () => {
        return props.questions.map((q) => ({
            header: <PromoText color={'baseColor'}>{q.title}</PromoText>,
            content: (
                <TextComponent>
                    <PromoDangerouslyHtml color={'baseColor'} dangerouslyHtml={q.text} tag={'div'} />
                </TextComponent>
            ),
        }));
    };

    return (
        <Container bgColor={props.bgColor}>
            <PromoSectionContainer>
                <HeadingSection title={props.title} noBottomIndent />
                {Array.isArray(props.questions) && <Accordion items={getItems()} />}
            </PromoSectionContainer>
        </Container>
    );
};
FaqSection.defaultProps = {
    bgColor: 'white',
};
FaqSection.propTypes = {
    bgColor: PropTypes.oneOf(['baseColor', 'white', 'purple5', 'gray5', 'aubergine01', 'eggshell01', 'eggshell02', 'eggshell03', 'transparent']),
    questions: PropTypes.array,
    title: PropTypes.string,
};
export default FaqSection;
