import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import promoColors from '../../../../theme/promoColors';
import promoMedia from '../../../../theme/promoMedia';
import { PromoIcon } from '../PromoIcon/PromoIcon';
import { PromoText } from '../PromoText/PromoText';

const IconWrap = styled.span`
    display: inline-block;
    margin: 0 4px;
    position: relative;
    top: 3px;
    width: 20px;
    height: 20px;

    ${promoMedia.desktop1440} {
        top: 6px;
    }
`;
const Container = styled.div`
    padding: 24px 0;
    display: flex;
    align-items: center;

    a {
        display: inherit;

        &:hover {
            ${IconWrap} svg {
                --accent: ${promoColors.purple40};
            }
        }
    }
`;

export const Breadcrumbs = (props) => {
    return (
        <Container>
            <PromoText tag="link" variant="subheader" href={props.firstItemLink} nowrap>
                {props.firstItemCaption}
                <IconWrap>
                    <PromoIcon name="chevron-right" color="primary" />
                </IconWrap>
            </PromoText>
            <PromoText variant="subheader" color="gray80" ellipsis>
                {props.activeItemCaption}
            </PromoText>
        </Container>
    );
};

Breadcrumbs.propTypes = {
    firstItemCaption: PropTypes.string,
    firstItemLink: PropTypes.string,
    activeItemCaption: PropTypes.string,
};
