import React, { useState } from 'react';
import { connect } from 'react-redux';

import styled from 'styled-components';

import { togglePopup } from '../../../actions/appAction';
import { Row } from '../Layout/Row';
import ComplexPopup from '../Popup/ComplexPopup';

const Image = styled.img`
    max-width: 100%;
`;

const FileUploadPopup = (props) => {
    const [imageUrl, setImageUrl] = useState(null);

    const reader = new FileReader();
    reader.onload = function () {
        setImageUrl(reader.result);
    };
    reader.readAsDataURL(props.file);
    return (
        <ComplexPopup
            primaryButtonText={props.send}
            onPrimaryButtonClick={props.uploadFile}
            secondaryButtonText={props.cancel}
            onSecondaryButtonClick={() => props.togglePopup(false, {})}
        >
            <Row flex justify="center">
                <Image src={imageUrl} alt={props.file?.name} />
            </Row>
        </ComplexPopup>
    );
};

const mapStateToProps = (state) => {
    return {
        send: state.messages?.jsMessages['chat.js.message.upload.send'],
        cancel: state.messages?.jsMessages['chat.js.message.upload.cancel'],
    };
};

export default connect(mapStateToProps, { togglePopup })(FileUploadPopup);
