import React from 'react';
import { connect } from 'react-redux';

import ColoredSectionContainer from 'components/no-auth/common/ColoredSectionContainer';
import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { PromoSectionContainer } from 'components/no-auth/common/PromoSectionContainer';
import FaqSection from 'components/no-auth/common/sections/FaqSection';
import HeadingSection from 'components/no-auth/common/sections/HeadingSection';
import { HeroSection } from 'components/no-auth/common/sections/HeroSection';
import { HighlightedTilesSection } from 'components/no-auth/common/sections/HighlightedTilesSection';
import { ListingTilesSection } from 'components/no-auth/common/sections/ListingTilesSection';
import { PaymentSystemLogos } from 'components/no-auth/common/sections/PaymentSystemLogos';
import { QuoteSectionV2 } from 'components/no-auth/common/sections/QuoteSectionV2';
import { StepBlockSection } from 'components/no-auth/common/sections/StepBlockSection';
import { TilesSection } from 'components/no-auth/common/sections/TilesSection';
import VideoSectionV2 from 'components/no-auth/common/sections/VideoSectionV2';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

const BusinessAcquiringPageV2 = (props) => {
    return (
        <PromoPageContainer bgColor="eggshell02" noIndent>
            {props.heroTitle && (
                <HeroSection
                    title={props.heroTitle}
                    text={props.heroText}
                    img={props.heroImg}
                    btnVariant="primary"
                    noIconBtn
                    horizontalLayout
                    data={props.heroData}
                    fetchPriorityHigh
                />
            )}
            {props.videoUrl && (
                <VideoSectionV2
                    sectionTitle={props.videoTitle}
                    btn={props.videoBtn}
                    videoUrl={props.videoUrl}
                    videoTitle={props.videoTitle}
                    videoPreviewImg={props.videoPreviewImg}
                    hasTopPadding
                />
            )}
            <PromoSectionContainer>
                {props.headline1Title && <HeadingSection title={props.headline1Title} text={props.headline1Text} />}
                {props.tilesHighlighted && <HighlightedTilesSection data={props.tilesHighlighted} />}
                {props.acceptPaymentsTitle && (
                    <HeadingSection title={props.acceptPaymentsTitle} text={props.acceptPaymentsText} btn={props.acceptPaymentsBtn} />
                )}
                {props.logos && <PaymentSystemLogos logos={props.logos} topIcon={props.topIcon} />}
            </PromoSectionContainer>

            <ColoredSectionContainer bgColor="purple5" bottomPadding>
                {props.benefitsTitle && <HeadingSection title={props.benefitsTitle} text={props.benefitsText} btn={props.benefitsBtn} />}
                {props.benefits && <HighlightedTilesSection data={props.benefits} />}
            </ColoredSectionContainer>

            <ColoredSectionContainer>
                {props.headline2Title && <HeadingSection title={props.headline2Title} text={props.headline2Text} />}
                {props.cards && <TilesSection lang={props.lang} country={props.country} data={props.cards} loadingLazy />}
                {props.headline3Title && <HeadingSection title={props.headline3Title} text={props.headline3Text} />}
                {props.teamCards && <ListingTilesSection data={props.teamCards} loadingLazy />}
            </ColoredSectionContainer>

            <ColoredSectionContainer>
                {props.quote_section && <QuoteSectionV2 data={props.quote_section} />}
                {props.onboardingTitle && <HeadingSection title={props.onboardingTitle} text={props.onboardingText} />}
                {props.steps && <StepBlockSection data={props.steps} />}
            </ColoredSectionContainer>

            {props.faqTitle && props.questions && <FaqSection title={props.faqTitle} questions={props.questions} bgColor="white" />}

            {props.acquisitionTitle && (
                <ColoredSectionContainer bgColor="white" bottomPadding>
                    <HeadingSection
                        text={props.acquisitionText}
                        title={props.acquisitionTitle}
                        btn={props.acquisitionBtn}
                        btnMobile={props.acquisitionBtnMobile}
                        isMobileBtn={props.acquisitionIsMobileBtn}
                        sendFrom={'Acquiring page'}
                        noBottomIndent
                    />
                </ColoredSectionContainer>
            )}
        </PromoPageContainer>
    );
};
const mapStateToProps = (state) => {
    return {
        heroTitle: state.extra?.strapiApi?.hero_block?.title,
        heroText: state.extra?.strapiApi?.hero_block?.text,
        heroImg: state.extra?.strapiApi?.hero_block?.img,
        heroData: state.extra?.strapiApi?.hero_block,

        videoTitle: state.extra?.strapiApi?.video_section?.title,
        videoUrl: state.extra?.strapiApi?.video_section?.url,
        videoBtn: state.extra?.strapiApi?.video_section?.btn,
        videoPreviewImg: state.extra?.strapiApi?.video_section?.img,

        headline1Title: state.extra?.strapiApi?.headline1?.title,
        headline1Text: state.extra?.strapiApi?.headline1?.text,

        tilesHighlighted: state.extra?.strapiApi?.tiles_highlighted,

        acceptPaymentsTitle: state.extra?.strapiApi?.accept_payments_headline?.title,
        acceptPaymentsText: state.extra?.strapiApi?.accept_payments_headline?.text,
        acceptPaymentsBtn: state.extra?.strapiApi?.accept_payments_headline?.btn,

        logos: state.extra?.strapiApi?.cards_icons,
        topIcon: state.extra?.strapiApi?.cards_icons2,

        benefitsTitle: state.extra?.strapiApi?.benefits_headline?.title,
        benefitsText: state.extra?.strapiApi?.benefits_headline?.text,
        benefitsBtn: state.extra?.strapiApi?.benefits_headline?.btn,
        benefitsBtnMobile: state.extra?.strapiApi?.benefits_headline?.btn_mobile,
        benefitsBtnIsMobileBtn: state.extra?.strapiApi?.benefits_headline?.isMobileBtn,
        benefits: state.extra?.strapiApi?.benefits_highlighted_tiles,

        headline2Title: state.extra?.strapiApi?.headline2.title,
        headline2Text: state.extra?.strapiApi?.headline2.text,

        cards: state.extra?.strapiApi?.tiles_section,

        headline3Title: state.extra?.strapiApi?.headline3?.title,
        headline3Text: state.extra?.strapiApi?.headline3?.text,

        teamCards: state.extra?.strapiApi?.team_section,

        quote_section: state.extra?.strapiApi?.quote_section,

        onboardingTitle: state.extra?.strapiApi?.onboarding_headline?.title,
        onboardingText: state.extra?.strapiApi?.onboarding_headline?.text,
        steps: state.extra?.strapiApi?.onboarding_steps_block,

        acquisitionTitle: state.extra?.strapiApi?.acquisition_headline?.title,
        acquisitionText: state.extra?.strapiApi?.acquisition_headline?.text,
        acquisitionBtn: state.extra?.strapiApi?.acquisition_headline?.btn,
        acquisitionBtnMobile: state.extra?.strapiApi?.acquisition_headline?.btn_mobile,
        acquisitionIsMobileBtn: state.extra?.strapiApi?.acquisition_headline?.isMobileBtn,

        faqTitle: state.extra?.strapiApi?.faq?.title,
        questions: state.extra?.strapiApi?.faq?.questions,

        lang: state.extra.noAuthFooter?.language?.lang,
        country: state.extra.noAuthFooter?.language?.country,
    };
};
export default connect(mapStateToProps)(NotAuthBasePage(BusinessAcquiringPageV2));
