import React, { useRef } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import FileUploadPopup from './FileUploadPopup';
import { ImageContainer, InputContainer, ReplaceContainer } from './styles';
import { addErrorMessageBody, addOfakimImg, togglePopup, toggleSpinner } from '../../../actions/appAction';
import { changeFormFieldValue } from '../../../actions/formAction';
import { OFAKIM_FILE_DIR, OFAKIM_SEND_FILE } from '../../../constants/UrlConstants';
import { apiFetchFormData } from '../../../helpers/api';
import { getField } from '../../form-multi/utils';
import { Icon } from '../Icon/Icon';
import { Row } from '../Layout/Row';
import PopupButton from '../Popup/PopupButton';
import SimplePopup from '../Popup/SimplePopup';
import { Text } from '../Text/Text';

const AddPhoto = (props) => {
    const inputFile = useRef(null);
    const file = useRef(null);

    const clearFieldEvent = (e) => {
        e.preventDefault();
        props.togglePopup(
            true,
            <SimplePopup withoutBorder>
                <PopupButton
                    iconName="cross"
                    text={props.removePhoto}
                    onClick={() => {
                        props.changeFormFieldValue(props.formName, '', props.field.name);
                        props.togglePopup(false);
                    }}
                />
            </SimplePopup>,
        );
    };

    const onChangeHandler = (e) => {
        e.preventDefault();
        e.stopPropagation();
        file.current = e.target.files[0];
        const size = file.current.size || file.current.fileSize;
        if (size > 5242880) {
            // 5MB
            console.log('Max file size : 5Mb');
            props.addErrorMessageBody('Max file size : 5Mb');
            return;
        }
        props.togglePopup(true, <FileUploadPopup file={file.current} uploadFile={uploadFile} />);
        e.target.value = null;
    };

    const uploadFile = (e) => {
        e.preventDefault();
        props.togglePopup(false);
        props.toggleSpinner(true);
        apiFetchFormData(OFAKIM_SEND_FILE, { file: getField('file', file.current) })
            .then((json) => {
                if (json.result === 'OK') {
                    props.togglePopup(false, {});
                    props.changeFormFieldValue(props.formName, json.imgUrl, props.field.name);
                } else if (json.result === 'ERROR') {
                    props.addErrorMessageBody(json.message);
                }
            })
            .catch((e) => {
                console.warn(e);
            })
            .finally(() => {
                file.current = null;
                props.toggleSpinner(false);
            });
    };

    return (
        <>
            <InputContainer imgUrl={props.field.value}>
                <Row
                    indent="16px"
                    onClick={(e) => {
                        e.stopPropagation();
                        inputFile.current.click();
                    }}
                >
                    <Icon name="plus" color="purple" />
                </Row>
                {props.field.info}
                <input type="file" name="file" className="hidden" onChange={onChangeHandler} ref={inputFile} />
            </InputContainer>
            <ImageContainer dir={OFAKIM_FILE_DIR} imgUrl={props.field.value} onClick={clearFieldEvent}>
                <ReplaceContainer>
                    <Row indent="16px">
                        <Icon name="retry" color="white" />
                    </Row>
                    <Text size="main" color="white">
                        {props.replacePhoto}
                    </Text>
                </ReplaceContainer>
            </ImageContainer>
        </>
    );
};

AddPhoto.propTypes = {
    onClick: PropTypes.func,
};
const mapStateToProps = (state) => {
    return {
        removePhoto: state.messages.pageMessages.removePhoto,
        replacePhoto: state.messages.pageMessages.replacePhoto,
    };
};

export default connect(mapStateToProps, { addErrorMessageBody, changeFormFieldValue, togglePopup, addOfakimImg, toggleSpinner })(AddPhoto);
