import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import { LANDING_LINK } from '../../../constants/UrlConstants';
import { resolveArguments } from '../../../helpers/utils';
import { InfoPage } from '../../common/Layout/InfoPage/InfoPage';

const NotFoundInfo = (props) => {
    const getErrorText = resolveArguments(props.errorText, props.billId);

    return (
        <InfoPage
            iconName="cross-rounded-thin"
            iconColor="red"
            headerText={getErrorText}
            primaryButtonCaption={props.homePage}
            primaryButtonType="link"
            primaryButtonLink={LANDING_LINK}
        />
    );
};
const mapStateToProps = (state) => {
    return {
        errorText: state.extra?.strapiApi?.errorText,
        homePage: state.extra?.strapiApi?.homePage,
        billId: state.extra?.strapiApi?.billId,
    };
};
NotFoundInfo.propTypes = {
    errorText: PropTypes.string.isRequired,
    homePage: PropTypes.string.isRequired,
};

export default connect(mapStateToProps)(NotFoundInfo);
