import { connect } from 'react-redux';

import styled from 'styled-components';

import RequestAmountFrom from 'components/calculator/Amount/RequestAmountFrom';
import Form from 'components/form-multi/Form';
import Loading from 'components/utils/Loading';

import { togglePopup } from 'actions/appAction';
import { updateCalculatorFieldValue } from 'actions/calculatorAction';
import { changeFormFieldValue, setNewForm } from 'actions/formAction';

import promoMedia from 'theme/promoMedia';

import Amount from './Amount/Amount';
import RequestAmountTo from './Amount/RequestAmountTo';
import FeeBlock from './FeeBlock/FeeBlock';
import { AMOUNT_FROM, AMOUNT_TO } from '../../constants/fieldsName';
import { CalculatorLandingService } from '../../services/calculatorLandingService';
import colors from '../../theme/colors';
import { PromoButton } from '../no-auth/common/PromoButton/PromoButton';
import TextPopup from '../sharedComponents/popups/TextPopup';

const getFormPropName = (isSend) => (isSend ? 'paymentForm' : 'requestForm');

const getInfoPropName = (isSend) => (isSend ? 'paymentInfo' : 'requestInfo');

function useDebounce(value, deps) {
    const [debouncedValue, setDebouncedValue] = useState(value);

    useEffect(() => {
        const handler = setTimeout(() => {
            setDebouncedValue(value);
        }, 300);

        return () => {
            clearTimeout(handler);
        };
    }, [value, ...deps]);

    return debouncedValue;
}

function useCalc(isSpinner, callbackCalcFunc) {
    const [state, setState] = useState({});

    const flag = useDebounce(state.value, [state.value, state.isFrom]);

    useEffect(() => {
        if (state.value !== undefined && state.isFrom !== undefined && flag !== undefined && isSpinner !== true) {
            callbackCalcFunc(state.isFrom);
        }
    }, [flag, state.isFrom]);

    return (isFrom, value) => setState({ isFrom, value });
}

