const CalculatorForLanding = (props) => {
    const formRef = useRef();
    const helpForm = props[getFormPropName(props.isSend)];
    const [oldFeeText, setOldFeeText] = useState('');

    const calculatorLandingService = new CalculatorLandingService(
        props.isSend,
        props.formName,
        props.commission,
        helpForm,
        () => props.togglePopup(true, <TextPopup withoutBorder text={props.tariffErrorText} buttonText={props.gotIt} />),
        props.updateCalculatorFieldValue,
        props.changeFormFieldValue,
        { defaultFeeText: oldFeeText },
    );

    const [isChangingFrom, setChangingFrom] = useState(false);
    const [isChangingTo, setChangingTo] = useState(false);
    const [fee, setFee] = useState(props.feeAmount);

    const calcFunc = useCalc(props.isShowSpinner, (isFrom) => {
        const value = props.fields[isFrom ? AMOUNT_FROM : AMOUNT_TO]?.value;
        const currentVal = value ? value.toString().replace(/\s/g, '') : '';
        calculatorLandingService.calcCommission(isFrom, setFee, currentVal);
    });

    useEffect(() => {
        setOldFeeText(helpForm.feeText);
    }, [props.isSend]);

    useEffect(() => {
        const fromAmount = props.fields[helpForm?.fromAmount?.name]?.value;
        stopCalculate(true);
        calcFunc(true, fromAmount);
    }, [helpForm?.fromAmount]);

    useEffect(() => {
        setFee(props.feeAmount);
    }, [props.feeAmount]);

    if (!helpForm) {
        return <Loading />;
    }

    const amountFromChangeHandler = (value) => {
        if (!props.isShowSpinner && !isChangingTo) {
            calcFunc(true, value);
        }
    };

    const amountToChangeHandler = (value) => {
        if (!props.isShowSpinner && !isChangingFrom) {
            calcFunc(false, value);
        }
    };

    const amountToChangeHandlerRequest = (value) => amountToChangeHandler(value);

    const { countryTo, countryFrom } = props.countryMenu;

    const startCalculateFrom = () => {
        setChangingTo(false);
        setChangingFrom(true);
    };

    const startCalculateTo = () => {
        setChangingFrom(false);
        setChangingTo(true);
    };

    const stopCalculate = (value = false) => {
        setChangingFrom(value);
        setChangingTo(value);
    };

    const renderSend = () => (
        <>
            <CalculatorRow>
                <Amount
                    onFocusHandler={startCalculateFrom}
                    onBlurHandler={stopCalculate}
                    isFrom={true}
                    isSend={props.isSend}
                    isLanding={true}
                    formName={props.formName}
                    amountFieldBean={helpForm.fromAmount}
                    localeDependentURI={props.localeDependentURI}
                    countryBean={countryFrom}
                    countryTo={countryTo}
                    countryFrom={countryFrom}
                    enabledValidateMinMaxAmount={false}
                    countryList={props.countryFromList}
                    onChangeHandler={amountFromChangeHandler}
                />
            </CalculatorRow>
            <CalculatorRow>
                <Amount
                    onFocusHandler={startCalculateTo}
                    onBlurHandler={stopCalculate}
                    isFrom={false}
                    isSend={props.isSend}
                    isLanding={true}
                    localeDependentURI={props.localeDependentURI}
                    formName={props.formName}
                    amountFieldBean={helpForm.toAmount}
                    countryBean={countryTo}
                    countryTo={countryTo}
                    countryFrom={countryFrom}
                    enabledValidateMinMaxAmount={false}
                    countryList={props.countryToList}
                    onChangeHandler={amountToChangeHandler}
                />
            </CalculatorRow>
        </>
    );

    const renderRequest = () => (
        <>
            <CalculatorRow>
                <RequestAmountTo
                    isFrom={false}
                    isSend={props.isSend}
                    isLanding={true}
                    localeDependentURI={props.localeDependentURI}
                    formName={props.formName}
                    amountFieldBean={helpForm.toAmount}
                    countryBean={countryTo}
                    countryTo={countryTo}
                    countryFrom={countryFrom}
                    enabledValidateMinMaxAmount={false}
                    countryList={props.countryToList}
                    onChangeHandler={amountToChangeHandlerRequest}
                />
            </CalculatorRow>
            <CalculatorRow>
                <RequestAmountFrom
                    formName={props.formName}
                    localeDependentURI={props.localeDependentURI}
                    isLanding={true}
                    countryTo={countryTo}
                    countryBean={countryFrom}
                    countryList={props.countryFromList}
                />
            </CalculatorRow>
        </>
    );

    return (
        <CalculatorBlock>
            <Form id="precal_payment_form_id" formName={props.formName} forwardedRef={formRef}>
                {props.isSend ? renderSend() : renderRequest()}
            </Form>
            <FeeBlock
                paymentForm={helpForm}
                feeAmount={fee}
                fromIso={props.fromIso}
                formName={props.formName}
                isLandingRequest={!props.isSend}
                countryToId={countryTo.id}
                fields={props.fields}
                amountFieldBean={helpForm.fromAmount}
                canUsePromoCode={props.canUsePromoCode}
                canUseInviteCode={props.canUseInviteCode}
                isLanding={true}
            />
            <ButtonContainer>
                <PromoButton caption={props.bntCaption} type="link" href={props.href} onClick={props.onClick} data-testid={props.dataTestid} />
            </ButtonContainer>
        </CalculatorBlock>
    );
};

CalculatorForLanding.defaultProps = {
    showDeliveryMethods: true,
};

const mapStateToProps = ({ calculator, form, send, app, messages }, ownProps) => ({
    fields: form[ownProps.formName].fields,
    feeAmount: calculator?.commission?.fee,
    fromIso: calculator?.commission?.restrictionFrom?.ccyIso,
    canUsePromoCode: send.canUsePromoCode,
    canUseInviteCode: send.canUseInviteCode,
    isShowSpinner: app.isShowSpinner,
    [getFormPropName(ownProps.isSend)]: calculator[getFormPropName(ownProps.isSend)],
    [getInfoPropName(ownProps.isSend)]: calculator[getInfoPropName(ownProps.isSend)],
    countryMenu: calculator.countryMenu,
    countryToList: calculator.countryTo,
    countryFromList: calculator.countryFrom,
    commission: calculator.commission,
    tariffErrorText: messages.pageMessages?.tariffErrorText,
    gotIt: messages.pageMessages?.gotIt,
});
export default connect(mapStateToProps, {
    setNewForm,
    changeFormFieldValue,
    updateCalculatorFieldValue,
    togglePopup,
})(CalculatorForLanding);
