import { UPDATE_AUTH, UPDATE_BONUS_BALANCE } from 'actions/authAction';

import update from 'helpers/Immutability';

const auth = (state = {}, action) => {
    let mergeState;
    const data = action.data;
    switch (action.type) {
        case UPDATE_AUTH:
            mergeState = update(state, {
                $auto: { $set: data },
            });
            break;
        case UPDATE_BONUS_BALANCE:
            mergeState = update(state, {
                $auto: {
                    bonusAmount: { $set: data.bonusAmount },
                },
            });
            break;
        default:
            return state;
    }
    return mergeState;
};
export default auth;
