export const isShortActiveUrl = (url = '') => {
    const regexp = new RegExp(`^/(ml|l|b|new/b)-`);
    return regexp.test(url);
};

export const pushPath = (sourceRoutes = [], routs) => {
    sourceRoutes.forEach((r) => {
        if (Array.isArray(r.path)) {
            r.path.forEach((path) => {
                routs.push({ ...r, path: path });
            });
        } else {
            routs.push(r);
        }
    });
    return routs;
};
export const getLangAndCountryFromUrl = (url = '', userLangCookie, countryByIpA2Code) => {
    let lang = 'en';
    let country = countryByIpA2Code;

    const langURI = getLanguage(url);

    if (langURI?.length === 2) {
        lang = langURI;
    } else if (checkPrefix('_', langURI)) {
        const arr = langURI.split('_');
        lang = arr[0];
        country = arr[1] || countryByIpA2Code;
    } else if (checkPrefix('-', langURI)) {
        const arr = langURI.split('-');
        lang = arr[0];
        country = arr[1] || countryByIpA2Code;
    } else if (userLangCookie?.length === 5) {
        const arr = userLangCookie.split('-');
        lang = arr[0];
        country = arr[1] || countryByIpA2Code;
    }

    return { lang: lang, country: country };
};

const checkPrefix = (prefix, lang) => {
    return lang?.length === 5 && lang?.includes(prefix);
};
const getLanguage = (referrer) => {
    if (!referrer) return null;

    // Remove "/api" if it's present at the beginning of the referrer
    const cleanedReferrer = referrer.startsWith('/api') ? referrer.slice(4) : referrer;

    const language = cleanedReferrer.split('/')[1];
    return language || null;
};
