import React from 'react';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import { CarouselProvider, DotGroup, Slide, Slider } from 'pure-react-carousel';

import { WAIT_BEFORE_SLIDES_IS_ACTIVE } from './Constants';
import { CarouselDotGroup } from './styles';
import promoMedia from '../../../../theme/promoMedia';
import { CarouselTile } from '../CarouselTile/CarouselTile';

const Container = styled.div`
    margin: -24px -20px 32px -20px;

    ${promoMedia.tablet744} {
        margin: -24px -20px 48px -20px;
    }

    ${promoMedia.desktop1440} {
        margin: -24px -20px 64px -20px;
    }
`;

const CarouselTileSlide = styled.div`
    cursor: grab;
    padding: 24px;
    min-height: 100%;
    width: 100%;
    box-sizing: border-box;
    display: flex;
`;

export const CarouselTilesSection = (props) => {
    const hasData = props.data && Array.isArray(props.data);
    const totalSlides = hasData ? props.data?.length : null;
    return (
        <Container>
            <CarouselProvider
                interval={WAIT_BEFORE_SLIDES_IS_ACTIVE}
                naturalSlideHeight="100%"
                naturalSlideWidth="100%"
                totalSlides={totalSlides}
                isPlaying={!__isDev__}
                isIntrinsicHeight
            >
                <Slider>
                    {hasData &&
                        props.data
                            .filter((item) => item !== null)
                            .map((item, index) => {
                                return (
                                    <Slide index={index} key={item.id}>
                                        <CarouselTileSlide>
                                            <CarouselTile
                                                bgColor={item.color?.bgColor}
                                                img={item.img}
                                                title={item.title}
                                                titleGradient={item.color?.titleGradient}
                                                mainTitle={item.mainTitle}
                                                subtitle={item.subtitle}
                                                note={item.note}
                                            />
                                        </CarouselTileSlide>
                                    </Slide>
                                );
                            })}
                </Slider>
                {hasData && props.data.length > 1 && (
                    <CarouselDotGroup>
                        <DotGroup />
                    </CarouselDotGroup>
                )}
            </CarouselProvider>
        </Container>
    );
};

CarouselTilesSection.propTypes = {
    data: PropTypes.array,
};
