import React from 'react';
import InputMask from 'react-input-mask';

import InputBase from 'components/form-multi/InputBase';

import { inputPropType } from 'helpers/customPropTypes';

const getNumberSpaces = (value) => {
    if (!value) return 0;
    const strVal = value.toString().trim() || '';
    const resArr = strVal.split(/\s/g);
    if (resArr.length > 0) {
        return resArr.length - 1;
    }
    return 0;
};

const InputAccount = (props) => {
    const { field } = props;
    const hasMaxLength = props.setMaxLength && field?.maxLength;
    const numberSpaces = hasMaxLength ? getNumberSpaces(field?.value) : 0;
    const value = props.customValue || field.value;
    return (
        <InputMask
            mask="**** **** **** **** **** **** **** **** **** ****"
            maskChar={null}
            id={field.name}
            name={field.canonicalPath ? field.canonicalPath : field.name}
            value={value ?? ''}
            type="text"
            className={props.isNewInput ? null : 'field field-duo'}
            autoComplete="off"
            readOnly={field.isReadOnly || false}
            autoFocus={props.autoFocus}
            onFocus={props.onFocus}
            onChange={props.onChange}
            onBlur={props.onBlur}
        >
            {(inputProps) => <input {...inputProps} maxLength={hasMaxLength ? +field.maxLength + numberSpaces : null} />}
        </InputMask>
    );
};

InputAccount.propTypes = inputPropType(['bank_account', 'pan', 'text', 'phone']);

export default InputBase(InputAccount, false);
