import React from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import PopupButton from 'components/common/Popup/PopupButton';
import SimplePopup from 'components/common/Popup/SimplePopup';

import { toggleModal, togglePopup } from 'actions/appAction';
import { toggleChat } from 'actions/chatAction';

import { HELP_PAYSEND } from 'constants/UrlConstants';

const SupportPopup = (props) => {
    return (
        <SimplePopup withoutBorder popupRef={props.popupRef}>
            <PopupButton
                href={HELP_PAYSEND}
                iconName="info-rounded"
                text={props.morePopupFaqTitle}
                onClick={() => {
                    props.popupRef?.current ? props.popupRef.current.close() : props.togglePopup(false);
                }}
            />
            <PopupButton
                iconName="message-bubble"
                text={props.morePopupChatTitle}
                onClick={(e) => {
                    e.preventDefault();
                    props.popupRef?.current ? props.popupRef.current.close() : props.togglePopup(false);
                    props.toggleChat(true);
                }}
            />
        </SimplePopup>
    );
};

SupportPopup.propTypes = {
    popupRef: PropTypes.shape({ current: PropTypes.object }),
};
SupportPopup.defaultProps = {
    popupRef: null,
};

const mapStateToProps = (state) => ({
    morePopupChatTitle: state.messages.pageMessages.morePopupChatTitle,
    morePopupFeedbackTitle: state.messages.pageMessages.morePopupFeedbackTitle,
    morePopupFaqTitle: state.messages.pageMessages.morePopupFaqTitle,
    lang: state.language.lang,
    userCountry: state.extra?.userCountry,
    country: state.app.country,
});
export default connect(mapStateToProps, { togglePopup, toggleModal, toggleChat })(SupportPopup);
