import React from 'react';
import { connect } from 'react-redux';

import { togglePopup } from 'actions/appAction';

import { CHINA } from 'constants/WellKnownCountries';

import { SEPA_FREE } from '../../constants/FieldType';
import { DangerouslyHtml } from '../common/DangerouslyHtml/DangerouslyHtml';
import PopupButton from '../common/Popup/PopupButton';
import SimplePopup from '../common/Popup/SimplePopup';

const AuthPromoTextPopup = (props) => {
    const getLinkForComment = () => {
        return <PopupButton href={getLinkForTerms()} target="_blank" iconName="arrow-forward" text={props.offerTitle} />;
    };
    const getText = (text) => {
        return <DangerouslyHtml dangerouslyHtml={text} />;
    };

    const getLinkForTerms = () => {
        let url = '';
        if (props.countryToId === CHINA) {
            url = '//cloud.paysend.com/web/docs/promo_alipay_july_2021.pdf';
        } else if (props.type === SEPA_FREE) {
            url = '//cloud.paysend.com/web/docs/sepa_cashback.pdf';
        } else {
            url = '//cloud.paysend.com/web/docs/mastercard_hero_promo_action_ru.pdf';
        }
        return url;
    };

    return (
        <SimplePopup text={getText(props.text)}>
            {props.isComment && getLinkForComment()}
            <PopupButton
                iconName="check"
                text={props.feeLandingCloseTitle}
                onClick={(e) => {
                    e.preventDefault();
                    if (props.closeHandler) {
                        props.closeHandler(e);
                    } else {
                        props.togglePopup(false);
                    }
                }}
            />
        </SimplePopup>
    );
};
const mapStateToProps = (state) => {
    return {
        lang: state.language.lang,
        country: state.app.country,
        offerTitle: state.messages.pageMessages.offerTitle,
        feeLandingChinaAlipayInfo: state.messages.pageMessages.feeLandingChinaAlipayInfo,
        feeLandingCloseTitle: state.messages.pageMessages.feeLandingCloseTitle,
    };
};

export default connect(mapStateToProps, { togglePopup })(AuthPromoTextPopup);
