import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';

import { PromoPageContainer } from 'components/no-auth/common/PromoPageContainer/PromoPageContainer';
import { PromoSectionContainer } from 'components/no-auth/common/PromoSectionContainer';
import VacanciesV2 from 'components/no-auth/hr/Vacancies/VacanciesV2';
import VacancyDetailsMain from 'components/no-auth/hr/VacancyDetailsMain/VacancyDetailsMain';
import NotAuthBasePage from 'components/no-auth/NotAuthBasePage';

import { setNewForm, updateFields } from 'actions/formAction';

const VacancyDetailsPage = (props) => {
    const [jobs, setJobs] = useState(props.jobs);

    useEffect(() => {
        if (Array.isArray(jobs)) {
            setJobs(jobs.filter((job) => job.id !== props.job_id));
        }
    }, []);

    return (
        <PromoPageContainer noIndent>
            <PromoSectionContainer>
                {props.title && props.location && props.description && props.job_id && props.job_btn && (
                    <VacancyDetailsMain
                        title={props.title}
                        location={props.location}
                        description={props.description}
                        jobId={props.job_id}
                        btn={props.job_btn}
                    />
                )}
                {props.lang && props.country && props.jobs && props.department && (
                    <VacanciesV2
                        lang={props.lang}
                        country={props.country}
                        jobs={jobs}
                        departamentProps={props.department}
                        title={props.vacanciesTitle}
                        jobId={props.job_id}
                        singularVerb={props.singularVerb}
                        pluralVerb={props.pluralVerb}
                        insideVacancy
                    />
                )}
            </PromoSectionContainer>
        </PromoPageContainer>
    );
};

const mapStateToProps = (state) => {
    return {
        lang: state.extra.noAuthFooter?.language?.lang,
        country: state.extra.noAuthFooter?.language?.country,
        title: state.extra?.strapiApi?.selected_job_info?.title,
        location: state.extra?.strapiApi?.selected_job_info?.location,
        description: state.extra?.strapiApi?.selected_job_info?.description,
        jobs: state.extra?.strapiApi?.all_jobs_list,
        department: state.extra?.strapiApi?.selected_job_info?.department,
        job_id: state.extra?.strapiApi?.selected_job_info?.job_id,
        job_btn: state.extra?.strapiApi?.job_btn,
        vacanciesTitle: state.extra?.strapiApi?.job_amount_title,
        singularVerb: state.extra?.strapiApi?.singular_be_verb,
        pluralVerb: state.extra?.strapiApi?.plural_be_verb,
    };
};
export default connect(mapStateToProps, { setNewForm, updateFields })(NotAuthBasePage(VacancyDetailsPage));
