import React, { useRef } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import styled from 'styled-components';

import promoMedia from 'theme/promoMedia';

import { AppButtons } from './styles';
import PopupNew from '../../../common/Popup/PopupNew';
import { AppButton } from '../AppButton/AppButton';
import QRPopup from '../Popups/QRPopup';
import { PromoButton } from '../PromoButton/PromoButton';
import PromoButtonExtended from '../PromoButton/PromoButtonExtended';
import { PromoDangerouslyHtml } from '../PromoDangerouslyHtml/PromoDangerouslyHtml';

const HeadingContainer = styled.div`
    padding: 32px 0;
    margin-bottom: ${(props) => (props.noBottomIndent ? null : '16px')};
    text-align: ${(props) => (props.textLeft ? 'left' : 'center')};

    ${promoMedia.tablet744} {
        padding: ${(props) => (props.textLeft ? '48px 0' : '48px')};
        margin-bottom: ${(props) => (props.noBottomIndent ? null : '24px')};
    }

    ${promoMedia.desktop1440} {
        padding: ${(props) => (props.textLeft ? '64px 0' : '64px')};
        margin-bottom: ${(props) => (props.noBottomIndent ? null : '32px')};
    }
`;

const HeadingTitle = styled.div`
    margin-bottom: ${(props) => (props.text ? '8px' : null)};

    ${promoMedia.tablet744} {
        margin-bottom: ${(props) => (props.text ? '12px' : null)};
    }

    ${promoMedia.desktop1440} {
        margin-bottom: ${(props) => (props.text ? '16px' : null)};
    }
`;

const BtnContainer = styled.div`
    margin-top: 16px;

    ${promoMedia.tablet744} {
        margin-top: 24px;
    }

    ${promoMedia.desktop1440} {
        margin-top: 32px;
    }
`;

const HeadingSection = (props) => {
    const popupRef = useRef(null);

    return (
        <>
            <HeadingContainer text={props.text} noBottomIndent={props.noBottomIndent} textLeft={props.textLeft} data-testid={props.dataTestId}>
                {props.title && (
                    <HeadingTitle text={props.text}>
                        <PromoDangerouslyHtml
                            tag="h2"
                            variant={props.titleVariant}
                            color={props.whiteTitle ? 'white' : 'gray80'}
                            dangerouslyHtml={props.title}
                        />
                    </HeadingTitle>
                )}
                {props.text && <PromoDangerouslyHtml dangerouslyHtml={props.text} tag="div" variant={props.bodyText ? 'div' : 'subheader'} color="gray40" />}
                {props.btn && (
                    <BtnContainer>
                        <PromoButtonExtended
                            btn={props.btn}
                            btnMobile={props.btnMobile}
                            isMobileBtn={props.isMobileBtn}
                            iconName={props.iconName}
                            variant={props.variant}
                            sendFrom={props.sendFrom}
                        />
                    </BtnContainer>
                )}
                {props.hasAppsBtns && (
                    <>
                        <AppButtons>
                            <AppButton href={props.apple_store} variant="apple" areaLabel="App Store" />
                            <AppButton href={props.google_play} variant="google" areaLabel="Google Play" />
                            <AppButton href={props.huawei_store} variant="huawei" areaLabel="AppGallery" />
                        </AppButtons>
                        <AppButtons>
                            <PromoButton
                                variant="tertiary"
                                type="button"
                                onClick={(e) => {
                                    e.preventDefault();
                                    popupRef.current?.open(<QRPopup popupRef={popupRef} />);
                                }}
                                caption={props.qrPopupBtnTitle}
                            />
                        </AppButtons>
                    </>
                )}
            </HeadingContainer>
            <PopupNew ref={popupRef} getPopupComponent={(component) => component} />
        </>
    );
};

HeadingSection.defaultProps = {
    titleVariant: 'h2',
};

const mapStateToProps = (state) => {
    return {
        qrPopupBtnTitle: state.extra?.downloadButtons?.find((item) => item.type === 'qr')?.description,
        apple_store: state.extra?.downloadButtons?.find((item) => item.type === 'apple')?.url,
        google_play: state.extra?.downloadButtons?.find((item) => item.type === 'android')?.url,
        huawei_store: state.extra?.downloadButtons?.find((item) => item.type === 'huawei')?.url,
    };
};

HeadingSection.propTypes = {
    title: PropTypes.string,
    text: PropTypes.string,
    iconName: PropTypes.string,
    variant: PropTypes.oneOf(['primary', 'secondary', 'tertiary']),
    btn: PropTypes.object,
    btnMobile: PropTypes.object,
    isMobileBtn: PropTypes.bool,
    noBottomIndent: PropTypes.bool,
    whiteTitle: PropTypes.bool,
    titleVariant: PropTypes.oneOf(['h2', 'h3']),
    textLeft: PropTypes.bool,
    bodyText: PropTypes.bool,
    dataTestId: PropTypes.string,
    hasAppsBtns: PropTypes.bool,
};

export default connect(mapStateToProps, null)(HeadingSection);
